package adventOfCode.year2021

import adventOfCode.{AocDay, VisualDebugger}
import cats.effect.IO
import outwatch.dsl._

object Day05 extends AocDay {

  val day = 5

  case class Coord(x: Int, y: Int) {
    override def toString: String = s"($x,$y)"
  }
  case class Line(start: Coord, end: Coord) {
    override def toString: String = s"$start -> $end"

    val isHorizontal = start.y == end.y
    val isVertical = start.x == end.x

    def points: Seq[Coord] = {
      val Coord(x1, y1) = start
      val Coord(x2, y2) = end

      if (isHorizontal || isVertical) {
        val deltaX = if (x1 > x2) -1 else 1
        val deltaY = if (y1 > y2) -1 else 1
        for {
          x <- x1.to(x2, deltaX)
          y <- y1.to(y2, deltaY)
        } yield Coord(x, y)
      } else {
        /*
An entry like 1,1 -> 3,3 covers points 1,1, 2,2, and 3,3.
An entry like 9,7 -> 7,9 covers points 9,7, 8,8, and 7,9.
         */

        val deltaX = if (x1 > x2) -1 else 1
        val deltaY = if (y1 > y2) -1 else 1

        val length = Math.abs(x1 - x2)
        0.to(length).map { i =>
          Coord(x1 + i * deltaX, y1 + i * deltaY)

        }
      }
    }
  }

  def parseCoord(str: String) = {
    val List(x, y) = str.split(",").map(_.toInt).toList
    Coord(x, y)
  }

  def parseLine(line: String): Line = {
    val List(start, end) = line.split(" -> ").map(parseCoord).toList
    Line(start, end)
  }

  def draw(
      lines: List[Line]
  )(implicit visualDebugger: VisualDebugger) = {
    val minX = (lines.map(_.start.x) ++ lines.map(_.end.x)).min
    val maxX = (lines.map(_.start.x) ++ lines.map(_.end.x)).max
    val minY = (lines.map(_.start.y) ++ lines.map(_.end.y)).min
    val maxY = (lines.map(_.start.y) ++ lines.map(_.end.y)).max

    import svg._
    visualDebugger.render(
      svg(
        height := "300px",
        viewBox := s"$minX $minY $maxX $maxY",
        style := "background-color:lightGrey",
        lines.map(l =>
          line(
            x1 := l.start.x.toString,
            x2 := l.end.x.toString,
            y1 := l.start.y.toString,
            y2 := l.end.y.toString,
            stroke := "black"
          )
        )
      )
    )
  }

  def solvePart1(input: String)(implicit visualDebugger: VisualDebugger) = IO{

    val inputLines = input.linesIterator.toList.filterNot(_.isEmpty)
    val lines = inputLines.map(parseLine)
    val verticalOrHorizontal = lines.filter(l => l.isVertical || l.isHorizontal)

    draw(verticalOrHorizontal)(visualDebugger)
    calcCoords(verticalOrHorizontal)
  }

  def solvePart2(input: String)(implicit visualDebugger: VisualDebugger) = IO{

    val inputLines = input.linesIterator.toList.filterNot(_.isEmpty)
    val lines = inputLines.map(parseLine)
    draw(lines)
    calcCoords(lines)(VisualDebugger.noOpLoggingDebugger)
  }

  private def calcCoords(
      verticalOrHorizontal: List[Line]
  )(implicit visualDebugger: VisualDebugger): String = {
    val coords: List[Coord] = verticalOrHorizontal.flatMap(_.points).toList
    val occurrences: Map[Coord, Int] =
      coords.groupBy(c => c).map(t => (t._1, t._2.size))

    val relevantPoints = occurrences.filter(_._2 >= 2)

    visualDebugger.log("relevant lines")
    verticalOrHorizontal.foreach(l => visualDebugger.log(l.toString))

    visualDebugger.log("all coordinates")
    coords.foreach(c => visualDebugger.log(c.toString))
    visualDebugger.log("occurrencesMap")
    occurrences.foreach { case (c, cnt) => visualDebugger.log(s"$c: ${cnt}x") }
    visualDebugger.log("points with overlap")
    relevantPoints.foreach { case (c, cnt) =>
      visualDebugger.log(s"$c: ${cnt}x")
    }

    relevantPoints.size.toString
  }

}
