package adventOfCode.year2022

import adventOfCode.{AocDay, VisualDebugger}
import cats.effect.IO
import adventOfCode.util.Parsers._
import cats.data.NonEmptyList
import cats.parse.{Numbers, Parser => P}

object Day05 extends AocDay {
  val day = 5

  val parser                  = identityStringParser
  def parseLine(line: String) = parser.parse(line).toOption.get._2

  val stackItemP                           = ((P.char('[') *> P.anyChar) <* P.char(']')).map(Option.apply)
  val emptySpaceP                          = P.char(' ').rep(3, 3).as(Option.empty[Char])
  val slotP                                = stackItemP | emptySpaceP
  val lineP: P[NonEmptyList[Option[Char]]] = slotP.repSep(P.char(' '))
  val instructionP                         =
    ((P.string("move ") *> intParser <* P.string(" from ")) ~ (intParser <* P.string(" to ")) ~ intParser).map {
      case ((amount, from), to) => Move(amount, from, to)
    }

  case class Move(amount: Int, from: Int, to: Int)

  /*
      [D]
  [N] [C]
  [Z] [M] [P]
   1   2   3

  move 6 from 1 to 7
   */

  def renderSlot(slot: Option[Char]): String =
    s"[${slot.getOrElse(' ')}]"

  def transposeToStacks(rows: Vector[Vector[Option[Char]]]): Vector[String] = {
    val numStacks       = rows.head.size
    val numItemsInStack = rows.size
    assert(rows.forall(_.size == numStacks))

    val realStacks = Vector.fill(numStacks)(Array.fill(numItemsInStack)(' '))

    0.until(numStacks).foreach { s =>
      0.until(numItemsInStack).foreach { i =>
        realStacks(s).update(i, rows(i)(s).getOrElse(' '))
      }
    }
    realStacks.map(_.mkString.reverse.trim)

  }

  // Fix IDE's removal of trailing whitespace
  def fillStringWithWhitespaces(string: String): String = {
    val lines   = string.linesIterator.toList
    val longest = lines.map(_.length).max

    lines.map(_.padTo(longest, ' ')).mkString("\n")
  }

  def applyMove(stacks: Vector[String], move: Move, moveReversesOrder: Boolean = true): Vector[String] = {

    val from = stacks(move.from - 1)
    val to   = stacks(move.to - 1)

    val (fromNew, cutoff) = from.splitAt(from.length - move.amount)
    val toNew             = to + (if (moveReversesOrder) cutoff.reverse else cutoff)

    stacks
      .updated(move.from - 1, fromNew)
      .updated(move.to - 1, toNew)
  }

  def solve(input: String, useReverseOrder: Boolean)(implicit visualDebugger: VisualDebugger) = IO {
    val Array(stacks, instructionsStr) = input.split("\n\n")

    val onlyStacks                           = fillStringWithWhitespaces(stacks).linesIterator.toList.dropRight(1)
    val parsed: Vector[Vector[Option[Char]]] = onlyStacks.map { line =>
      val result = lineP.parse(line)
      result.toOption.get._2.toList.toVector
    }.toVector

    val instructions = instructionsStr.linesIterator.toList.map { line =>
      instructionP.parse(line).toOption.get._2
    }

    val transposed = transposeToStacks(parsed)

    val finalResult = instructions.foldLeft(transposed)((acc, move) => applyMove(acc, move, useReverseOrder))

    visualDebugger.log("parsed")
    parsed.foreach(list => visualDebugger.log(list.map(renderSlot).toList.mkString(" ")))
    visualDebugger.log("\ntransposed")
    transposed.foreach(line => visualDebugger.log(line))

    visualDebugger.log("\nafter first move")
    val after1 = applyMove(transposed, instructions.head, useReverseOrder)
    after1.foreach(line => visualDebugger.log(line))

    visualDebugger.log("\n\nfinal result")
    finalResult.foreach(line => visualDebugger.log(line))

    val result = finalResult.map(_.last).mkString

    result
  }

  def solvePart1(input: String)(implicit visualDebugger: VisualDebugger) = solve(input, useReverseOrder = true)

  def solvePart2(input: String)(implicit visualDebugger: VisualDebugger) = solve(input, useReverseOrder = false)
}
