package adventOfCode.year2021

import adventOfCode.{AocDay, VisualDebugger}
import cats.effect.IO

object Day03 extends AocDay {

  val day = 3

  case class BitPositionResult(index: Int, occurrencesOfBits: Map[Char, Int]) {
    val mostCommon = occurrencesOfBits.toList.sortBy(_._2).last._1
    val leastCommon = occurrencesOfBits.toList.sortBy(_._2).head._1
  }

  def solvePart1(input: String)(implicit visualDebugger: VisualDebugger) = IO{

    val lines = input.linesIterator.toList.filterNot(_.isEmpty)

    val bitPositionResults: List[BitPositionResult] = calcAllBitPositions(lines)

    val assembledGammaRate =
      bitPositionResults.sortBy(_.index).map(_.mostCommon).mkString
    val assembledEpsilonRate =
      bitPositionResults.sortBy(_.index).map(_.leastCommon).mkString

    visualDebugger.log("Bit Overview")
    bitPositionResults.sortBy(_.index).foreach { case bpr =>
      visualDebugger.log(
        s"  ${bpr.index}; mostCommon: ${bpr.mostCommon}; leastCommon: ${bpr.leastCommon} "
      )
    }

    val gammaRate = Integer.parseInt(assembledGammaRate, 2)
    val epsilonRate = Integer.parseInt(assembledEpsilonRate, 2)

    val result = gammaRate * epsilonRate
    visualDebugger.log("assembledGammaRate", assembledGammaRate)
    visualDebugger.log("gammaRate", gammaRate)
    visualDebugger.log("assembledEpsilonRate", assembledEpsilonRate)
    visualDebugger.log("epsilonRate", epsilonRate)

    visualDebugger.log("result", result)

    result.toString
  }

  def solvePart2(input: String)(implicit visualDebugger: VisualDebugger) = IO{

    val lines = input.linesIterator.toList.filterNot(_.isEmpty)

    val oxygenGeneratorRatingBits = findRating(0, lines, _.mostCommon)
    val oxygenGeneratorRating = Integer.parseInt(oxygenGeneratorRatingBits, 2)

    val co2ScrubberRatingBits = findRating(0, lines, _.leastCommon)
    val co2ScrubberRating = Integer.parseInt(co2ScrubberRatingBits, 2)

    val result = oxygenGeneratorRating * co2ScrubberRating

    visualDebugger.log("oxygenGeneratorRatingBits", oxygenGeneratorRatingBits)
    visualDebugger.log("oxygenGeneratorRating", oxygenGeneratorRating)

    visualDebugger.log("co2ScrubberRatingBits", co2ScrubberRatingBits)
    visualDebugger.log("co2ScrubberRating", co2ScrubberRating)

    visualDebugger.log("result", result)

    result.toString
  }

  def findRating(
      index: Int,
      lines: List[String],
      bitCriteria: BitPositionResult => Char
  ): String = {

    if (lines.size == 1)
      lines.head
    else {

      val result = calcBitPositionResult(index, lines)
      val mostCommon = bitCriteria(result)
      val linesWithMatchingBit = lines.filter(_(index) == mostCommon)

      findRating(index + 1, linesWithMatchingBit, bitCriteria)

    }
  }

  private def calcAllBitPositions(lines: List[String]) = {
    val numberOfBits = lines.head.length
    val bitPositionResults = 0.until(numberOfBits).map { i =>
      calcBitPositionResult(i, lines)
    }
    bitPositionResults.toList
  }

  private def calcBitPositionResult(index: Int, lines: List[String]) = {
    val bitsOfAllLinesAtIndex = lines.map(line => line(index))
    val occurrencesOfBits: Map[Char, Int] =
      bitsOfAllLinesAtIndex.groupMapReduce(identity)(_ => 1)(_ + _)
    BitPositionResult(index, occurrencesOfBits)
  }
}
