package adventOfCode.year2021

import adventOfCode.{AocDay, VisualDebugger}
import cats.effect.IO
import outwatch.dsl._
object Day17 extends AocDay {
  val day = 17

  case class TargetArea(xMin: Int, xMax: Int, yMin: Int, yMax: Int) {
    def contains(p: Pos): Boolean = {
      p.x >= xMin && p.x <= xMax && p.y >= yMin && p.y <= yMax
    }
  }

  case class Pos(x: Int, y: Int)
  case class Velocity(vx: Int, vy: Int)

  def parse(input: String) = {
    val numbers = "[-\\d]+".r

    val List(xMin, xMax, yMin, yMax) = numbers.findAllIn(input).toList.map(_.toInt)

    TargetArea(xMin, xMax, yMin, yMax)
  }

  val myTd = td(cls := "border border-gray-300 align-baseline")
  val myTh = th(cls := "border border-gray-300 align-baseline")
  val myTable = table(cls := "border-collapse border border-gray-400")

  def findXMinAndMaxVelocity(targetArea: TargetArea) = {
    /*
    e.g. target area: x=20..30, y=-10..-5

    triangular number: 1+2+3+4 = 10
    n * (n+1) / 2 = triangular
    4 * 5 / 2 = 10

    t * 2 = n * (n+1)
    n = 1/2 sqrt(8t + 1) - 1
     */
    val minVx = math.ceil(1 / 2.0 * math.sqrt(8 * targetArea.xMin + 1) - 1).toInt
    val maxVx = targetArea.xMax
    minVx.to(maxVx)
  }

  def simulate(pos: Pos, velocity: Velocity, targetArea: TargetArea, positions: List[Pos] = List.empty): List[Pos] = {
    //println(s"simulate(pos: $pos, velocity: $velocity)")
    if (targetArea.contains(pos)) {
      //println("hit target")
      positions.reverse
    } else if (pos.x > targetArea.xMax || pos.y < targetArea.yMin) {
      //println(s"overshot target. $pos, $targetArea")
      List.empty
    } else {
      val newPos = Pos(pos.x + velocity.vx, pos.y + velocity.vy)
      val vXDragReduce = if (velocity.vx > 0) 1 else -1
      val newVx = if (velocity.vx == 0) 0 else velocity.vx - vXDragReduce
      val newVelocity = Velocity(newVx, velocity.vy - 1)
      simulate(newPos, newVelocity, targetArea, pos :: positions)
    }
  }

  def solvePart1(input: String)(implicit visualDebugger: VisualDebugger) = IO{
    val targetArea = parse(input.trim)

    val validSpeeds: List[(Velocity, List[Pos])] = calculateValidStartingVelocities(input)

    println(s"found ${validSpeeds.size} valid starting velocities")
    val (startingVelocityForHighestShot, highestPoint) =
      validSpeeds.map(t => t._1 -> t._2.maxBy(_.y)).maxBy(t => t._2.y)
    println(s"highest point: $highestPoint. Starting velocity: $startingVelocityForHighestShot")

    visualDebugger.render(
      div(
        h3("Highest shot", cls := "text-2xl mb-2"),
        div(
          p("Point: ", highestPoint.toString),
          p("Starting velocity: ", startingVelocityForHighestShot.toString)
        ),
        h3("Target Area", cls := "text-2xl mb-2"),
        myTable(
          thead(tr(myTh("xMin"), myTh("xMax"), myTh("yMin"), myTh("yMax"))),
          tbody(
            tr(
              myTd(targetArea.xMin),
              myTd(targetArea.xMax),
              myTd(targetArea.yMin),
              myTd(targetArea.yMax)
            )
          )
        )
      )
    )

    highestPoint.y.toString
  }

  def solvePart2(input: String)(implicit visualDebugger: VisualDebugger) = IO{
    val validSpeeds: List[(Velocity, List[Pos])] = calculateValidStartingVelocities(input)

    val distinctSpeeds = validSpeeds.distinctBy(_._1)
    println(s"found ${distinctSpeeds.size} distinct starting velocities")
    validSpeeds.distinct.size.toString
  }

  private def calculateValidStartingVelocities(input: String): List[(Velocity, List[Pos])] = {
    val targetArea = parse(input.trim)

    val vXRange = findXMinAndMaxVelocity(targetArea)
    val vYRange = math.min(targetArea.yMin, targetArea.yMax).to(2000)
    println(s"vx: $vXRange")
    println(s"vy: $vYRange")

    val speeds = for {
      vx <- vXRange
      vy <- vYRange
    } yield Velocity(vx, vy)

    val validSpeeds = speeds.flatMap { v =>
      val points: List[Pos] = simulate(Pos(0, 0), v, targetArea)

      if (points.isEmpty) List.empty
      else {
        List(v -> points)
      }
    }
    validSpeeds.toList
  }
}
