package adventOfCode.year2022

import adventOfCode.{AocDay, VisualDebugger}
import cats.effect.IO
import outwatch.dsl._

object Day01 extends AocDay {
  val day = 1

  def solvePart1(input: String)(implicit visualDebugger: VisualDebugger) = IO {
    val reindeers = parseReindeerCalories(input)

    visualize("Reindeers and their total calories", reindeers)
    val (_, result) = reindeers.maxBy(_._2)
    result.toString
  }

  def solvePart2(input: String)(implicit visualDebugger: VisualDebugger) = IO {
    val reindeers = parseReindeerCalories(input)
    val top3      = reindeers.sortBy(_._2).reverse.take(3)

    visualize("Top 3 reindeers per calories", top3)

    val result = top3.map(_._2).sum
    result.toString
  }

  def parseReindeerCalories(input: String): Array[(List[Int], Int)] =
    input.split("\n\n").map { block =>
      val perReindeer = block.linesIterator.map(_.toInt).toList
      perReindeer -> perReindeer.sum
    }

  def visualize(description: String, reindeers: Array[(List[Int], Int)])(implicit visualDebugger: VisualDebugger) = {
    import adventOfCode.util.ui.Helpers.FormattedTable._
    visualDebugger.render(
      div(
        h3(cls := "font-medium leading-tight text-2xl mt-0 mb-2", description),
        table(
          thead(
            tr(
              th("calories"),
              th("sum"),
            ),
          ),
          tbody(
            reindeers.map { case (calories, total) =>
              tr(
                td(calories.mkString(", ")),
                td(cls := "text-right", total),
              )
            },
          ),
        ),
      ),
    )
  }

}

