package adventOfCode.year2021

import adventOfCode.{AocDay, VisualDebugger}
import cats.effect.IO
import outwatch.dsl._

object Day12 extends AocDay {
  def findRoutes(
      connections: List[Connection],
      allowOneMultivisitOfSmallNode: Boolean = false
  ): List[List[Cave]] = {

    val nodes = connections.map(_.from).toSet
    val start: Cave = nodes.find(_.name == "start").get

    val maxNumberOfSmallNodeVisit = if (allowOneMultivisitOfSmallNode) 2 else 1

    def helper(
        openList: List[Cave],
        path: List[Cave],
        visited: Map[Cave, Int],
        results: List[List[Cave]]
    ): List[List[Cave]] = {

      def findValidDestinations(current: Cave) = {
        connections
          .filter { case Connection(from, to @ Cave(_, size)) =>
            from == current && to.name != "start" && (visited.getOrElse(
              to,
              0
            ) <= maxNumberOfSmallNodeVisit || size == CaveSize.Big)
          }
          .map(_.to)
      }

      openList match {

        //reached end
        case ::(endNode, next) if endNode.name == "end" =>
          helper(
            next,
            endNode :: path,
            visited, //.updated(endNode, visited.getOrElse(endNode, 0) + 1),
            (endNode :: path).reverse :: results
          )

        //revisiting a big cave
        case ::(bigCave, next) if bigCave.size == CaveSize.Big =>
          findValidDestinations(bigCave).flatMap(d =>
            helper(d :: next, bigCave :: path, visited, results)
          )

        //revisiting ONE small cave
        case ::(smallCave, next)
            if smallCave.size == CaveSize.Small && !visited.values.exists(
              _ >= maxNumberOfSmallNodeVisit
            ) =>
          findValidDestinations(smallCave).flatMap(d =>
            helper(
              d :: next,
              smallCave :: path,
              visited.updated(smallCave, visited.getOrElse(smallCave, 0) + 1),
              results
            )
          )

        //already visited node
        case ::(alreadyVisited, next) if visited.contains(alreadyVisited) =>
          helper(next, path, visited, results)

        //new node
        case ::(current, next) =>
          findValidDestinations(current).flatMap(d =>
            helper(
              d :: next,
              current :: path,
              visited.updated(current, visited.getOrElse(current, 0) + 1),
              results
            )
          )

        //empty openList
        case Nil =>
          results
      }
    }

    helper(start :: Nil, List.empty, Map.empty, List.empty)

  }

  val day = 12

  /*
start-A
start-b
A-c
A-b
b-d
A-end
b-end

    start
    /   \
c--A-----b--d
    \   /
     end

   */

  sealed trait CaveSize

  object CaveSize {
    case object Big extends CaveSize
    case object Small extends CaveSize
  }

  case class Cave(name: String, size: CaveSize)
  case class Connection(from: Cave, to: Cave)

  def parseCave(str: String) = {
    import CaveSize._

    Cave(str, if (str.head.isLower) Small else Big)
  }

  def parseConnections(input: String): List[Connection] = {
    input.linesIterator
      .filterNot(_.trim.isEmpty)
      .toList
      .flatMap { line =>
        val List(from, to) = line.split("-").toList.map(parseCave)
        List(
          Connection(from, to),
          Connection(to, from)
        )
      }
  }

  def solve(input: String, allowOneRevisitOfSmallCave: Boolean)(implicit
      visualDebugger: VisualDebugger
  ) = {

    val connections = parseConnections(input)
    val caves = connections.map(_.from).distinct
    val routes = findRoutes(connections, allowOneRevisitOfSmallCave)

    visualDebugger.render(
      div(
        h2(s"Details"),
        p(s"${connections.size / 2} connections"),
        p(s"${caves.size} caves"),
        p(s"${caves.count(_.size == CaveSize.Big)} big caves"),
        p(s"${caves.count(_.size == CaveSize.Small)} small caves"),
        h2(s"routes"),
        if (routes.size > 1000) {
          p(s"${routes.size} routes found (too much to display)")
        } else {
          table(
            thead(tr(th("path"))),
            tbody(
              routes.map { path =>
                tr(
                  td(path.map(_.name).mkString(","))
                )
              }
            )
          )
        }
      )
    )

    routes.size.toString

  }

  def solvePart1(input: String)(implicit visualDebugger: VisualDebugger) = IO{
    solve(input, allowOneRevisitOfSmallCave = false)

  }

  def solvePart2(input: String)(implicit visualDebugger: VisualDebugger) = IO{

    solve(input, allowOneRevisitOfSmallCave = true)
  }
}
