package adventOfCode.year2021

import adventOfCode.{AocDay, VisualDebugger}
import cats.effect.IO

object Day22 extends AocDay {
  val day = 22

  case class Cuboid(
    on: Boolean,
    x1: Int,
    x2: Int,
    y1: Int,
    y2: Int,
    z1: Int,
    z2: Int,
  ) {
    def coords = for {
      x <- x1.to(x2)
      y <- y1.to(y2)
      z <- z1.to(z2)
    } yield (x, y, z)
  }

  def solvePart1(input: String)(implicit visualDebugger: VisualDebugger) = IO {

    def clamp(value: Int): Int =
      if (value < -50) -50
      else if (value > 50) 50
      else value

    val cuboids = input.linesIterator.toList.collect { case s"$step x=$x1..$x2,y=$y1..$y2,z=$z1..$z2" =>
      val on = step == "on"
      Cuboid(
        on,
        clamp(x1.toInt),
        clamp(x2.toInt),
        clamp(y1.toInt),
        clamp(y2.toInt),
        clamp(z1.toInt),
        clamp(z2.toInt),
      )
    }

    visualDebugger.log(cuboids.map(_.toString).mkString("\n"))

    val xs = cuboids.flatMap(c => List(c.x1, c.x2)).distinct.sorted
    val ys = cuboids.flatMap(c => List(c.y1, c.y2)).distinct.sorted
    val zs = cuboids.flatMap(c => List(c.z1, c.z2)).distinct.sorted

    val compressedCuboids = cuboids.map { c =>
      val x1 = xs.indexOf(c.x1)
      val y1 = ys.indexOf(c.y1)
      val z1 = zs.indexOf(c.z1)

      val x2 = xs.indexOf(c.x2)
      val y2 = ys.indexOf(c.y2)
      val z2 = zs.indexOf(c.z2)

      Cuboid(c.on, x1, x2, y1, y2, z1, z2)
    }

    visualDebugger.log(s"xs:  $xs")
    visualDebugger.log(s"ys:  $ys")
    visualDebugger.log(s"zs:  $zs")

    visualDebugger.log(compressedCuboids.map(_.toString).mkString("\n"))
    val onCoords = compressedCuboids.foldLeft(Set.empty[(Int, Int, Int)]) { (acc, cub) =>
      if (cub.on)
        acc.union(cub.coords.toSet)
      else acc.diff(cub.coords.toSet)
    }

    visualDebugger.log(s"${onCoords.size} enabled cubes")
    visualDebugger.log(onCoords.toList.sorted.map(_.toString).mkString("\n"))

    def decompressRange(idx: Int, values: Vector[Int]): Range = {
      val v1 = values(idx)
      if (idx >= values.size - 1)
        v1.to(v1)
      else v1.until(values(idx + 1))
    }

    // translate back
    val sizes = onCoords.toList.map { case cube @ (x, y, z) =>
      val xRange = decompressRange(x, xs.toVector)
      val yRange = decompressRange(y, ys.toVector)
      val zRange = decompressRange(z, zs.toVector)

      val size = xRange.size * yRange.size * zRange.size
      (cube, xRange, yRange, zRange, size)
    }

    visualDebugger.log(s"decompressed enabled cubes")
    visualDebugger.log(sizes.toList.sortBy(_._1).map(_.toString).mkString("\n"))

    val result = sizes.map(_._5).sum
    result.toString


  }

  def solvePart2(input: String)(implicit visualDebugger: VisualDebugger) = IO {
    "???"
  }
}
