package adventOfCode.year2022

import adventOfCode.{AocDay, VisualDebugger}
import cats.effect.IO
import adventOfCode.util.Parsers._
import cats.parse.{Numbers, Parser => P}

object Day06 extends AocDay {
  val day = 6

  // Started      : 9:14
  // solved part 1: 9:19
  // solved part 2: 9:20

  val parser                  = identityStringParser
  def parseLine(line: String) = parser.parse(line).toOption.get._2

  def solve(input: String, distinctSize: Int): Int = {
    val (_, startingIdx) =
      input
        .sliding(size = distinctSize, step = 1)
        .zipWithIndex
        .find { case (str, _) => str.distinct.length == distinctSize }
        .get

    val result = startingIdx + distinctSize
    result

  }

  def solvePart1(input: String)(implicit visualDebugger: VisualDebugger) = IO {
    solve(input, 4).toString
  }

  def solvePart2(input: String)(implicit visualDebugger: VisualDebugger) = IO {

    solve(input, 14).toString
  }
}
