object Day18 extends AocDay {

  import Expr.{NodeMetadata, Number, Pair, TreeInfo}
  import InputParser._
  val day = 18

  // override def pageLayout: PageLayout = PageLayout.Vertical

  sealed trait Expr {
    val depth: Int
    lazy val magnitude: Int = this match {
      case Number(value, _)     => value
      case Pair(left, right, _) => 3 * left.magnitude + 2 * right.magnitude
    }
  }

  object Expr {
    case class Number(value: Int, depth: Int)            extends Expr {
      override def toString: String = value.toString
    }
    case class Pair(left: Expr, right: Expr, depth: Int) extends Expr {
      override def toString: String = s"[$left,$right]"
    }

    case class TreeInfo(
      nodes: Map[NodeMetadata, Expr],
      parentChild: Map[NodeMetadata, List[NodeMetadata]],
      inDfsOrder: Vector[Number],
    )
    case class NodeMetadata(depth: Int, idx: Int) {
      override def toString: String = s"NodeMetadata(depth: $depth, idx: $idx)"
    }

    def calcTreeInfo(root: Expr): TreeInfo = {

      @tailrec
      def helper(
        queue: Queue[(Expr, Int, Int)],
        visited: Map[NodeMetadata, Expr],
      ): Map[NodeMetadata, Expr] =
        queue.dequeueOption match {
          case None =>
            visited

          case Some(((node, depth, idx), rest)) =>
            node match {
              case n: Number =>
                helper(rest, visited.updated(NodeMetadata(depth, idx), n))

              case p: Pair =>
                helper(
                  rest.enqueueAll(List((p.left, depth + 1, idx * 2 - 1), (p.right, depth + 1, idx * 2))),
                  visited.updated(NodeMetadata(depth, idx), p),
                )
            }
        }
      val nodes       = helper(Queue((root, 0, 1)), Map.empty)
      val allNodes    = nodes.keys.toList
      val parentChild = allNodes.flatMap { meta =>
        val theoreticalChildren: List[NodeMetadata] =
          List(NodeMetadata(meta.depth + 1, meta.idx * 2 - 1), NodeMetadata(meta.depth + 1, meta.idx * 2))
        List(meta -> theoreticalChildren.filter(nodes.contains))
      }.toMap

      val inOrder = dfsLeafs(root).toVector
      TreeInfo(nodes, parentChild, inOrder)
    }

    def dfsLeafs(root: Expr): List[Number] = {

      def helper(node: Expr, inOrder: List[Number]): List[Number] =
        node match {
          case n: Number            => n :: inOrder
          case Pair(left, right, _) =>
            val fromLeft = helper(left, inOrder)
            helper(right, fromLeft)
        }

      helper(root, List.empty).reverse
    }

  }

  case class ParseState(expr: String, pos: Int, depth: Int)
  type Parsed[A] = StateT[Option, ParseState, A]

  sealed trait Crumb

  object Crumb {
    case class LeftCrumb(right: Expr) extends Crumb
    case class RightCrumb(left: Expr) extends Crumb
  }

  type Breadcrumbs = List[Crumb]
  type Zipper      = (Expr, Breadcrumbs)

  def calcNodeMetaFromBreadcrumbs(breadcrumbs: Breadcrumbs): NodeMetadata = {

    def walk(breadcrumbs: Breadcrumbs, depth: Int, idx: Int): NodeMetadata =
      // List((p.left, depth + 1, idx * 2 - 1), (p.right, depth + 1, idx * 2))),
      breadcrumbs match {
        case ::(_: Crumb.LeftCrumb, next)  => walk(next, depth + 1, idx * 2 - 1)
        case ::(_: Crumb.RightCrumb, next) => walk(next, depth + 1, idx * 2)
        case Nil                           => NodeMetadata(depth, idx)
      }

    walk(breadcrumbs.reverse, 0, 1)
  }

  import Crumb._
  def goLeft(zipper: Zipper): Option[Zipper] =
    zipper match {
      case (_: Number, _)         => None
      case (p: Pair, breadcrumbs) => Some(p.left, Crumb.LeftCrumb(p.right) :: breadcrumbs)
    }

  def find(zipper: Zipper)(partial: PartialFunction[Expr, Boolean]): Option[Zipper] = {
    val (number, path) = zipper

    if (partial.isDefinedAt(number) && partial(number)) Some(zipper)
    else { // no direct match, we need to explore the tree
      number match {
        case _: Number => None
        case _: Pair   =>
          goLeft(zipper) match {
            case Some(l) =>
              find(l)(partial) match {
                case Some(hit) => Some(hit)
                case None      =>
                  goRight(zipper) match {
                    case None    => None
                    case Some(r) =>
                      find(r)(partial) match {
                        case None      => None
                        case Some(hit) => Some(hit)
                      }
                  }
              }

            case None =>
              goRight(zipper) match {
                case None    => None
                case Some(r) =>
                  find(r)(partial) match {
                    case None      => None
                    case Some(hit) => Some(hit)
                  }
              }
          }
      }
    }
  }

  def findReduceAction(zipper: Zipper): Option[Zipper] =
    findExplode(zipper).orElse(findSplit(zipper))

  def reduceOnce(zipper: Zipper): Option[Zipper] =
    findExplode(zipper)
      .flatMap(performExplode)
      .orElse(findSplit(zipper).flatMap(performSplit))
      .flatMap(topMost)

  def reduce(zipper: Zipper): Option[Zipper] = {
    @tailrec
    def helper(zipper: Zipper, steps: Int): Option[Zipper] =
      reduceOnce(zipper) match {
        case Some(value)        => helper(value, steps + 1)
        case None if steps == 0 => None
        case None               => Some(zipper)
      }
    helper(zipper, 0)
  }

  def reduceWithRecording(zipper: Zipper): (Option[Zipper], List[Expr]) = {
    @tailrec
    def helper(zipper: Zipper, steps: Int, recording: Queue[Expr]): (Option[Zipper], List[Expr]) =
      reduceOnce(zipper) match {
        case Some(value)        => helper(value, steps + 1, recording.enqueue(value._1))
        case None if steps == 0 => (None, recording.toList)
        case None               => (Some(zipper), recording.toList)
      }
    helper(zipper, 0, Queue.empty)
  }

  def add(left: Expr, right: Expr): Expr = {
    val sum = expr.runA(initialState(s"[$left,$right]")).get
    reduce(sum, Nil).map(_._1).getOrElse(sum)
  }

  def addListOfNumbers(lines: List[String]): Expr = {
    val expressions = lines.map(l => expr.runA(initialState(l)).get)
    expressions.tail.foldLeft(expressions.head)((acc, curr) => add(acc, curr))
  }

  def findSplit(zipper: Zipper): Option[Zipper] =
    find(zipper) { case n: Number => n.value > 9 }

  def performSplit(zipper: Zipper): Option[Zipper] =
    zipper match {
      case (n: Number, bs) => Some(splitNumber(n), bs)
      case (_: Pair, _)    => None
    }

  def performExplode(zipper: Zipper): Option[Zipper] =
    zipper match {
      case (p @ Pair(l: Number, r: Number, _), _) =>
        val zero = modify(_ => Number(0, p.depth))(zipper)

        val leftApplied: Zipper = leftSibling(zero) match {
          case None        => zero
          case Some(lefty) =>
            // we are now at the leftSibling.
            // after modifying the value, we have to go back to the right sibling (which is the zero value) to have the zipper be at the correct location
            rightSibling(modify { case Number(v, d) => Number(v + l.value, d) }(lefty))
              .getOrElse(zero)
        }

        val rightApplied = rightSibling(leftApplied)
          .map(modify { case Number(v, d) => Number(v + r.value, d) })
          .getOrElse(leftApplied)

        Some(rightApplied)

      case _ => None
    }

  def splitNumber(number: Number): Pair = {
    val l = number.value / 2
    val r = (number.value + 1) / 2
    Pair(Number(l, number.depth + 1), Number(r, number.depth + 1), number.depth)
  }

  def findExplode(zipper: Zipper): Option[Zipper] =
    find(zipper) { case p: Pair => p.depth >= 4 }

  @tailrec
  def bottomLeft(zipper: Zipper): Option[Zipper] =
    goLeft(zipper) match {
      case Some(value) => bottomLeft(value)
      case None        => Some(zipper)
    }

  @tailrec
  def bottomRight(zipper: Zipper): Option[Zipper] =
    goRight(zipper) match {
      case Some(value) => bottomRight(value)
      case None        => Some(zipper)
    }

  @tailrec
  def rightSibling(zipper: Zipper): Option[Zipper] =
    goUp(zipper) match {
      case None         => None
      case Some(parent) =>
        goRight(parent) match {
          case None                               => None
          case Some(sibling) if sibling != zipper => bottomLeft(sibling)
          case Some(sibling)                      => rightSibling(parent)
        }
    }

  @tailrec
  def leftSibling(zipper: Zipper): Option[Zipper] =
    goUp(zipper) match {
      case None         => None
      case Some(parent) =>
        goLeft(parent) match {
          case None                               => None
          case Some(sibling) if sibling != zipper => bottomRight(sibling)
          case Some(sibling)                      => leftSibling(parent)
        }
    }

  def goRight(zipper: Zipper): Option[Zipper] =
    zipper match {
      case (_: Number, _)         => None
      case (p: Pair, breadcrumbs) => Some(p.right, Crumb.RightCrumb(p.left) :: breadcrumbs)
    }

  def goUp(zipper: Zipper): Option[Zipper] =
    zipper match {
      case (t, LeftCrumb(r) :: bs)  => Some(Pair(t, r, t.depth - 1) -> bs)
      case (t, RightCrumb(l) :: bs) => Some(Pair(l, t, t.depth - 1) -> bs)
      case (_, Nil)                 => None
    }

  def modify(fn: Expr => Expr)(zipper: Zipper): Zipper =
    zipper match {
      case (exp, bs) => fn(exp) -> bs
    }

  def topMost(zipper: Zipper): Option[Zipper] =
    zipper match {
      case (t, Nil) => Some(t, Nil)
      case _        => goUp(zipper).flatMap(z => topMost(z))
    }

  def solvePart1(inputString: String)(implicit visualDebugger: VisualDebugger) = IO {
    solvePart1TreeZipper(inputString)
  }

  def solvePart1ListZipper(inputString: String)(implicit visualDebugger: VisualDebugger) = {

//     list zipper is way slower than tree zipper (even without the magnitude calculation)
//     3234ms (for listzipper) vs 132ms (for treezipper)

    val (finalResult, duration) = withTime(
      Day18List.addListOfNumbers(inputString),
    )
    visualDebugger.log(s"performed addition in $duration")
    Day18List.magnitude(finalResult, Nil).get.value.toString

  }

  def solvePart1TreeZipper(inputString: String)(implicit visualDebugger: VisualDebugger) = {

    val (finalResult, duration) = withTime(
      addListOfNumbers(inputString.trim.linesIterator.map(_.trim).filterNot(_.isEmpty).toList),
    )

    // before (immutable): 1577ms
    // after (mutable)  :   38ms
    visualDebugger.log(s"performed addition in $duration")
    visualDebugger.render(
      div(
        h3("Expression Tree"),
        div(
          visualizeTree(finalResult),
          p(cls := "font-mono", finalResult.toString),
        ),
      ),
    )

    finalResult.magnitude.toString
  }

  val myTd = td(cls := "border border-gray-300 align-baseline")
  val myTh = th(cls := "border border-gray-300 align-baseline")

  def solvePart2(inputString: String)(implicit visualDebugger: VisualDebugger) = IO {
    val numbers =
      inputString.trim.linesIterator.toList
        .filterNot(_.trim.isEmpty)
        .map { line =>
          expr.runA(initialState(line.trim))
        }
        .collect { case Some(e) => e }

    val combinations: Vector[List[Expr]] =
      numbers.combinations(2).toVector.flatMap(list => List(list, list.reverse)) // not commutative x+y != y+x

    val (results, duration) = withTime(combinations.zipWithIndex.map { case (List(e1, e2), idx) =>
      val sum = add(e1, e2)
      (idx, e1.toString, e2.toString, sum.toString, sum.magnitude)

    })
    visualDebugger.log(s"performed ${combinations.size} comparisons in $duration")

    val highest = results.maxByOption(_._5)

    visualDebugger.render(
      div(
        h3("winner"),
        table(
          cls := "border-collapse border border-gray-400",
          thead(
            tr(
              myTh("idx"),
              myTh("e1"),
              myTh("e2"),
              myTh("sum"),
              myTh("magnitude"),
            ),
          ),
          tbody(
            highest.map { case (idx, e1, e2, sum, magnitude) =>
              tr(
                myTd(idx),
                myTd(cls := "font-mono", e1),
                myTd(cls := "font-mono", e2),
                myTd(cls := "font-mono", sum),
                myTd(magnitude),
              )
            },
          ),
        ),
      ),
    )

    // 9900 combinations in 450s
    // 2,2 combinations/s --> 455 ms/combination

    highest.map(_._5.toString).getOrElse("No result found")
  }

  object InputParser {
    val char: Parsed[Char] = for {
      state <- StateT.get[Option, ParseState]
      _     <-
        if (state.expr.isEmpty)
          ().raiseError[Parsed, ParseState]
        // same: ApplicativeError[Parser, Unit].raiseError(())
        // same: StateT.liftF[Option, String, Char](None)
        else
          StateT.modify[Option, ParseState](ps => ParseState(ps.expr.tail, ps.pos + 1, ps.depth))
    } yield state.expr.head

    def peek(n: Int): Parsed[String] = StateT.inspect[Option, ParseState, String](ps => ps.expr.take(n))

    def customChar(c: Char): Parsed[Char] = matchChar(_ == c)

    def digit: Parsed[Char] = matchChar(_.isDigit)

    val digits: StateT[Option, ParseState, Int] = {
      def digits0: StateT[Option, ParseState, List[Char]] = for {
        head <- digit
        tail <- digits0 <+> StateT.pure(List())
      } yield head :: tail
      digits0.map(_.mkString.toInt) // we don't mind the overflow!
    }

    val expr: Parsed[Expr] =
      peek(1).flatMap {
        case "[" => bracketExpr
        case _   => digits.transform((ps, n) => (ps, Number(n, ps.depth)))
      }

    val bracketExpr: Parsed[Expr] = (for {
      _     <- open.modify(ps => ps.copy(depth = ps.depth + 1))
      left  <- expr
      _     <- customChar(',')
      right <- expr
      _     <- close.modify(ps => ps.copy(depth = ps.depth - 1))
    } yield (left, right)).transform { case (ps, (left, right)) =>
      (ps, Pair(left, right, ps.depth))
    }

    def matchChar(p: Char => Boolean): Parsed[Char] = for {
      char <- char
      _    <-
        if (p(char))
          StateT.get[Option, ParseState] // we don't use it, but we need to return a StateT!
        else
          ().raiseError[Parsed, Unit]
    } yield char

    def open: Parsed[Char]  = customChar('[')
    def close: Parsed[Char] = customChar(']')

    def initialState(expr: String): ParseState =
      ParseState(expr, 0, 0)
  }

  object SvgDraw {
    def visualizeTree(root: Expr): VModifier = {
      import svg._

      val canvasWidth  = 200
      val canvasHeight = 200
      val nodeSize     = 4.5

      // val mutable = MutableExpr.fromExpr(root)
      val treeInfo = Expr.calcTreeInfo(root)

      val TreeInfo(nodes, parentChild, inDfsOrder) = treeInfo
      val treeDepth                                = 5 // nodes.maxBy(_._2.depth)._2.depth + 1

      def calcPosition(meta: NodeMetadata): (Double, Double) = {
        val posX = meta.idx * canvasWidth / (math.pow(2, meta.depth) + 1)
        val posY = nodeSize * 2 + meta.depth * (canvasHeight - nodeSize * 5) / treeDepth.toDouble

        (posX, posY)
      }

      val reduceAction = findReduceAction(root, Nil).map { case (expr, breadcrumbs) =>
        val nodeMeta = calcNodeMetaFromBreadcrumbs(breadcrumbs)
        (nodeMeta, expr)
      }

      val normalColor       = "black"
      val explodeNodeColor  = "red"
      val splitNodeColodr   = "red"
      val reduceActionColor = "blue"

      def drawNode(meta: NodeMetadata, expr: Expr) = {
        val isReduceActionNode = reduceAction.map(_._1).contains(meta)
        val nodeColor          =
          if (isReduceActionNode) reduceActionColor
          else
            expr match {
              case n: Number =>
                if (n.value >= 10) splitNodeColodr else normalColor

              case _: Pair =>
                if (meta.depth >= 4) explodeNodeColor else normalColor
            }

        expr match {
          case Number(value, _) =>
            val (posX, posY) = calcPosition(meta)
            text(
              x        := (posX - nodeSize / 2).toString,
              y        := (posY + 1.5 * nodeSize).toString,
              fontSize := "8",
              cls      := "font-mono",
              fill     := nodeColor,
              value.toString,
            )

          case _: Pair =>
            val (posX, posY) = calcPosition(meta)
            rect(
              x      := (posX - nodeSize / 2).toString,
              y      := (posY - nodeSize / 2).toString,
              width  := nodeSize.toString,
              height := nodeSize.toString,
              fill   := nodeColor,// complementary color
            )
        }
      }

      val svgNodes = nodes.toList.map { case (meta, expr) =>
        drawNode(meta, expr)
      }
      val svgLines = parentChild.toList.map { case (parent, children) =>
        val (parentX, parentY) = calcPosition(parent)
        g(
          children.map { child =>
            val (childX, childY) = calcPosition(child)
            line(
              x1          := parentX.toString,
              y1          := parentY.toString,
              x2          := childX.toString,
              y2          := childY.toString,
              stroke      := normalColor,
              strokeWidth := "1",
            )
          },
        )
      }
      svg(
        cls := "w-11/12 bg-gray-100",
        viewBox := s"0 0 $canvasWidth $canvasHeight",
        svgLines,
        svgNodes,
      )
    }

  }

}

object Day18List {

  import Token._

  case class Pair(open: Open, left: Number, right: Number, close: Close)
  sealed trait Token

  object Token {
    case class Open(depth: Int)               extends Token
    case class Close(depth: Int)              extends Token
    case class Number(value: Int, depth: Int) extends Token
  }

  case class ParseState(expr: String, pos: Int, depth: Int)
  type Parsed[A] = StateT[Option, ParseState, A]

  type ListZipper[A] = (List[A], List[A])

  def goForward[A](zipper: ListZipper[A]): Option[ListZipper[A]] =
    zipper match {
      case (x :: xs, bs) => Some((xs, x :: bs))
      case _             => None
    }

  def goBack[A](zipper: ListZipper[A]): Option[ListZipper[A]] =
    zipper match {
      case (xs, b :: bs) => Some((b :: xs, bs))
      case _             => None
    }

  def goToBeginning[A](zipper: ListZipper[A]): Option[ListZipper[A]] =
    zipper match {
      case (_, Nil)     => Some(zipper)
      case (curr, rest) =>
        Some((rest.reverse ::: curr, Nil))
    }

  def addListOfNumbers(inputString: String): List[Token] = {
    val numbers = inputString.trim.linesIterator.map(_.trim).filterNot(_.isEmpty).toList.map(parse)
    numbers.tail.foldLeft(numbers.head)(add)
  }

  def add(e1: List[Token], e2: List[Token]): List[Token] = {
    val sumPair = createSumPair(e1, e2)
    reduce(sumPair, Nil).map(_._1).getOrElse(sumPair)
  }

  def createSumPair(e1: List[Token], e2: List[Token]): List[Token] =
    Open(0) :: increaseDepth(e1) ::: increaseDepth(e2) ::: Close(0) :: Nil

  def increaseDepth(e1: List[Token]): List[Token]                      =
    e1.map {
      case Open(depth)          => Open(depth + 1)
      case Close(depth)         => Close(depth + 1)
      case Number(value, depth) => Number(value, depth + 1)
    }

  @tailrec
  def reduce(zipper: ListZipper[Token]): Option[ListZipper[Token]] =
    reduceOnce(zipper) match {
      case None          => Some(zipper)
      case Some(reduced) => reduce(reduced)
    }
  def reduceOnce(zipper: ListZipper[Token]): Option[ListZipper[Token]] =
    findExplodePair(zipper)
      .flatMap(explodePair)
      .orElse(findSplit(zipper).flatMap(splitNumber))
      .flatMap(goToBeginning)

  def splitNumber(zipper: ListZipper[Token]): Option[ListZipper[Token]] =
    zipper match {
      case (::(n: Number, tail), bs) =>
        val left  = Number(n.value / 2, n.depth + 1)
        val right = Number((n.value + 1) / 2, n.depth + 1)
        val pair  = Open(n.depth) :: left :: right :: Close(n.depth) :: Nil
        Some((pair ::: tail, bs))
      case _                         => None
    }
  def explodePair(zipper: ListZipper[Token]): Option[ListZipper[Token]] =
    zipper._1.take(4) match {
      case List(_: Open, l: Number, r: Number, _: Close) =>
        // is pair
        val zero = replacePair(zipper)

        val leftApplied = zero
          .flatMap(findLeftNeighbor) match {
          case None =>
            zero

          case Some(lefty) =>
            modifyNumber(n => n.copy(n.value + l.value))(lefty) // modify number
              .flatMap(findRightNeighbor)                       // go back to right neighbor (which is the newly created zero element)
              .orElse(zero) // couldn't do anything, so we fall back to the zero element
        }

        val rightApplied = leftApplied
          .flatMap(findRightNeighbor)
          .flatMap(modifyNumber(n => n.copy(n.value + r.value)))
          .orElse(leftApplied)

        rightApplied
      case _                                             => None
    }

  def replacePair(zipper: ListZipper[Token]): Option[ListZipper[Token]] =
    zipper._1.take(4) match {
      case List(Open(d), _: Number, _: Number, _: Close) =>
        // currently at beginning of pair
        val number = Number(value = 0, depth = d)
        Some((number :: zipper._1.drop(4), zipper._2))
      case Nil                                           => None
    }

  def replaceNumberPairWithMagnitude(zipper: ListZipper[Token]): Option[ListZipper[Token]] =
    zipper._1.take(4) match {
      case List(Open(d), l: Number, r: Number, _: Close) =>
        // currently at beginning of pair
        val number = Number(value = 3 * l.value + 2 * r.value, depth = d)
        Some((number :: zipper._1.drop(4), zipper._2))
      case Nil                                           => None
    }

  @tailrec
  def magnitude(zipper: ListZipper[Token]): Option[Number] =
    zipper match {
      case (::(n: Number, Nil), Nil) => Some(n)
      case _                         =>
        goToBeginning(zipper).flatMap(findNumberPair).flatMap(replaceNumberPairWithMagnitude) match {
          case Some(updated) => magnitude(updated)
          case None          => None
        }
    }

  def modifyNumber(fn: Number => Number)(zipper: ListZipper[Token]): Option[ListZipper[Token]] =
    zipper._1 match {
      case ::(n: Number, tail) => Some((fn(n) :: tail, zipper._2))
      case Nil                 => None
    }

  def findLeftNeighbor(zipper: ListZipper[Token]): Option[ListZipper[Token]] = {
    @tailrec
    def findLeftNeighborHelper(zipper: ListZipper[Token]): Option[ListZipper[Token]] = {
      println(s"findLeftNeighborHelper(zipper: $zipper)")
      zipper._1 match {
        case ::(_: Number, _) => Some(zipper)
        case Nil              => None
        case _                =>
          goBack(zipper) match {
            case Some(newZipper) => findLeftNeighborHelper(newZipper)
            case None            => None
          }
      }
    }
    goBack(zipper).flatMap(findLeftNeighborHelper)
  }

  def findRightNeighbor(zipper: ListZipper[Token]): Option[ListZipper[Token]] = {

    @tailrec
    def findRightNeighborHelper(zipper: ListZipper[Token]): Option[ListZipper[Token]] = {
      println(s"findRightNeighborHelper(zipper: $zipper)")
      zipper._1 match {
        case ::(_: Number, _) => Some(zipper)
        case Nil              => None
        case _                =>
          goForward(zipper) match {
            case Some(newZipper) => findRightNeighborHelper(newZipper)
            case None            => None
          }
      }
    }
    // if the current node is a pair, we have to advance 4 tokens
    // if the current node is a number, we only have to advance 1
    val startingToken = zipper._1.headOption match {
      case Some(_: Number) =>
        println("  starting at a number - goForward once")
        goForward(zipper)

      case Some(_) =>
        println("  NOT starting at a number - goForward 4x")
        goForward(zipper)
          .flatMap(goForward)
          .flatMap(goForward)
          .flatMap(goForward)

      case None => None
    }
    // we are at the beginning of the pair, so we move forward 4 tokens and start searching from there
    startingToken.flatMap(findRightNeighborHelper)
  }

  @tailrec
  final def findNumberPair(zipper: ListZipper[Token]): Option[ListZipper[Token]] =
    zipper._1.take(4) match {
      case List(_: Open, _: Number, _: Number, _: Close) => Some(zipper)
      case _                                             =>
        goForward(zipper) match {
          case Some(value) => findNumberPair(value)
          case None        => None
        }
    }

  @tailrec
  final def findExplodePair(zipper: ListZipper[Token]): Option[ListZipper[Token]] =
    zipper._1.take(4) match {
      case List(Open(d), _: Number, _: Number, _: Close) if d >= 4 => Some(zipper)
      case _                                                       =>
        goForward(zipper) match {
          case Some(value) => findExplodePair(value)
          case None        => None
        }
    }

  @tailrec
  def findSplit(zipper: ListZipper[Token]): Option[ListZipper[Token]] =
    zipper._1.headOption match {
      case Some(n: Number) if n.value >= 10 => Some(zipper)
      case None                             => None
      case _                                =>
        goForward(zipper) match {
          case Some(newZipper) => findSplit(newZipper)
          case None            => None
        }
    }

  def parse(input: String): List[Token] = {

    @tailrec
    def helper(str: String, depth: Int, acc: Queue[Token]): Queue[Token] =
      str.headOption match {
        case Some('[')            => helper(str.tail, depth + 1, acc.enqueue(Open(depth)))
        case Some(']')            => helper(str.tail, depth - 1, acc.enqueue(Close(depth - 1)))
        case Some(',')            => helper(str.tail, depth, acc)
        case Some(c) if c.isDigit =>
          val numberStr = str.takeWhile(_.isDigit)
          val number    = numberStr.toInt
          helper(str.drop(numberStr.length), depth, acc.enqueue(Number(number, depth)))
        case None                 => acc
      }
    helper(input, 0, Queue.empty).toList
  }

}
