package adventOfCode.year2021

import Day19.Vec3D.{ordering, permutationFunctions}
import adventOfCode.util.Util.withTime
import adventOfCode.{AocDay, VisualDebugger, util}
import cats.effect.IO
import outwatch.dsl._

import scala.annotation.tailrec
import scala.collection.immutable.Queue

object Day19 extends AocDay {
  val day = 19

  def parseScanners(input: String): Map[String, List[Vec3D]] =
    input
      .split("\n\n")
      .toList
      .map { scannerStr =>
        val label :: coords = scannerStr.trim.linesIterator.toList.map(_.trim).filterNot(_.isEmpty)
        val vectors         = coords.map { coordStr =>
          val List(x, y, z) = coordStr.split(",").toList
          Vec3D(x.toInt, y.toInt, z.toInt)
        }
        label.replace("---", "").trim -> vectors
      }
      .toMap

  // memoizing the expensive computation
  val dpMap = collection.mutable.Map.empty[String, Map[PointCloud, Vec3D]]

  def solveScannerLocations(input: String): Map[PointCloud, Vec3D] =
    dpMap.getOrElseUpdate(
      input, {

        val scanners: Map[String, List[Vec3D]] = parseScanners(input)
        val pointClouds: List[PointCloud]      = scanners.toList
          .sortBy(_._1)
          .map { case (label, points) => PointCloud(label, "", points) }

        val overlaps     = calcOverlaps(pointClouds)
        val scannerChain = assembleScannerChain(overlaps)

        val scannerLocations =
          calcScannerAndBeaconLocations(pointClouds.find(_.label == "scanner 0").get, scannerChain)

        scannerLocations
      },
    )

  def solvePart1(input: String)(implicit visualDebugger: VisualDebugger) = IO {
    dpMap.clear()

    val (scannerLocations, timeScanner) = withTime(solveScannerLocations(input))
    val (beaconLocations, timeBeacons)  = withTime {
      scannerLocations.toList.flatMap { case (pc, location) =>
        pc.points.map(p => location + p)
      }.distinct
    }

    visualDebugger.log(s"calculated locations of ${scannerLocations.size} scanners in $timeScanner")
    visualDebugger.log(s"calculated locations of ${beaconLocations.size} beacons in $timeBeacons")

    beaconLocations.size.toString
  }

  def solvePart2(input: String)(implicit visualDebugger: VisualDebugger) = IO {

    val scannerLocations = solveScannerLocations(input)

    val (maxDistance, timeMaxDistance) = withTime {
      scannerLocations.toList
        .map(_._2)
        .combinations(2)
        .map { case List(p1, p2) =>
          Vec3D.manhattanDistance(p1, p2)
        }
        .max
    }
    visualDebugger.log(s"calculated maxDistance in $timeMaxDistance")
    maxDistance.toString
  }

  case class PointCloud(label: String, setupLabel: String, points: List[Vec3D]) {

    lazy val normalized: List[Vec3D] =
      normalizeAt(points.min)

    def normalizeAt(ref: Vec3D): List[Vec3D] = points.map { case Vec3D(x, y, z) =>
      Vec3D(x - ref.x, y - ref.y, z - ref.z)
    }

    lazy val permutations: List[PointCloud] =
      // every point could be the reference point
      Vec3D.permutationFunctions.map { case (transformationLabel, fn) =>
        PointCloud(label, s"$transformationLabel", points.map(p => fn(p)))
      }

  }

  case class Vec3D(x: Int, y: Int, z: Int) {
    def rotateAroundX: Vec3D = Vec3D(x, z, -y)
    def rotateAroundY: Vec3D = Vec3D(-z, y, x)
    def rotateAroundZ: Vec3D = Vec3D(y, -x, z)
    def flipX: Vec3D         = Vec3D(-x, y, z)
    def flipY: Vec3D         = Vec3D(x, -y, z)
    def flipZ: Vec3D         = Vec3D(x, y, -z)

    override def toString: String =
      s"($x,$y,$z)"

    def +(other: Vec3D): Vec3D =
      Vec3D(x + other.x, y + other.y, z + other.z)

    def -(other: Vec3D): Vec3D =
      Vec3D(x - other.x, y - other.y, z - other.z)

    def *(other: Vec3D): Vec3D =
      Vec3D(x * other.x, y * other.y, z * other.z)

    def sumParts: Int =
      x + y + z
  }

  def renderScanners(scanners: Map[String, List[Vec3D]])(implicit visualDebugger: VisualDebugger) = {
