  }
  def renderRotations(origin: Vec3D)(implicit visualDebugger: VisualDebugger): Unit = {
    val all: List[(String, Vec3D)] = permutationFunctions.map { case (label, fn) => (label, fn(origin)) }

    import util.ui.Helpers.FormattedTable._
    visualDebugger.render(
      div(
        table(
          thead(
            tr(
              th("configuration"),
              th(cls := "text-right", "x"),
              th(cls := "text-right", "y"),
              th(cls := "text-right", "z"),
            ),
          ),
          tbody(
            all.sortBy { case (label, Vec3D(x, y, z)) =>
              (x, y, z, label.length)
            }.map { case tup @ (label, Vec3D(x, y, z)) =>
              tr(
                td(label),
                td(cls := "text-right", x),
                td(cls := "text-right", y),
                td(cls := "text-right", z),
              )
