  }

  object Vec3D {

    def manhattanDistance(p1: Vec3D, p2: Vec3D): Int =
      math.abs(p2.x - p1.x) +
        math.abs(p2.y - p1.y) +
        math.abs(p2.z - p1.z)

    implicit val ordering: Ordering[Vec3D] = Ordering.by { case Vec3D(x, y, z) => (x, y, z) }

    // https://www.euclideanspace.com/maths/algebra/matrix/transforms/examples/index.htm
    val validRotations = List(
      List(Vec3D(1, 0, 0), Vec3D(0, 1, 0), Vec3D(0, 0, 1)),
      List(Vec3D(1, 0, 0), Vec3D(0, 0, -1), Vec3D(0, 1, 0)),
      List(Vec3D(1, 0, 0), Vec3D(0, -1, 0), Vec3D(0, 0, -1)),
      List(Vec3D(1, 0, 0), Vec3D(0, 0, 1), Vec3D(0, -1, 0)),
      List(Vec3D(0, -1, 0), Vec3D(1, 0, 0), Vec3D(0, 0, 1)),
      List(Vec3D(0, 0, 1), Vec3D(1, 0, 0), Vec3D(0, 1, 0)),
      List(Vec3D(0, 1, 0), Vec3D(1, 0, 0), Vec3D(0, 0, -1)),
      List(Vec3D(0, 0, -1), Vec3D(1, 0, 0), Vec3D(0, -1, 0)),
      List(Vec3D(-1, 0, 0), Vec3D(0, -1, 0), Vec3D(0, 0, 1)),
      List(Vec3D(-1, 0, 0), Vec3D(0, 0, -1), Vec3D(0, -1, 0)),
      List(Vec3D(-1, 0, 0), Vec3D(0, 1, 0), Vec3D(0, 0, -1)),
      List(Vec3D(-1, 0, 0), Vec3D(0, 0, 1), Vec3D(0, 1, 0)),
      List(Vec3D(0, 1, 0), Vec3D(-1, 0, 0), Vec3D(0, 0, 1)),
      List(Vec3D(0, 0, 1), Vec3D(-1, 0, 0), Vec3D(0, -1, 0)),
      List(Vec3D(0, -1, 0), Vec3D(-1, 0, 0), Vec3D(0, 0, -1)),
      List(Vec3D(0, 0, -1), Vec3D(-1, 0, 0), Vec3D(0, 1, 0)),
      List(Vec3D(0, 0, -1), Vec3D(0, 1, 0), Vec3D(1, 0, 0)),
      List(Vec3D(0, 1, 0), Vec3D(0, 0, 1), Vec3D(1, 0, 0)),
      List(Vec3D(0, 0, 1), Vec3D(0, -1, 0), Vec3D(1, 0, 0)),
      List(Vec3D(0, -1, 0), Vec3D(0, 0, -1), Vec3D(1, 0, 0)),
      List(Vec3D(0, 0, -1), Vec3D(0, -1, 0), Vec3D(-1, 0, 0)),
      List(Vec3D(0, -1, 0), Vec3D(0, 0, 1), Vec3D(-1, 0, 0)),
      List(Vec3D(0, 0, 1), Vec3D(0, 1, 0), Vec3D(-1, 0, 0)),
      List(Vec3D(0, 1, 0), Vec3D(0, 0, -1), Vec3D(-1, 0, 0)),
    )

    val permutationFunctions: List[(String, Vec3D => Vec3D)] =
      validRotations.map(rotMatrix => (rotMatrix.map(_.toString).mkString(", "), transform(_, rotMatrix)))

    def transform(point: Vec3D, rotation: List[Vec3D]): Vec3D = {
      val List(x, y, z) = rotation.map(r => (r * point).sumParts)

      Vec3D(x, y, z)
    }
  }

  def calcScannerLocation(overlap: OverlapResult): Vec3D = {
    val distances = overlap.pointMappingToTransformed.map { case (p1, p2Mut) => p1 - p2Mut }.distinct
    assert(distances.size == 1)
    distances.head
  }

  def calcScannerAndBeaconLocations(origin: PointCloud, scannerChain: List[OverlapResult]): Map[PointCloud, Vec3D] =
    scannerChain.foldLeft(Map(origin -> Vec3D(0, 0, 0))) { (acc, res) =>
      val p1Absolute         = acc(res.original1)
      val relativeP2Location = calcScannerLocation(res)
      val p2Absolute         = p1Absolute + relativeP2Location
      acc.updated(res.transformed2, p2Absolute)
    }

  def assembleScannerChain(combinations: List[(PointCloud, PointCloud)]): List[OverlapResult] = {

    val all = combinations ++ combinations.map(_.swap)

    def helper(
      next: Queue[PointCloud],
      reached: Set[PointCloud],
      result: Queue[OverlapResult],
    ): List[OverlapResult] =
      // scanner 0
      next.dequeueOption match {
        case Some((current, rest)) =>
          // find all combos starting from the current position that haven't been visited
          val combos                              = all.filter { case (start, end) =>
            start.label == current.label && !reached.map(_.label).contains(end.label)
          }
          val newElements                         = combos.map(_._2)
          val overlapResults: List[OverlapResult] =
            newElements.map(pc => findSetupOfAlreadyMatchingPairs(current, pc).get)
          val alignedNewElements                  = overlapResults.map(_.transformed2)
          helper(
            rest.enqueueAll(alignedNewElements),
            reached.union(newElements.toSet),
            result.enqueueAll(overlapResults),
          )
        case None                  => result.toList
      }
    // we'll start at scanner 0
    val allPoints = all.map(_._1).distinct.sortBy(_.label)
    val scanner0  = allPoints.head
    assert(scanner0.label.contains("scanner 0"))
    helper(next = Queue(scanner0), reached = Set(scanner0), result = Queue.empty)
  }

  def calcOverlaps(pointClouds: List[PointCloud]): List[(PointCloud, PointCloud)] =
    pointClouds
      .combinations(2)
      .toList
      .map { case List(pc1, pc2) =>
        (pc1, pc2, isMatch(pc1, pc2))
      }
      .filter(_._3)
      .map(t => (t._1, t._2))

  case class OverlapResult(
    original1: PointCloud,
    original2: PointCloud,
    transformed2: PointCloud,
    intersection: List[Vec3D],
    pointMappingToTransformed: List[(Vec3D, Vec3D)],
    pointMappingToOriginal: List[(Vec3D, Vec3D)],
  )

  def isMatch(original1: PointCloud, original2: PointCloud): Boolean = {
    // we measure the absolute point-to-point distances of every pair in each pointcloud
    // since we only measure the absolute distances, we have to consider 12 take 2 = 66 as the criterium
    val distanceDetailsOriginal1 = distances(original1)
    val distanceDetailsOriginal2 = distances(original2)
    val overlaps                 = distanceDetailsOriginal1.map(_._4).toSet.intersect(distanceDetailsOriginal2.map(_._4).toSet)

    overlaps.size >= 66
  }

  /** Calculates the distances between each point-pair in the pointcloud.
    */
  def distances(originalPC: PointCloud): List[(Vec3D, Vec3D, Vec3D, Int)] = originalPC.points
    .combinations(2)
    .toList
    .map { case List(p1, p2) =>
      val distance    = p2 - p1
      val magDistance = math.abs(distance.x) + math.abs(distance.y) + math.abs(distance.z)
      (p1, p2, distance, magDistance)
    }

  def findSetupOfAlreadyMatchingPairs(original1: PointCloud, original2: PointCloud): Option[OverlapResult] = {

    // we measure the absolute point-to-point distances of every pair in each pointcloud
    // since we only measure the absolute distances, we have to consider 12 take 2 = 66 as the criterium
    val distanceDetailsOriginal1 = distances(original1)
    // if we have 12 matches, we're going to find the exact transformation of original2.
    // this is a bit more time consuming, so we only do this, if we know that we have a match.

    val candidates = LazyList
      .from(original2.permutations)

    candidates.find { pc2 =>
      val distanceDetailsP2 = distances(pc2)

      val overlaps = distanceDetailsOriginal1.map(_._3).toSet.intersect(distanceDetailsP2.map(_._3).toSet)
      overlaps.size >= 12
    }.map { pc2 =>
      val distanceDetailsP2    = distances(pc2)
      val overlaps             = distanceDetailsOriginal1.map(_._3).intersect(distanceDetailsP2.map(_._3))
      val overlapsRelativeToP1 = distanceDetailsOriginal1.filter(tup => overlaps.contains(tup._3))
      val overlapsRelativeToP2 = distanceDetailsP2.filter(tup => overlaps.contains(tup._3))

      val seenFrom1 = overlapsRelativeToP1.map { case (p1, p2, distanceVector, _) =>
        distanceVector -> (p1, p2)
      }.toMap
      val seenFrom2 = overlapsRelativeToP2.map { case (p1, p2, distanceVector, _) =>
        distanceVector -> (p1, p2)
      }.toMap

      val pointMappingToTransformed = seenFrom1.keys.toList.flatMap { distanceVector =>
        val pc1Points = seenFrom1(distanceVector)
        val pc2Points = seenFrom2(distanceVector)
        List(
          (pc1Points._1, pc2Points._1),
          (pc1Points._2, pc2Points._2),
        )
      }.distinct

      val pointMappingToOriginal = pointMappingToTransformed.map { case (fromOriginal, fromTransformedPC2) =>
        // the pointMapping already contains the transformed coordinates
        // the testcase uses the original coordinates
        val fromScanner1Idx = pc2.points.indexOf(fromTransformedPC2)
        val fromOriginal2   = original2.points(fromScanner1Idx)
        (fromOriginal, fromOriginal2)
      }

      OverlapResult(
        original1,
        original2 = original2,
        transformed2 = pc2,
        intersection = overlaps,
        pointMappingToTransformed,
        pointMappingToOriginal,
      )
    }

    // OverlapResult(original1, original2, p1, p2, p1.points.intersect(p2.points))

  }

  def findOverlapping(pointClouds: List[PointCloud]): List[OverlapResult] = {

    @tailrec
    def helper(
      queue: Queue[PointCloud],
      alreadyMatched: Set[PointCloud],
      result: List[OverlapResult],
    ): List[OverlapResult] =
      queue.dequeueOption match {
        case None                  => result
        case Some((toCheck, rest)) =>
          val evaluateAgainst = rest.toSet.diff(Set(toCheck)).diff(alreadyMatched)

          val (results, time) = withTime {
            val possibilities: LazyList[(PointCloud, PointCloud)] =
              LazyList
                .from(evaluateAgainst)
                .map(pc2 => (toCheck, pc2))
            val matches                                           = possibilities.flatMap { case (pc1, pc2) =>
              findSetupOfAlreadyMatchingPairs(pc1, pc2)
            }
            matches.toList
          }

          println(
            s"  ${toCheck.label}: matches: ${results.size}; Time: $time; candidates: ${evaluateAgainst.size}; toCheck: ${queue.size}; alreadyMatched: ${alreadyMatched.size}",
          )

          results match {
            case Nil =>
              helper(rest, alreadyMatched, result)

            case _ =>
              val leftMatches = results.map(_.original1)
              results.foreach(r =>
                println(s"    ${toCheck.label}: found match between ${r.original1.label} and ${r.original2.label}"),
              )
              helper(
                rest,
                alreadyMatched.union(leftMatches.toSet),
                results ::: result,
              )
          }
      }
    helper(Queue.from(pointClouds), Set.empty, List.empty)
  }

}
