package adventOfCode.year2021

import adventOfCode.{AocDay, VisualDebugger}
import cats.effect.IO

object Day02 extends AocDay {

  val day = 2

  case class Position(horizontal: Int, depth: Int) {
    def add(other: Position): Position = {
      Position(horizontal + other.horizontal, depth + other.depth)
    }

    def move(m: Move): Position = {
      /*
        forward 5
        down 5
        forward 8
        up 3
        down 8
        forward 2


        forward X increases the horizontal position by X units.
        down X increases the depth by X units.
        up X decreases the depth by X units.

       */
      val delta = m.direction match {
        case Direction.Forward => Position(horizontal = m.length, depth = 0)
        case Direction.Down    => Position(horizontal = 0, depth = m.length)
        case Direction.Up      => Position(horizontal = 0, depth = -m.length)
      }

      add(delta)
    }
  }

  case class PositionPart2(horizontal: Int, depth: Int, aim: Int) {

    def move(m: Move): PositionPart2 = {
      /*
        down X increases your aim by X units.
        up X decreases your aim by X units.
        forward X does two things:
        It increases your horizontal position by X units.
        It increases your depth by your aim multiplied by X.
       */

      val updated = m.direction match {
        case Direction.Forward =>
          this.copy(
            horizontal = horizontal + m.length,
            depth = depth + aim * m.length
          )
        case Direction.Down => this.copy(aim = aim + m.length)
        case Direction.Up   => this.copy(aim = aim - m.length)

      }
      updated
    }
  }

  case class Move(direction: Direction, length: Int)

  object Move {
    def parse(str: String): Move = {
      val List(dir, length) = str.split(" ").toList
      Move(Direction.parse(dir), length.toInt)
    }
  }
  sealed trait Direction

  object Direction {
    case object Forward extends Direction
    case object Down extends Direction
    case object Up extends Direction

    def parse(str: String): Direction = {
      str match {
        case "forward" => Forward
        case "down"    => Down
        case "up"      => Up
      }
    }
  }

  def solvePart1(input: String)(implicit visualDebugger: VisualDebugger) = IO{
    val finalPos = input.linesIterator
      .filterNot(_.isEmpty)
      .map(Move.parse)
      .foldLeft(Position(0, 0))(_.move(_))

    val result = finalPos.depth * finalPos.horizontal
    result.toString
  }
  def solvePart2(input: String)(implicit visualDebugger: VisualDebugger) = IO{
    val finalPos = input.linesIterator
      .filterNot(_.isEmpty)
      .map(Move.parse)
      .foldLeft(PositionPart2(0, 0, 0))(_.move(_))

    val result = finalPos.depth * finalPos.horizontal
    result.toString

  }

}
