package adventOfCode.year2022

import adventOfCode.util.Parsers._
import adventOfCode.util.ui.Helpers.expandableDivButton
import adventOfCode.{AocDay, VisualDebugger}
import cats.data.NonEmptyList
import cats.effect.IO
import cats.implicits.{catsSyntaxOptionId, catsSyntaxSemigroup}
import cats.parse.Numbers.bigInt
import cats.parse.{Parser => P, Parser0}
import colibri.Subject
import outwatch.VModifier
import outwatch.dsl._

import scala.annotation.tailrec
import scala.collection.immutable.Queue

object Day11 extends AocDay {
  val day = 11

  val whitespace: P[Unit]         = P.charIn(" \t\r\n").void
  val whitespaces0: Parser0[Unit] = whitespace.rep0.void

  type MonkeyMap     = Map[Int, Monkey]
  type MonkeySummary = Map[Int, BigInt]

  val opP = P.oneOf(
    List(
      P.char('+').as(Op.Addition),
      P.char('*').as(Op.Multiplication),
    ),
  )

  private val monkeyIdP      = P.string("Monkey ") *> intParser <* P.char(':')
  private val startingItemsP = P.string("Starting items: ") *> P.repSep0(bigInt, P.string(", "))
  private val operandP       = bigInt.map(Operand.IntValue) | P.string("old").as(Operand.Self)
  private val operationP     = (P.string("Operation: new = old ") *> opP <* P.char(' ')) ~ operandP
  private val testP          = P.string("Test: divisible by ") *> intParser
  private val throwToP       = P.string("throw to monkey ") *> intParser
  private val trueAction     = P.string("If true: ") *> throwToP
  private val falseAction    = P.string("If false: ") *> throwToP

  val monkeyP: P[Monkey] =
    (monkeyIdP ~ (whitespaces0 *> startingItemsP) ~ (whitespaces0 *> operationP) ~ (whitespaces0 *> testP) ~
      (whitespaces0 *> trueAction) ~
      (whitespaces0 *> falseAction)).map {
      case (((((monkeyId, startingItems), operation), test), trueAction), falseAction) =>
        Monkey(monkeyId, Queue.from(startingItems), operation, test, trueAction, falseAction)
    }

  val inputP: P[NonEmptyList[Monkey]] = monkeyP.repSep(whitespaces0)

  case class Monkey(
    id: Int,
    items: Queue[BigInt],
    operation: (Op, Operand),
    testDivisible: Int,
    trueThrowReceiver: Int,
    falseThrowReceiver: Int,
  )

  def solvePart1(input: String)(implicit visualDebugger: VisualDebugger) = IO {
    val parseResult                   = inputP.parse(input)
    val monkeys: NonEmptyList[Monkey] = parseResult.toOption.get._2

    val monkeyMap: MonkeyMap = monkeys.toList.map(m => m.id -> m).toMap

    val states = playNRounds(monkeyMap, 20)

    visualDebugger.render(
      expandableDivButton(
        "Details of move 1",
        false, {
          val details = states.head._2.groupBy(_.monkey.id).toList.sortBy(_._1).map { case (mId, actions) =>
            s"""Monkey $mId:
               |${actions.map(ma => renderMonkeyAction(ma)).mkString("\n")}""".stripMargin
          }

          details.mkString("\n")
        },
      ),
    )

    visualDebugger.render(
      expandableDivButton(
        "Rounds",
        false,
        states.zipWithIndex.map { case ((s, _), idx) =>
          s"""After round ${idx + 1}, the monkeys are holding items with these worry levels
             |${renderMonkeyState(s)}""".stripMargin
        }.mkString("\n\n"),
      ),
    )

    val inspectionCount =
      states.flatMap(_._2).groupBy(_.monkey.id).map { case (id, actions) =>
        id -> actions.size
      }

    inspectionCount.toList.sorted.foreach { case (id, cnt) =>
      visualDebugger.log(s"Monkey $id inspected items $cnt times.")
    }

    val result = inspectionCount.toList.sortBy(_._2 * -1).take(2).map(_._2).product

    result.toString
  }

  def solvePart2(input: String)(implicit visualDebugger: VisualDebugger) = IO {

    val parseResult                   = inputP.parse(input)
    val monkeys: NonEmptyList[Monkey] = parseResult.toOption.get._2

    val monkeyMap: MonkeyMap = monkeys.toList.map(m => m.id -> m).toMap
    val testDivisibles       = monkeys.map(m => m.testDivisible)
    val testDivisiblesLcm    = lcm(testDivisibles.toList.map(v => v: BigInt))

    visualDebugger.log(s"lcm of all testDivisibles ($testDivisibles) = $testDivisiblesLcm")

    val inspectionCount = playNRoundsFast(monkeyMap, 10000, testDivisiblesLcm)

    inspectionCount.toList.sorted.foreach { case (id, cnt) =>
      visualDebugger.log(s"Monkey $id inspected items $cnt times.")
    }

    val result = inspectionCount.toList.sortBy(_._2 * -1).take(2).map(_._2).product

    result.toString
  }

  def playNRoundsFast(initial: MonkeyMap, numberOfGames: Int, testDivisiblesLcm: BigInt) = {

    def recurse(
      current: MonkeyMap,
      restGames: Int,
      result: MonkeySummary,
    ): MonkeySummary = {
      val numGamesPlayed = numberOfGames - restGames
      if (numGamesPlayed % 100 == 0) {
        println(s"$numGamesPlayed games played")
      }
      if (restGames == 0) {
        result
      }
      else {
        val (newState, actions) = processOneRound(current, testDivisiblesLcm.some)

        val inspectionCount = actions.groupBy(_.monkey.id).map { case (id, actions) =>
          id -> (actions.size: BigInt)
        }
        recurse(newState, restGames - 1, result |+| inspectionCount)
      }
    }

    recurse(initial, numberOfGames, Map.empty)
  }

  def lcm(list: Seq[BigInt]): BigInt = list.foldLeft(1: BigInt) { (a, b) =>
    b * a /
      LazyList.iterate((a, b)) { case (x, y) => (y, x % y) }.dropWhile(_._2 != 0).head._1.abs
  }

  sealed trait Op
  object Op {
    case object Addition       extends Op
    case object Multiplication extends Op
  }

  sealed trait Operand

  object Operand {
    case object Self               extends Operand
    case class IntValue(v: BigInt) extends Operand
  }

  case class MonkeyAction(
    monkey: Monkey,
    item: BigInt,
    rest: Queue[BigInt],
    newWorryLevel: BigInt,
    afterBoredValue: BigInt,
    testResult: Boolean,
    throwReceiver: Int,
  )

  def playNRounds(initial: MonkeyMap, numberOfGames: Int) = {

    def recurse(
      current: MonkeyMap,
      restGames: Int,
      result: List[(MonkeyMap, List[MonkeyAction])],
    ): List[(MonkeyMap, List[MonkeyAction])] =
      if (restGames == 0) {
        result.reverse
      }
      else {
        val (newState, actions) = processOneRound(current, None)
        recurse(newState, restGames - 1, (newState, actions) :: result)
      }

    recurse(initial, numberOfGames, List.empty)
  }

  def calcMonkeyAction(m: Monkey, maybeTestDivisiblesLcm: Option[BigInt]): Option[MonkeyAction] =
    m.items.dequeueOption match {
      case None               =>
        None
      case Some((item, rest)) =>
        val op1            = item
        val (op, operand2) = m.operation
        val op2            = operand2 match {
          case Operand.Self        => item
          case Operand.IntValue(v) => v
        }
        val newWorryLevel  = op match {
          case Op.Addition       => op1 + op2
          case Op.Multiplication => op1 * op2
        }

        val afterBoredValue = maybeTestDivisiblesLcm match {
          case Some(lcm) => newWorryLevel % lcm
          case None      => newWorryLevel / 3
        }

//        println("calcMonkeyAction")
//        println(s"m: $m")
//        println(s"newWorryLevel: $newWorryLevel")
//        println(s"afterBoredValue: $afterBoredValue")
//
        val testResult    = afterBoredValue % m.testDivisible == 0
        val throwReceiver = if (testResult) m.trueThrowReceiver else m.falseThrowReceiver

        MonkeyAction(m, item, rest, newWorryLevel, afterBoredValue, testResult, throwReceiver).some
    }

  def renderMonkeyState(monkeyMap: MonkeyMap): String =
    monkeyMap.keys.toList.sorted.map { id =>
      val m = monkeyMap(id)
      s"Monkey $id: ${m.items.map(_.toString).mkString(", ")}"
    }.mkString("\n")

  def processMonkeyMove(monkey: Monkey, action: MonkeyAction): Monkey =
    monkey.copy(items = action.rest)

  @tailrec
  def handleMonkeysTurn(
    monkey: Monkey,
    maybeTestDivisiblesLcm: Option[BigInt],
    res: List[MonkeyAction] = List.empty,
  ): (Monkey, List[MonkeyAction]) =
    calcMonkeyAction(monkey, maybeTestDivisiblesLcm) match {
      case None         => monkey -> res.reverse
      case Some(action) =>
        val newState = processMonkeyMove(monkey, action)
        handleMonkeysTurn(newState, maybeTestDivisiblesLcm, action :: res)
    }

  def processOneRound(monkeyMap: MonkeyMap, maybeTestDivisiblesLcm: Option[BigInt]): (MonkeyMap, List[MonkeyAction]) = {

    def process(
      ids: List[Int],
      current: MonkeyMap,
      actions: List[MonkeyAction],
    ): (MonkeyMap, List[MonkeyAction]) =
      ids match {
        case Nil                 => (current, actions)
        case ::(currentId, next) =>
          val (newState, allActionsOfMonkey) = handleMonkeysTurn(current(currentId), maybeTestDivisiblesLcm)
          val newStateOfAllMonkeys           = allActionsOfMonkey.foldLeft(current.updated(newState.id, newState)) {
            (acc, currAction) =>
              val receiver = acc(currAction.throwReceiver)
              acc.updated(receiver.id, receiver.copy(items = receiver.items.enqueue(currAction.afterBoredValue)))
          }
          process(next, newStateOfAllMonkeys, actions ++ allActionsOfMonkey)
      }

    process(monkeyMap.keys.toList.sorted, monkeyMap, List.empty)
  }

  def renderMonkeyAction(ma: MonkeyAction): String = {
    val operationName = ma.monkey.operation._1 match {
      case Op.Addition       => "increases by"
      case Op.Multiplication => "is multiplied by"
    }
    val operand2Name  = ma.monkey.operation._2 match {
      case Operand.Self        => "itself"
      case Operand.IntValue(v) => v.toString
    }
    s"""  Monkey inspects an item with a worry level of ${ma.item}.
       |    Worry level ${operationName} $operand2Name to ${ma.newWorryLevel}.
       |    Monkey gets bored with item. Worry level is divided by 3 to ${ma.afterBoredValue}.
       |    Current worry level is ${if (ma.testResult) "" else "not "}divisible by ${ma.monkey.testDivisible}.
       |    Item with worry level ${ma.afterBoredValue} is thrown to monkey ${ma.throwReceiver}.""".stripMargin
  }

}

