package adventOfCode

import adventOfCode.year2021.Pages2021.pages2021
import adventOfCode.year2022.Pages2022.pages2022
import colibri.Subject
import colibri.router._
import outwatch.Attr

sealed trait Page {
  final def href: Attr = outwatch.dsl.href := s"#${Page.toPath(this).pathString}"
}
object Page       {
  case object Landing                     extends Page
  case class LandingYear(year: Int)       extends Page
  case class DayPage(year: Int, day: Int) extends Page
  case object Day18Debug                  extends Page

  def fromPath(path: Path) =
    path match {
      case Root / "2021" / "day-18-debug"                         => Page.Day18Debug
      case Root / year / "day" / num if yearDayChecker(year, num) => Page.DayPage(year.toInt, num.toInt)
      case Root / year if yearChecker(year)                       => Page.LandingYear(year.toInt)
      case _                                                      => Page.Landing
    }

  def yearChecker(year: String): Boolean =
    intParser(year).exists(yearExists)

  def yearDayChecker(year: String, day: String): Boolean =
    intParser(year)
      .flatMap(y => intParser(day).map(d => y -> d))
      .exists { case (y, d) => dayExists(y, d) }

  def intParser(str: String): Option[Int] =
    str.toIntOption

  def toPath(page: Page): Path = page match {
    case Page.DayPage(year, day) => Root / year.toString / "day" / day.toString
    case Page.Day18Debug         => Root / "2021" / "day-18-debug"
    case Page.LandingYear(year)  => Root / year.toString
    case Page.Landing            => Root
  }

  val yearDayPages = Map(
    2021 -> pages2021,
    2022 -> pages2022,
  )

  def yearExists(year: Int): Boolean =
    yearDayPages.contains(year)

  def dayExists(year: Int, day: Int): Boolean =
    yearDayPages.get(year) match {
      case Some(yearMap) => yearMap.get(day).flatten.isDefined
      case None          => false
    }

  val current: Subject[Page] = Router.path
    .imapSubject[Page](Page.toPath)(Page.fromPath)
}
