package adventOfCode.util

import cats.parse.{Numbers, Parser => P}

object Parsers {

  val intParser: P[Int] = Numbers.signedIntString.map(_.toInt)
  val rangeParser: P[Range.Inclusive] = ((intParser <* P.char('-')) ~ intParser).map { case (start, end) => start.to(end) }

  val identityStringParser: P[String] = P.anyChar.rep.map(nel => String.valueOf(nel.toList.toArray))

}
