package adventOfCode

import cats.effect.IO
import outwatch.VModifier
import outwatch.dsl._

trait AocDay {
  val day: Int
  def solvePart1(input: String)(implicit visualDebugger: VisualDebugger): IO[String]
  def solvePart2(input: String)(implicit visualDebugger: VisualDebugger): IO[String]

  def pageLayout: PageLayout = PageLayout.Horizontal
}


sealed trait PageLayout

object PageLayout {
  case object Vertical   extends PageLayout
  case object Horizontal extends PageLayout
}

class VisualDebugger {

  private val logger     = collection.mutable.ListBuffer.empty[String]
  private val visualizer = collection.mutable.ListBuffer.empty[VModifier]

  def log(message: Any, optionalParams: Any*): Unit =
    logger.addOne(
      message.toString + " " + optionalParams.map(_.toString).mkString(" "),
    )

  def getLogLines: List[String] = logger.toList


  def getLogContent = pre(
    logger.mkString("\n"),
  )

  def getRenderContent: VModifier = div(visualizer.toList)

  def render(showContent: VModifier): Unit =
    visualizer.addOne(showContent)

  def clear(): Unit = {
    logger.clear()
    visualizer.clear()
  }
}

object VisualDebugger {
  val noOpLoggingDebugger: VisualDebugger = new VisualDebugger {
    override def log(message: Any, optionalParams: Any*): Unit = ()
  }
}
