package adventOfCode.year2021

import adventOfCode.{AocDay, VisualDebugger}
import cats.effect.IO
import outwatch.VModifier
import outwatch.dsl._

object Day13 extends AocDay {
  val day = 13

  sealed trait FoldAxis

  object FoldAxis {
    case object XAxis extends FoldAxis
    case object YAxis extends FoldAxis
  }

  case class Pos(x: Int, y: Int) {
    override def toString: String = s"($x, $y)"
  }

  object Pos {
    def parse(str: String): Pos = {
      val List(x, y) = str.split(",").toList
      Pos(x.toInt, y.toInt)
    }
  }

  case class FoldInstruction(orientation: FoldAxis, line: Int)

  object FoldInstruction {
    def parse(line: String): FoldInstruction = {
      val List(axis, lineIdx) =
        line.replace("fold along ", "").split("=").toList
      val a = axis match {
        case "y" => FoldAxis.YAxis
        case "x" => FoldAxis.XAxis
      }
      FoldInstruction(a, lineIdx.toInt)
    }
  }

  case class Input(grid: Grid, folds: List[FoldInstruction])

  case class Grid(dots: List[Pos]) {
    val (width, height) = dots.foldLeft((0, 0)) { case ((w, h), Pos(x, y)) =>
      math.max(w, x + 1) -> math.max(h, y + 1)
    }
  }

  def parse(input: String): Input = {
    val dots =
      input.trim.linesIterator.toList.takeWhile(_.trim.nonEmpty).map(Pos.parse)
    val foldInstructions = input.trim.linesIterator.toList
      .dropWhile(_.nonEmpty)
      .filterNot(_.isEmpty)
      .map(FoldInstruction.parse)

    Input(Grid(dots), foldInstructions)
  }

  def performFold(
      grid: Grid,
      foldInstruction: FoldInstruction
  ): Grid = {

    /*
4,0
x=2
--> 0,0
..|.#
#.

6,0
x=4
-->3,0
....|.#
..#.

5,0
x=4
--> 4-(5-4) = 3,0
....|#.
...#



x-offset from fold-line is 2
2-2 = 0
newX = 0
     */

    val pivotPoint = foldInstruction.line

    val newGrid = foldInstruction.orientation match {
      case FoldAxis.YAxis =>
        val (toBeFoldedDots, nonFoldedDots) =
          grid.dots.partition(_.y > pivotPoint)
        val foldedDots = toBeFoldedDots.map { case Pos(x, y) =>
          Pos(x, pivotPoint - (y - pivotPoint))
        }
        Grid((nonFoldedDots ++ foldedDots).distinct)

      case FoldAxis.XAxis =>
        val (toBeFoldedDots, nonFoldedDots) =
          grid.dots.partition(_.x > pivotPoint)
        val foldedDots = toBeFoldedDots.map { case Pos(x, y) =>
          Pos(pivotPoint - (x - pivotPoint), y)
        }
        Grid((nonFoldedDots ++ foldedDots).distinct)
    }

    println(s"original dimensions: (${grid.width}x${grid.height})")
    println(s"     new dimensions: (${newGrid.width}x${newGrid.height})")
    newGrid
  }

  def solvePart1(input: String)(implicit visualDebugger: VisualDebugger) = IO{
    val in = parse(input)

    val firstFold = in.folds.head
    val newGrid = performFold(in.grid, firstFold)
    val result = newGrid.dots.size
    result.toString
  }

  def solvePart2(input: String)(implicit visualDebugger: VisualDebugger) = IO{
    val in = parse(input)

    val finalGrid = in.folds.foldLeft(in.grid)(performFold)

    visualDebugger.log(
      s"dimensions of final grid: (${finalGrid.width} x ${finalGrid.height})"
    )

    visualDebugger.render(visualizeGrid(finalGrid))

    "use your eyes and tell me what you see"
  }

  def visualizeGrid(grid: Grid): VModifier = {
    import svg._

    svg(
      cls := "w-11/12 bg-purple-600",
      viewBox := s"0 0 ${grid.width * 5} ${grid.height * 5}",
      grid.dots.map { pos =>
        rect(
          x := (0.25 + pos.x * 5).toString,
          y := (0.25 + pos.y * 5).toString,
          width := "4.5",
          height := "4.5",
          fill := "#ede13a" //complementary color
        )
      }
    )
  }
  //

}
