package adventOfCode.year2021

import adventOfCode.{AocDay, VisualDebugger}
import cats.effect.IO

object Day06 extends AocDay {

  val day = 6

  def solvePart1(input: String)(implicit visualDebugger: VisualDebugger) = IO{
    solve(input, numberOfDays = 80)
  }

  def solvePart2(input: String)(implicit visualDebugger: VisualDebugger) = IO{
    solve(input, numberOfDays = 256)
  }

  def solve(input: String, numberOfDays: Int)(implicit
      visualDebugger: VisualDebugger
  ): String = {
    val lines = input.linesIterator.toList
    val overview = parseState(lines.head)
    val result = evolveFishes(numberOfDays, overview)

    visualDebugger.log(s"State after $numberOfDays days")
    result.toList
      .sortBy(_._1)
      .foreach(kv => visualDebugger.log(s"     ${kv._1}: ${kv._2}"))

    result.values.sum.toString
  }

  def parseState(line: String): Map[Int, Long] =
    line.split(",").map(_.toInt).groupMapReduce(identity)(_ => 1L)(_ + _)

  def evolveFishes(
      numberOfDays: Int,
      fishesState: Map[Int, Long]
  ): Map[Int, Long] = {

    1
      .to(numberOfDays)
      .foldLeft(fishesState) { (fs, i) =>
        tick(fs)
      }
  }

  def tick(fishesState: Map[Int, Long]): Map[Int, Long] = {
    val updated = fishesState.keys
      .filterNot(_ == 0)
      .map { k =>
        k - 1 -> fishesState(k)
      }
      .toMap

    import cats.implicits._
    val zeroes = fishesState
      .get(0)
      .map(zeroValue => Map(6 -> zeroValue, 8 -> zeroValue))
      .getOrElse(Map.empty)

    // the map might already contain a 6, so we have to add them together with the newly added sixes
    updated |+| zeroes
  }

}
