package adventOfCode.year2021

import adventOfCode.{AocDay, VisualDebugger}
import cats.effect.IO
import colibri.Subject
import outwatch.dsl._
import outwatch.{VModifier, VNode}

object Day04 extends AocDay {

  val day = 4

  case class BingoBoard(rows: Vector[Vector[Int]]) {
    def hasWon(drawn: List[Int]): Boolean = {
      sets.exists(v => v.intersect(drawn).size == width)
    }

    val allNumbers = rows.flatten

    val colums = rows.transpose
    val sets = rows ++ colums
    val width = rows.head.length
    val height = rows.length
  }

  def parseBoards(
      lines: List[String],
      current: List[Vector[Int]] = Nil,
      result: List[List[Vector[Int]]] = Nil
  ): List[BingoBoard] = {
    lines match {
      case ::(head, next) if head.isEmpty =>
        parseBoards(next, Nil, current :: result)
      case ::(head, next) =>
        parseBoards(
          next,
          (head.trim.split("\\s+").map(_.toInt).toVector) :: current,
          result
        )
      case Nil =>
        (current :: result).reverse.map(rows =>
          BingoBoard(rows.reverse.toVector)
        )
    }
  }

  def renderBoard(board: BingoBoard, drawnNumbers: Set[Int]): VModifier = {
    import scala.collection.decorators._

    val largestNumber = board.rows.flatten.max
    val sizeLargestNumber = largestNumber.toString.length
    val rowParagraphs = board.rows
      .map { row =>
        p(
          row
            .map[VModifier] { num =>
              val hasBeenDrawn = drawnNumbers.contains(num)
              val paddedNum = s"${" ".*(sizeLargestNumber)}$num".takeRight(
                sizeLargestNumber
              )

              if (hasBeenDrawn) span(b(paddedNum)) else span(paddedNum)
            }
            .intersperse(span(" "))
        )

      }

    div(
      cls := "font-mono",
      whiteSpace := "pre",
      rowParagraphs
    )
  }

  def solvePart1(input: String)(implicit visualDebugger: VisualDebugger) = IO{

    val lines = input.linesIterator.toList
    val drawedNumbers: List[Int] = lines.head.split(",").map(_.toInt).toList
    val boards: List[BingoBoard] = parseBoards(lines.tail.dropWhile(_.isEmpty))

    visualDebugger.log(
      s"found ${boards.size} boards of size ${boards.head.width}x${boards.head.height}"
    )

    val attempts: List[((BingoBoard, Int), Int)] =
      1.until(drawedNumbers.size)
        .flatMap(n =>
          boards.zipWithIndex.map { case (b, idx) => (b, idx) -> n }
        )
        .toList

    val result = attempts.find { case ((board, _idx), n) =>
      board.hasWon(drawedNumbers.take(n))
    } match {
      case None => "no solution found"
      case Some(((board, idx), n)) =>
        val numbers = drawedNumbers.take(n)

        val lastDrawnNumber = numbers.last
        val unmarkedNumbers = board.allNumbers.diff(numbers)
        val sumUnmarked = unmarkedNumbers.sum
        val result = lastDrawnNumber * sumUnmarked

        visualDebugger.render(
          div(
            s"found winning board (#$idx) after $n draws",
            renderBoard(board, numbers.toSet),
            p("last drawn number: ", lastDrawnNumber.toInt),
            p("sum unmarked numbers: ", sumUnmarked)
          )
        )
        result.toString

    }
    visualizeBoards(drawedNumbers, boards)(visualDebugger)
    result
  }

  private def visualizeBoards(
      drawedNumbers: List[Int],
      boards: List[BingoBoard]
  )(visualDebugger: VisualDebugger) = {
    val numberOfSelected = Subject.behavior(3)

    visualDebugger.render(
      div(
        h3("Manually draw numbers", cls := "text-xl mt-8 mb-2"),
        numberOfSelected.map { numSelected =>
          val drawn = drawedNumbers.take(numSelected)
          div(
            primaryButton(
              "+",
              onClick(numberOfSelected.map(_ + 1)) --> numberOfSelected
            ),
            primaryButton(
              "-",
              onClick(numberOfSelected.map(_ - 1)) --> numberOfSelected
            ),
            p(s"$numSelected numbers drawn"),
            p(drawn.mkString(", ")),
            boards.zipWithIndex.map { case (b, idx) =>
              div(
                cls := "mt-4",
                h3(s"Board #$idx"),
                renderBoard(b, drawedNumbers.take(numSelected).toSet)
              )
            }
          )
        }
      )
    )
  }

  val primaryButton: VNode = button(
    cls := "rounded-lg px-4 py-2 bg-gray-200 mb-2 mr-2"
  )

  def solvePart2(input: String)(implicit visualDebugger: VisualDebugger) = IO{

    val lines = input.linesIterator.toList
    val drawedNumbers: List[Int] = lines.head.split(",").map(_.toInt).toList
    val boards: List[BingoBoard] = parseBoards(lines.tail.dropWhile(_.isEmpty))

    visualDebugger.log(
      s"found ${boards.size} boards of size ${boards.head.width}x${boards.head.height}"
    )

    val results: List[(BingoBoard, Int)] = boards.flatMap { b =>
      1.until(drawedNumbers.size).find { n =>
        b.hasWon(drawedNumbers.take(n))
      } match {
        case Some(n) => List(b -> n)
        case None    => Nil
      }
    }

    val ((worstBoard, n), worstBoardIndex) = results.zipWithIndex.maxBy(_._1._2)

    val numbers = drawedNumbers.take(n)

    val lastDrawnNumber = numbers.last
    val unmarkedNumbers = worstBoard.allNumbers.diff(numbers)
    val sumUnmarked = unmarkedNumbers.sum
    val result = lastDrawnNumber * sumUnmarked

    visualDebugger.render(
      div(
        h3(
          s"Worst board (Board #$worstBoardIndex)",
          cls := "text-2xl mt-8 mb-8"
        ),
        renderBoard(worstBoard, numbers.toSet),
        p("last drawn number: ", lastDrawnNumber.toInt),
        p("sum unmarked numbers: ", sumUnmarked),
        h3("Board rankings", cls := "text-2xl mt-8 mb-8"),
        results.zipWithIndex.sortBy(_._1._2).reverse.map {
          case ((_, score), idx) =>
            p(s"#$idx: $score")
        }
      )
    )

    result.toString

  }

}
