package adventOfCode.year2021

import adventOfCode.{AocDay, VisualDebugger, util}
import cats.effect.IO
import outwatch.dsl._

import scala.collection.immutable.Queue

object Day21 extends AocDay {
  val day = 21

  val positionLookup: Map[Int, Int] = 1
    .to(20)
    .flatMap(_ => 1.to(10))
    .zipWithIndex
    .map { case (value, idx) =>
      (idx + 1, value)
    }
    .toMap

  case class GameState(
      p1Pos: Int,
      p2Pos: Int,
      p1Score: Int = 0,
      p2Score: Int = 0,
      dieValue: Int = 0,
      totalNumberOfDieRolls: Int = 0
  ) {
    override def toString: String =
      s"""
         |p1
         |  pos: $p1Pos
         |  score: $p1Score
         |p2
         |  pos: $p2Pos
         |  score: $p2Score
         |die: $dieValue
         |  """.stripMargin

    def hasWinner(winningScore: Int = 1000): Boolean = {
      p1Score >= winningScore || p2Score >= winningScore

    }

    def losingScore: Int = {
      math.min(p1Score, p2Score)
    }
  }

  object GameState {
    def parse(input: String): Option[GameState] = {
      input.linesIterator.toList.sorted match {
        case List(p1, p2) =>
          val p1Pos = p1.split(": ").last.toInt
          val p2Pos = p2.split(": ").last.toInt
          Some(GameState(p1Pos = p1Pos, p2Pos = p2Pos))
        case _ => None
      }
    }
  }

  def roll(die: Int, times: Int): List[Int] = {
    List.tabulate(times) { i =>
      val res = die + i + 1
      if (res > 100) res % 100 else res
    }
  }

  def playOneRound(gameState: GameState): GameState = {
    val p1Rolls = roll(gameState.dieValue, 3)
    val p1Positions = p1Rolls
      .scanLeft(gameState.p1Pos) { (pos, dieValue) =>
        val newPos = pos + dieValue
        positionLookup(newPos)
      }
      .drop(1)
    val p1NewPosition = p1Positions.last
    val p1NewScore = gameState.p1Score + p1NewPosition
    val gsAfterP1 = gameState.copy(
      p1Pos = p1NewPosition,
      p1Score = p1NewScore,
      dieValue = p1Rolls.last,
      totalNumberOfDieRolls = gameState.totalNumberOfDieRolls + 3
    )
    if (gsAfterP1.hasWinner()) {
      println("p1 has won - not evaluating player 2")
      //p1 won
      gsAfterP1
    } else {
      //p1 didn't win, so we evaluate p2
      val p2Rolls = roll(gsAfterP1.dieValue, 3)
      val p2Positions = p2Rolls
        .scanLeft(gsAfterP1.p2Pos) { (pos, dieValue) =>
          val newPos = pos + dieValue
          positionLookup(newPos)
        }
        .drop(1)
      println(s"  p1 position: ${gameState.p1Pos}")
      println(s"  p1 rolls: $p1Rolls")
      println(s"  p1 positions: $p1Positions")

      println(s"  p2 position: ${gameState.p2Pos}")
      println(s"  p2 rolls: $p2Rolls")
      println(s"  p2 positions: $p2Positions")

      val p2NewPosition = p2Positions.last
      val p2NewScore = gsAfterP1.p2Score + p2NewPosition
      val gsAfterP1AndP2 = gsAfterP1.copy(
        p2Pos = p2NewPosition,
        p2Score = p2NewScore,
        dieValue = p2Rolls.last,
        totalNumberOfDieRolls = gsAfterP1.totalNumberOfDieRolls + 3
      )
      gsAfterP1AndP2
    }
  }

  def play(gameState: GameState, winningScore: Int): List[GameState] = {

    def helper(gs: GameState, states: Queue[GameState]): List[GameState] = {
      println(s" ====== playing round ${states.size + 1} ======")
      if (gs.hasWinner(winningScore)) {
        states.toList
      } else {
        val newGs = playOneRound(gs)
        helper(newGs, states.enqueue(newGs))
      }
    }

    helper(gameState, Queue.empty)
  }

  def solvePart1(input: String)(implicit visualDebugger: VisualDebugger) = IO{
    val initial = GameState.parse(input).get

    import util.ui.Helpers.FormattedTable._

    val gameStates = play(initial, 1000)
    visualDebugger.render(
      div(
        table(
          thead(
            tr(
              th(cls := "text-right", "round"),
              th(cls := "text-right", "p1 pos"),
              th(cls := "text-right", "p1 score"),
              th(cls := "text-right", "p2 pos"),
              th(cls := "text-right", "p2 score"),
              th(cls := "text-right", "total die rolls")
            )
          ),
          tbody(
            gameStates.zipWithIndex
              .map { case (state, idx) =>
                tr(
                  td(cls := "text-right", idx + 1),
                  td(cls := "text-right", state.p1Pos),
                  td(cls := "text-right", state.p1Score),
                  td(cls := "text-right", state.p2Pos),
                  td(cls := "text-right", state.p2Score),
                  td(cls := "text-right", state.totalNumberOfDieRolls)
                )
              }
          )
        )
      )
    )

    val finalGameState = gameStates.last
    val result = finalGameState.losingScore * finalGameState.totalNumberOfDieRolls

    result.toString
  }

  def diracPlay(initial: GameState): (Long, Long) = {

    val cache = collection.mutable.Map.empty[(Int, Int, Long, Long), (Long, Long)]

    def diracRound(gs: GameState): (Long, Long) = {

      cache.get(gs.p1Pos, gs.p2Pos, gs.p1Score, gs.p2Score) match {
        case Some(cached) =>
          //println("found match")
          cached
        case None =>
          //println("no match")

          if (gs.p2Score >= 21) {
            val res = (0L, 1L)
            cache.update((gs.p1Pos, gs.p2Pos, gs.p1Score, gs.p2Score), res)
            res
          } else {
            val res = possibleRolls.foldLeft((0L, 0L)) { case ((totalP1, totalP2), (r1, r2, r3)) =>
              val newPosition = (gs.p1Pos + r1 + r2 + r3) % 10
              val newScore = gs.p1Score + newPosition + 1
              val (p2Wins, p1Wins) = diracRound(GameState(gs.p2Pos, newPosition, gs.p2Score, newScore))

              (totalP1 + p1Wins, totalP2 + p2Wins)
            }
            cache.update((gs.p1Pos, gs.p2Pos, gs.p1Score, gs.p2Score), res)
            res
          }
      }
    }
    diracRound(initial)

  }

  val possibleRolls: List[(Int, Int, Int)] = (
    for {
      x <- 1.to(3)
      y <- 1.to(3)
      z <- 1.to(3)
    } yield (x, y, z)
  ).toList

  def solvePart2(input: String)(implicit visualDebugger: VisualDebugger) = IO{
    val initial = GameState.parse(input).get

    val result = diracPlay(initial.copy(p1Pos = initial.p1Pos - 1, p2Pos = initial.p2Pos - 1))
    visualDebugger.log(result.toString)
    val answer = math.max(result._1, result._2)
    answer.toString
  }
}
