package adventOfCode.year2021

import Day15.Pos.neighborOffsets
import adventOfCode.util.{DijkstraMutable, Graph}
import adventOfCode.{AocDay, VisualDebugger}
import cats.effect.IO
import outwatch.dsl._

object Day15 extends AocDay {
  val day = 15

  case class Pos(x: Int, y: Int) {
    override def toString: String = s"($x, $y)"

    def neighbors: List[Pos] =
      neighborOffsets.map { case (dx, dy) =>
        Pos(x + dx, y + dy)
      }
  }

  object Pos {
    val neighborOffsets: List[(Int, Int)] = List(
      (0, 1),
      (1, 0),
      (-1, 0),
      (0, -1)
    )

    implicit val ordering: Ordering[Pos] = Ordering.by { case Pos(x, y) =>
      (x, y)
    }
  }

  case class Grid(cells: Vector[Vector[Int]]) {
    val height = cells.length
    val width = cells.head.length
    val size = height * width

    override def toString: String =
      cells.map(row => row.mkString).mkString("\n")

    def get(pos: Pos): Option[Int] = {
      if (!contains(pos)) None
      else Some(cells(pos.y)(pos.x))
    }

    def contains(pos: Pos): Boolean = {
      cells.isDefinedAt(pos.y) && cells(pos.y).isDefinedAt(pos.x)
    }

    def positions: List[Pos] = {
      (for {
        y <- 0.until(height)
        x <- 0.until(width)
      } yield Pos(x, y)).toList
    }

    def neighborValues(pos: Pos): List[Int] = {
      pos.neighbors.flatMap(get)
    }

    def neighbors(pos: Pos): List[Pos] = {
      pos.neighbors.filter(contains)
    }
  }

  val myTd = td(cls := "border border-gray-300 align-baseline")
  val myTh = th(cls := "border border-gray-300 align-baseline")

  def solve(
      riskLevelMap: Vector[Vector[Int]]
  )(implicit visualDebugger: VisualDebugger): Int = {
    val grid = Grid(riskLevelMap)

    val graph: Map[Pos, Map[Pos, Int]] = generateGraph(grid)

    val (totalCosts: Int, path: List[Pos]) = calculatePath(grid, graph)

    val pathCosts = path.map { case pos @ Pos(x, y) =>
      (pos, if (pos == Pos(0, 0)) 0 else riskLevelMap(y)(x))
    }

    visualDebugger.render(
      div(
        h2("Path"),
        table(
          cls := "border-collapse border border-gray-400",
          thead(tr(myTh("node"), myTh("costs"))),
          tbody(
            pathCosts.map { case (node @ Pos(x, y), c) =>
              tr(
                myTd(node.toString),
                myTd(c)
              )
            }
          )
        )
      )
    )

    totalCosts

  }

  def calculatePath(
      grid: Grid,
      graph: Map[Pos, Map[Pos, Int]]
  ): (Int, List[Pos]) = {

    val g: Graph[Pos] = (n: Pos) => graph(n)

    val start = Pos(0, 0)
    DijkstraMutable.shortestPath(g)(
      start,
      Pos(grid.width - 1, grid.height - 1)
    ) match {
      case None =>
        (0, List.empty)
      case Some(path) =>
        val costs =
          path.foldLeft(0) { (acc, pos) =>
            if (pos == start) acc
            else acc + grid.get(pos).getOrElse(0)
          }
        (costs, path)

    }

  }

  def generateGraph(grid: Grid): Map[Pos, Map[Pos, Int]] = {
    grid.positions.map { case p @ Pos(x, y) =>
      val neighbors = grid.neighbors(p)
      val neighborWeights = neighbors
        .filterNot(_ == Pos(0, 0))
        .map(n => (n, grid.cells(n.y)(n.x)))
        .toMap
      p -> neighborWeights
    }.toMap

  }

  def solvePart1(input: String)(implicit visualDebugger: VisualDebugger) = IO{
    val riskLevelMap = parseRiskLevelMap(input)

    solve(riskLevelMap).toString
  }

  def mappingFn(i: Int): Int = (i - 1) % 9 + 1

  def parseRiskLevelMap(input: String): Vector[Vector[Int]] = {
    input.linesIterator
      .filterNot(_.trim.isEmpty)
      .toVector
      .map(line => line.toVector.map(_.toString.toInt))
  }

  def generateExtendedMap(
      riskLevelMap: Vector[Vector[Int]],
      times: Int
  ): Vector[Vector[Int]] = {
    val range = 0.until(times).toVector

    val extendedRows = riskLevelMap
      .map { row =>
        range.flatMap { i =>
          row.map(value => mappingFn(i + value))
        }
      }

    range.flatMap { i =>
      extendedRows.map { row =>
        row.map(value => mappingFn(i + value))
      }
    }
  }

  def solvePart2(input: String)(implicit visualDebugger: VisualDebugger) = IO{
    val riskLevelMap = parseRiskLevelMap(input)
    val extendedMap = generateExtendedMap(riskLevelMap, 5)

    solve(extendedMap).toString
  }
}
