package adventOfCode.year2022

import adventOfCode.util.Parsers._
import adventOfCode.year2022.Day10.Op.Noop
import adventOfCode.{AocDay, VisualDebugger}
import cats.effect.IO
import cats.parse.{Parser => P}
import outwatch.VModifier
import outwatch.dsl._

object Day10 extends AocDay {
  val day = 10

  val parser: P[Op] = P.string("noop").as(Op.Noop) | P.string("addx ") *> intParser.map(Op.Addx)
  /*
  noop
  addx 3
  addx -5
   */

  def parseLine(line: String) = parser.parse(line).toOption.get._2

  case class State(startCycle: Int, endCycle: Int, op: Op, valueDuringCycle: Int)

  def solvePart1(input: String)(implicit visualDebugger: VisualDebugger) = IO {
    val operations = input.linesIterator.map(parseLine).toList

    val operationStates = calcOperationStates(operations)

    val interestingCycles = Vector(20, 60, 100, 140, 180, 220).flatMap { cycle =>
      operationStates.find { case s @ State(start, end, _, _) => cycle > start && cycle <= end }.map { matching =>
        cycle -> matching
      }
    }

    val result = interestingCycles.map(tuple => tuple._1 * tuple._2.valueDuringCycle).sum

    // relevant states
    renderStates(operationStates.filter(s => interestingCycles.exists(_._2 == s)), interestingCycles)

    // all states
    renderStates(operationStates, interestingCycles)

    result.toString
  }

  private def calcOperationStates(operations: List[Op]) =
    (operations ++ List(Noop)).scanLeft(State(0, 1, Noop, 1)) { (old, nextOp) =>
      val startCycle        = old.endCycle
      val (delta, duration) = old.op match {
        case Op.Noop        => (0, 1)
        case Op.Addx(value) => (value, 2)
      }
      val endCycle          = startCycle + duration
      val valueDuringCycle  = old.valueDuringCycle + delta
      State(startCycle, endCycle, nextOp, valueDuringCycle)
    }

  def drawCrtScreen(drawInstructions: List[Int]) =
    drawInstructions
      .grouped(40)
      .map { case (rowInstructions) =>
        rowInstructions.zipWithIndex.map { case (value, c) =>
          val middleOfSpriteLocation = value
          val crtDrawingLocation     = c

          if ((middleOfSpriteLocation - 1).to(middleOfSpriteLocation + 1).contains(crtDrawingLocation))
            ''
          else ' '

        }.mkString

      }
      .mkString("\n")

  def solvePart2(input: String)(implicit visualDebugger: VisualDebugger) = IO {
    val operations      = input.linesIterator.map(parseLine).toList
    val operationStates = calcOperationStates(operations)

    val cycleValues = operationStates.zipWithIndex.flatMap { case (s, idx) =>
      val duration = s.op match {
        case Op.Addx(_) => 2
        case Op.Noop    => 1
      }
      1.to(duration).map(_ => s.valueDuringCycle)
    }

    cycleValues.zipWithIndex.foreach { case (value, cycle) =>
      visualDebugger.log(s"cycle: $cycle --> $value")
    }

    visualDebugger.render(
      div(
        h3(cls  := "font-medium leading-tight text-2xl mt-0 mb-2", "Meant for humans to parse"),
        p("8 characters"),
        pre(cls := "text-white bg-black leading-none text-xs", drawCrtScreen(cycleValues.tail.dropRight(1))),
      ),
    )

    "???"
  }

//  import outwatch.dsl._
  import adventOfCode.util.ui.Helpers.FormattedTable._

  def renderStates(
    operationStates: List[State],
    interestingCycles: Vector[(Int, State)],
  )(implicit
    visualDebugger: VisualDebugger,
  ): Unit =
    visualDebugger.render(
      div(
        h3(cls := "font-medium leading-tight text-2xl mt-0 mb-2", "States"),
        table(
          thead(
            tr(
              th("start"),
              th("end"),
              th("value"),
              th("next op"),
            ),
          ),
          tbody(
            operationStates.map { case (s) =>
              val maybeInteresting = interestingCycles.find(_._2 == s)
              val isInteresting    = maybeInteresting.nonEmpty
              tr(
                td(s.startCycle),
                td(s.endCycle),
                td(s.valueDuringCycle, VModifier.ifTrue(isInteresting)(cls := "font-bold")),
                td(s.op.toString),
              )
            },
          ),
        ),
      ),
    )

  sealed trait Op

  object Op {
    case class Addx(value: Int) extends Op
    case object Noop            extends Op
  }

}

/*
Sprite position: ###.....................................

Start cycle   1: begin executing addx 15
During cycle  1: CRT draws pixel in position 0
Current CRT row: #

During cycle  2: CRT draws pixel in position 1
Current CRT row: ##
End of cycle  2: finish executing addx 15 (Register X is now 16)
Sprite position: ...............###......................

Start cycle   3: begin executing addx -11
During cycle  3: CRT draws pixel in position 2
Current CRT row: ##.

During cycle  4: CRT draws pixel in position 3
Current CRT row: ##..
End of cycle  4: finish executing addx -11 (Register X is now 5)
Sprite position: ....###.................................

Start cycle   5: begin executing addx 6
During cycle  5: CRT draws pixel in position 4
Current CRT row: ##..#

During cycle  6: CRT draws pixel in position 5
Current CRT row: ##..##
End of cycle  6: finish executing addx 6 (Register X is now 11)
Sprite position: ..........###...........................

 */
