package adventOfCode.year2021

import adventOfCode.{AocDay, VisualDebugger}
import cats.effect.IO
import outwatch.VModifier
import outwatch.dsl._

import scala.annotation.tailrec

object Day09 extends AocDay {
  val day = 9

  /*
2199943210
3987894921
9856789892
8767896789
9899965678
   */

  case class Pos(x: Int, y: Int) {
    def neighbors: List[Pos] = {
      List(
        this.copy(x = x - 1),
        this.copy(x = x + 1),
        this.copy(y = y - 1),
        this.copy(y = y + 1)
      )
    }

    override def toString: String = s"($x, $y)"
  }

  case class Grid(cells: Array[Array[Int]]) {
    val height = cells.length
    val width = cells.head.length

    override def toString: String =
      cells.map(row => row.mkString).mkString("\n")

    def get(pos: Pos): Option[Int] = {
      val Pos(x, y) = pos

      if (!cells.isDefinedAt(y)) None
      else if (!cells(y).isDefinedAt(x)) None
      else Some(cells(y)(x))
    }

    def positions: List[Pos] = {
      (for {
        y <- 0.until(height)
        x <- 0.until(width)
      } yield Pos(x, y)).toList
    }

    def neighborValues(pos: Pos): List[Int] = {
      pos.neighbors.flatMap(get)
    }
  }

  object Grid {
    def parse(lines: Seq[String]) = {

      val cells = lines.zipWithIndex.map { case (l, r) =>
        l.zipWithIndex.map { case (v, c) =>
          v.toString.toInt //char to int doesn't work iirc
        }.toArray
      }.toArray

      Grid(cells)
    }
  }

  def visualizeGrid(
      grid: Grid,
      cellsOfLargestBasins: Set[Pos] = Set.empty
  ): VModifier = {
    import svg._

    def getColor(pos: Pos): String = {
      val v = grid.get(pos).get
      val isBasinCell = cellsOfLargestBasins.contains(pos)
      val redValue = if (isBasinCell) 0.0 else 255 * v / 9.0
      val blueValue = if (isBasinCell) 0.0 else 255 - 255 * (v / 9.0)
      val greenValue = if (isBasinCell) 255 * (v / 9.0) else 0.0

      (if (v >= 9) "black" else s"rgb($redValue,$greenValue,$blueValue)")

    }

    svg(
      cls := "w-11/12",
      viewBox := s"0 0 ${grid.width * 5} ${grid.height * 5}",
      style := "background-color:lightGrey",
      grid.positions.map { pos =>
        rect(
          x := (pos.x * 5).toString,
          y := (pos.y * 5).toString,
          width := "4.5",
          height := "4.5",
          fill := getColor(pos)
        )
      }
    )
  }

  def findLowPoints(grid: Grid): Seq[NeighborAnalysis] = {
    val neighborAnalysis: List[NeighborAnalysis] = grid.positions.map { pos =>
      val height = grid.get(pos).get
      val neighborValues = grid.neighborValues(pos)
      val riskLevel = height + 1
      NeighborAnalysis(
        pos,
        height,
        neighborValues,
        neighborValues.forall(nv => height < nv),
        riskLevel
      )
    }
    val lowPoints = neighborAnalysis.filter(_.isLowest)
    lowPoints
  }

  case class Basin(positions: Vector[Pos])

  def findBasins(grid: Grid): List[Basin] = {

    @tailrec
    def expandBasin(
        curr: Pos,
        openList: List[Pos],
        basinMap: Map[Pos, Pos]
    ): Map[Pos, Pos] = {
      openList match {
        case Nil
            if basinMap.contains(curr) || grid.get(curr).getOrElse(9) >= 9 =>
          basinMap

        case Nil =>
          expandBasin(curr, curr :: openList, basinMap)

        case ::(head, next)
            if basinMap.contains(head) || grid.get(head).getOrElse(9) >= 9 =>
          expandBasin(curr, next, basinMap)

        case ::(head, next) =>
          val neighbors = head.neighbors.filter(p => grid.get(p).isDefined)
          expandBasin(curr, neighbors ::: next, basinMap.updated(head, curr))

      }
    }

    val lowPoints = findLowPoints(grid).filter(_.isLowest).map(t => t.pos)

    val finalMap = lowPoints.foldLeft(Map.empty[Pos, Pos]) {
      case (map, lowPoint) =>
        expandBasin(lowPoint, List.empty, map)
    }

    finalMap
      .groupBy(_._2)
      .values
      .map(_.keys)
      .map(positions => Basin(positions.toVector))
      .toList
  }

  case class NeighborAnalysis(
      pos: Pos,
      height: Int,
      neighborValues: List[Int],
      isLowest: Boolean,
      riskLevel: Int
  )

  def solvePart1(input: String)(implicit visualDebugger: VisualDebugger) = IO{
    val lines = input.linesIterator.filterNot(_.trim.isEmpty).toList
    val grid = Grid.parse(lines)
    val lowPoints = findLowPoints(grid)

    visualDebugger.render(
      div(
        h2("Heightmap"),
        visualizeGrid(grid),
        h2("neighbor analysis"),
        table(
          thead(
            tr(
              th("pos"),
              th("value"),
              th("neighbor values"),
              th("risk level")
            )
          ),
          tbody(
            lowPoints
              .sortBy { case NeighborAnalysis(Pos(x, y), _, _, _, _) => (y, x) }
              .map {
                case NeighborAnalysis(pos, value, neighbors, _, riskLevel) =>
                  tr(
                    td(pos.toString),
                    td(value),
                    td(neighbors.mkString(", ")),
                    td(riskLevel)
                  )
              }
          )
        )
      )
    )

    lowPoints.map(_.riskLevel).sum.toString
  }

  def solvePart2(input: String)(implicit visualDebugger: VisualDebugger) = IO{
    val lines = input.linesIterator.filterNot(_.trim.isEmpty).toList
    val grid = Grid.parse(lines)

    val basins = findBasins(grid)

    val sortedBasins = basins
      .sortBy(_.positions.size * -1)

    val relevantBasins = sortedBasins.take(3)
    val relevantBasinSizes = relevantBasins.map(_.positions.size)
    val result = relevantBasinSizes.product

    visualDebugger.render(
      div(
        h2("Heightmap with 3 largest basins"),
        visualizeGrid(grid, relevantBasins.flatMap(_.positions).toSet),
        h2("relevant basins"),
        table(
          thead(tr(th("size"))),
          tbody(relevantBasins.map { b => tr(td(b.positions.size)) })
        ),
        p(s"${relevantBasinSizes.mkString(" * ")} = $result"),
        h2("all basins"),
        p(s"found ${basins.size} basins"),
        table(
          thead(tr(th("size"))),
          tbody(sortedBasins.map { b => tr(td(b.positions.size)) })
        )
      )
    )

    result.toString
  }
}
