package adventOfCode.year2022

import adventOfCode.{AocDay, VisualDebugger}
import cats.effect.IO
import adventOfCode.util.Parsers._
import cats.parse.{Numbers, Parser => P}
import org.scalajs.dom.window
import outwatch.VModifier
import outwatch.dsl._

object Day09 extends AocDay {
  val day = 9

  val directionParser: P[Direction] = P.anyChar.flatMap {
    case 'U'   => P.pure(Direction.up)
    case 'D'   => P.pure(Direction.down)
    case 'L'   => P.pure(Direction.left)
    case 'R'   => P.pure(Direction.right)
    case other => P.failWith(s"not a valid direction: $other")
  }

  val motionParser: P[(Direction, Int)] = directionParser ~ (P.char(' ') *> intParser)

  def parseLine(line: String): (Direction, Int) = motionParser.parse(line).toOption.get._2

  def solvePart1(input: String)(implicit visualDebugger: VisualDebugger) = IO {
    val moves = input.trim.linesIterator.map(parseLine).toList

    visualDebugger.log("first 10 moves")
    moves.take(10).foreach { case (dir, length) =>
      visualDebugger.log(f"${dir.name}%7s $length")
    }

    val singleStepMoves                     = moves.flatMap { case (m, l) => 1.to(l).map(_ => m) }
    val locationsOfHead: Vector[(Int, Int)] = singleStepMoves.scanLeft((0, 0))((loc, dir) => dir.walk(loc)).toVector

    visualDebugger.log("first 10 steps")
    singleStepMoves.take(10).foreach(dir => visualDebugger.log(dir.name))

    visualDebugger.log("first 10 locations of head")
    locationsOfHead.take(10).foreach(loc => visualDebugger.log(loc))

    val locationsOfTail: Vector[(Int, Int)] = simulateTailMovesNTimes(locationsOfHead, 2).map(_.last)
    val locations                           = locationsOfTail.zip(locationsOfHead)

    render(locations.zip(singleStepMoves))

    val result = locationsOfTail.distinct.size
    result.toString
  }

  def simulateTailMovesNTimes(locationsOfHead: Vector[(Int, Int)], size: Int): Vector[Vector[(Int, Int)]] = {

    val initialState = Vector.fill(size)((0, 0))

    locationsOfHead.scanLeft(initialState) { case (old, newL) => simulateWholeMove(old, newL) }.tail
  }

  def calcNewLocation(predecessorLocation: (Int, Int), myLocation: (Int, Int)): (Int, Int) =
    if (areTouching(myLocation, predecessorLocation)) {
      myLocation
    }
    else {
      val d              = (predecessorLocation._1 - myLocation._1, predecessorLocation._2 - myLocation._2)
      val (dx, dy)       = d
      val (absDx, absDy) = (math.abs(dx), math.abs(dy))
      //          println("(dx,dy):", d)
      //          println("(absDx,absDy):", (absDx, absDy))
      if (absDx == 0 && absDy == 2 || absDx == 2 && absDy == 0) {
        // follow
        val move = (if (absDx > 0) dx / absDx else 0, if (absDy > 0) dy / absDy else 0)
        (myLocation._1 + move._1, myLocation._2 + move._2)
        //            println(s"following. NewLocation: ${newState(i)}")

      }
      else {
        val move = (if (absDx > 0) dx / absDx else 1, if (absDy > 0) dy / absDy else 1)
        (myLocation._1 + move._1, myLocation._2 + move._2)
        //            println(s"cutting corner. Move: $move. NewLocation: ${newState(i)}")

      }
    }

  def simulateWholeMove(vector: Vector[(Int, Int)], newHeadL: (Int, Int)): Vector[(Int, Int)] = {
    // 21H  right
    // 321H

    /*
    If the head is ever two steps directly up, down, left, or right from the tail, the tail must also move one step in that direction so it remains close enough:

    .....    .....    .....
    .TH.. -> .T.H. -> ..TH.
    .....    .....    .....

    ...    ...    ...
    .T.    .T.    ...
    .H. -> ... -> .T.
    ...    .H.    .H.
    ...    ...    ...
    Otherwise, if the head and tail aren't touching and aren't in the same row or column, the tail always moves one step diagonally to keep up:
    .....    .....    .....
    .....    ..H..    ..H..
    ..H.. -> ..... -> ..T..
    .T...    .T...    .....
    .....    .....    .....

    .....    .....    .....
    .....    .....    .....
    ..H.. -> ...H. -> ..TH.
    .T...    .T...    .....
    .....    .....    .....
     */

    val newState = Array.fill(vector.length)((0, 0))
    vector.indices.foreach { i =>
      if (i <= 0) newState(i) = newHeadL
      else {
        val predecessorLocation = newState(i - 1)
        val myLocation          = vector(i)
        val newLocation         = calcNewLocation(predecessorLocation, myLocation)
        newState(i) = newLocation

      }
    }
    val result   = newState.toVector
//    println(s"oldState: $vector")
//    println(s"newState: $result")
    result
  }

  import adventOfCode.util.ui.Helpers.FormattedTable._

  def render(
    locationsAndMove: Vector[(((Int, Int), (Int, Int)), Direction)],
  )(implicit visualDebugger: VisualDebugger): Unit = {
    def v(loc: (Int, Int)) = s"(${loc._1},${loc._2})"

    val xs       = locationsAndMove.map(_._1._1._1)
    val mapSize  = xs.max - xs.min
    val asciiArt = renderAsciiArt(locationsAndMove.map(_._1._1).toSet)

    visualDebugger.render(
      div(
        cls := "flex flex-col gap-4 mt-4",
        div(
          h4("final state"),
          VModifier.ifTrue(mapSize < 50)(pre(asciiArt)),
          button(
            tpe := "button",
            cls := "bg-purple-400 hover:bg-purple-700 px-5 py-2 text-sm leading-5 rounded font-semibold text-white",
            "copy ascii art to clipboard",
            onClick.doEffect {
              IO.fromFuture(
                IO(window.navigator.clipboard.writeText(asciiArt).toFuture),
              )
            },
          ),
        ),
        h3(cls := "font-medium leading-tight text-2xl mt-0 mb-2", "Locations"),
        table(
          thead(
            tr(
              th("move"),
              th("direction"),
              th("tail location"),
              th("new head location"),
            ),
          ),
          tbody(
            locationsAndMove.zipWithIndex.map { case (((tailL, newL), direction), idx) =>
              tr(
                td(idx + 1),
                td(direction.name),
                td(v(tailL)),
                td(v(newL)),
              )
            },
          ),
        ),
      ),
    )
  }

  def calcLocationsOfHead(moves: Vector[(Direction, Int)]): (Vector[Direction], Vector[(Int, Int)]) = {
    val singleStepMoves                     = moves.flatMap { case (m, l) => 1.to(l).map(_ => m) }
    val locationsOfHead: Vector[(Int, Int)] = singleStepMoves.scanLeft((0, 0))((loc, dir) => dir.walk(loc)).toVector

    singleStepMoves -> locationsOfHead
  }

  def solvePart2(input: String)(implicit visualDebugger: VisualDebugger) = IO {
    val moves = input.trim.linesIterator.map(parseLine).toList

    // 5216 too high

    visualDebugger.log("first 10 moves")
    moves.take(10).foreach { case (dir, length) =>
      visualDebugger.log(f"${dir.name}%7s $length")
    }

    val (singleStepMoves, locationsOfHead) = calcLocationsOfHead(moves.toVector)

    visualDebugger.log("first 10 steps")
    singleStepMoves.take(10).foreach(dir => visualDebugger.log(dir.name))

    visualDebugger.log("first 10 locations of head")
    locationsOfHead.take(10).foreach(loc => visualDebugger.log(loc))

    val locations              = simulateTailMovesNTimes(locationsOfHead, 10)
    val locationsOfLastElement = locations.map(_.last)

    render(locationsOfLastElement.zip(locationsOfHead).zip(singleStepMoves))

    val result = locationsOfLastElement.distinct.size
    result.toString
//    "???"
  }

  def renderAsciiArt(locations: Set[(Int, Int)]): String = {

    val maxX = locations.map(_._1).max
    val minX = locations.map(_._1).min
    val maxY = locations.map(_._2).max
    val minY = locations.map(_._2).min

    maxY
      .to(minY, step = -1)
      .map(y =>
        minX
          .to(maxX)
          .map { x =>
            if (locations.contains((x, y))) "#" else "."
          }
          .mkString,
      )
      .mkString("\n")

  }

  case class Direction(name: String, xDiff: Int, yDiff: Int) {
    def walk(loc: (Int, Int)): (Int, Int) = (loc._1 + xDiff, loc._2 + yDiff)
  }

  def areTouching(l1: (Int, Int), l2: (Int, Int)): Boolean = {
    val (x1, y1) = l1
    val (x2, y2) = l2

    math.abs(x1 - x2) <= 1 && math.abs(y1 - y2) <= 1

  }

  object Direction {
    val up            = Direction(name = "Up", yDiff = 1, xDiff = 0)
    val down          = Direction(name = "Down", yDiff = -1, xDiff = 0)
    val left          = Direction(name = "Left", yDiff = 0, xDiff = -1)
    val right         = Direction(name = "Right", yDiff = 0, xDiff = 1)
    val allDirections = List(up, down, left, right)
  }
}

