package adventOfCode.year2022

import adventOfCode.util.Parsers._
import adventOfCode.util.ui.Helpers.expandableDivButton
import adventOfCode.util.{DijkstraMutable, Graph}
import adventOfCode.year2022.Day12.Pos.neighborOffsets
import adventOfCode.{AocDay, VisualDebugger}
import cats.effect.IO
import outwatch.dsl._

object Day12 extends AocDay {
  val day = 12

  val parser                  = identityStringParser
  def parseLine(line: String) = parser.parse(line).toOption.get._2

  def renderMoves(end: Pos, grid: Grid, path: List[Pos]): String = {
    val movementMap = path
      .sliding(2, 1)
      .map { case curr :: next :: Nil =>
        val dx           = next.x - curr.x
        val dy           = next.y - curr.y
        val movementChar = {
          if (dx == 1) '>'
          else if (dx == -1) '<'
          else if (dy == -1) '^'
          else if (dy == 1) 'v'
          else ???
        }

        curr -> movementChar
      }
      .toMap

    grid.cells.zipWithIndex.map { case (row, y) =>
      row.zipWithIndex.map { case (_, x) =>
        val pos          = Pos(x, y)
        val movementChar = movementMap.getOrElse(pos, '.')
        if (pos == end) 'E' else movementChar
      }.mkString
    }.mkString("\n")
  }

  def solvePart1(input: String)(implicit visualDebugger: VisualDebugger) = IO {

    val (start: Pos, end: Pos, grid: Grid) = calcGrid(input)

    val graph         = generateNeighborMap(grid)
    val g: Graph[Pos] = (n: Pos) => graph(n)

    val gridValues = grid.positions.sorted.flatMap(p => grid.get(p).map(value => p -> value))
    gridValues.foreach { case (pos, value) => visualDebugger.log(s"$pos -> $value") }

    val path = DijkstraMutable.shortestPath(g)(start, end).get

    val gridStr: String = renderMoves(end, grid, path)

    expandableDivButton("Grid", false, gridStr)
    expandableDivButton(
      "Path Details",
      false,
      path.map(_.toString).mkString("\n"),
    )

    val result = path.size - 1

    //    trait Graph[N] {
//      /** Returns a map with the successors of `n` and their costs. */
//      def apply(n: N): Map[N, Int]

    result.toString
  }

  def calcGrid(input: String): (Pos, Pos, Grid) = {
    val (start, end, heightMap) = {
      var start = Pos(-1, -1)
      var end   = Pos(-1, -1)
      val grid  = input.linesIterator.toList.zipWithIndex.map { case (line, y) =>
        line.zipWithIndex.map { case (char, x) =>
          if (char == 'S') {
            start = Pos(x, y)
            0
          }
          else if (char == 'E') {
            end = Pos(x, y)
            'z' - 'a'
          }
          else char - 'a'

        }.toVector
      }.toVector

      (start, end, grid)
    }

    val grid = Grid(heightMap)
    (start, end, grid)
  }

  def solvePart2(input: String)(implicit visualDebugger: VisualDebugger) = IO {
    val (_, end: Pos, grid: Grid) = calcGrid(input)

    val graph                        = generateNeighborMap(grid)
    val g: Graph[Pos]                = (n: Pos) => graph(n)
    val gridValues: List[(Pos, Int)] = grid.positions.sorted.flatMap(p => grid.get(p).map(value => p -> value))

    val possibleStartingLocations: List[Pos] = gridValues.filter(_._2 == 0).map(_._1)

    val pathLengths = evaluateAllStartingPositions(end, g, possibleStartingLocations)

    val top10 = pathLengths.sortBy(_._2)

    visualDebugger.render(s"There are ${possibleStartingLocations.size} possible starting locations")
    visualDebugger.render(
      expandableDivButton(
        "Top 10 starting Points and lengths",
        true,
        top10.map { case (start, length) => s"Start: $start --> $length" }.mkString("\n"),
      ),
    )

    val result = top10.head._2
    result.toString
  }

  def evaluateAllStartingPositions(end: Pos, g: Graph[Pos], possibleStartingLocations: List[Pos]) = {

    val pointToPathLength = collection.mutable.HashMap.empty[Pos, Int]

    val result = possibleStartingLocations.flatMap { start =>
      if (pointToPathLength.contains(start)) {

        println("reusing precomputed result")
        Some(start -> pointToPathLength(start))
      }
      else {
        println("recomputing path")
        DijkstraMutable.shortestPath(g)(start, end).map { path =>
          // add the whole path to the startingPointMap to avoid recomputing path segments
          val length = path.size - 1
          path.zipWithIndex.foreach { case (loc, idx) =>
            pointToPathLength.update(loc, length - idx)
          }

          start -> length
        }
      }

    }

    result

  }

  def generateNeighborMap(grid: Grid): Map[Pos, Map[Pos, Int]] =
    grid.positions.map { case p @ Pos(x, y) =>
      val neighbors       = grid.neighbors(p)
      val neighborWeights = neighbors
        .filterNot(_ == Pos(0, 0))
        .map(n => (n, 1))
        .toMap
      p -> neighborWeights
    }.toMap

  case class Grid(cells: Vector[Vector[Int]]) {
    val height = cells.length
    val width  = cells.head.length
    val size   = height * width

    override def toString: String =
      cells.map(row => row.mkString).mkString("\n")

    def get(pos: Pos): Option[Int] =
      if (!contains(pos)) None
      else Some(cells(pos.y)(pos.x))

    def contains(pos: Pos): Boolean =
      cells.isDefinedAt(pos.y) && cells(pos.y).isDefinedAt(pos.x)

    def positions: List[Pos] =
      (for {
        y <- 0.until(height)
        x <- 0.until(width)
      } yield Pos(x, y)).toList

    def neighborValues(pos: Pos): List[Int] =
      pos.neighbors.flatMap(get)

    def neighbors(pos: Pos): List[Pos] = {
      val myValue = cells(pos.y)(pos.x)
      pos.neighbors.filter(n =>
        contains(n) && {
          val diff = cells(n.y)(n.x) - myValue
          diff <= 1
        },
      )
    }
  }

  case class Pos(x: Int, y: Int) {
    override def toString: String = s"($x, $y)"

    def neighbors: List[Pos] =
      neighborOffsets.map { case (dx, dy) =>
        Pos(x + dx, y + dy)
      }
  }

  object Pos {
    val neighborOffsets: List[(Int, Int)] = List(
      (0, 1),
      (1, 0),
      (-1, 0),
      (0, -1),
    )

    implicit val ordering: Ordering[Pos] = Ordering.by { case Pos(x, y) =>
      (x, y)
    }
  }

}
