package adventOfCode.year2021

import adventOfCode.{AocDay, VisualDebugger}
import cats.effect.IO
import cats.kernel.Monoid
import outwatch.dsl._

object Day14 extends AocDay {
  val day = 14

  case class InsertionRule(pair: String, inserted: Char)
  case class Input(polymerTemplate: String, insertionRules: List[InsertionRule])

  object InsertionRule {
    def parse(str: String): InsertionRule = {
      val List(pair, inserted) = str.split(" -> ").toList

      InsertionRule(pair, inserted.head)
    }
  }

  def applyRulesSlow(
      polymerTemplate: String,
      rules: List[InsertionRule]
  ): String = {
    val rulesMap = rules.map { case InsertionRule(pair, inserted) =>
      pair -> inserted
    }.toMap

    val indexed = polymerTemplate.zipWithIndex

    indexed
      .zip(indexed.tail)
      .map { case ((char1, _), (char2, idx2)) =>
        val pair = s"$char1$char2"
        rulesMap.get(pair) match {
          case Some(toBeInserted) =>
            val resulting = s"${pair.head}${toBeInserted}"

            resulting + (if (idx2 == polymerTemplate.size - 1) char2 else "")

          case None =>
            println(s"no match for pair '$pair'")
            pair
        }
      }
      .mkString
  }

  def generatePairMap(polymer: String): Map[String, Long] = {
    val pairs = polymer
      .zip(polymer.tail)
      .map { case (c1, c2) =>
        s"$c1$c2"
      }

    pairs.groupMapReduce(identity)(_ => 1L)(_ + _)
  }

  def applyRulesOptimized(
      pairMap: Map[String, Long],
      rules: List[InsertionRule]
  ): Map[String, Long] = {

    import cats.implicits._

    val rulesMap = rules.map { case InsertionRule(pair, inserted) =>
      pair -> inserted
    }.toMap

    /*
    NNCB
    NN -> C
    NC -> B
    CB -> H

    NN ---> NC CN
    NC ---> NB BC
    CB ---> CH HB

    for every pair
      check, if pair is in ruleset
        yes: expand this pair into two pairs (and remove it from pair map)
        no: keep it as it is
     */

    val newPairMaps = pairMap.map { case (pair, cnt) =>
      rulesMap.get(pair) match {
        case None =>
          Map.empty[String, Long]
        case Some(insertChar) =>
          val newPairs: Map[String, Long] = Map(
            s"${pair.head}$insertChar" -> cnt,
            s"$insertChar${pair.last}" -> cnt
          )

          newPairs
      }
    }

    Monoid.combineAll(newPairMaps)
  }

  def parse(input: String): Input = {
    val polymerTemplate = input.trim.linesIterator.take(1).toList.head

    val rules = input.trim.linesIterator.toList
      .filter(_.contains("->"))
      .map(InsertionRule.parse)

    Input(polymerTemplate, rules)
  }

  def solvePart1(input: String)(implicit visualDebugger: VisualDebugger) = IO{
    solve(input, 10)
  }

  def solvePart2(input: String)(implicit visualDebugger: VisualDebugger) = IO{
    solve(input, 40)
  }

  def solve(input: String, numberOfIterations: Int)(implicit
      visualDebugger: VisualDebugger
  ) = {

    val Input(polymerTemplate, insertionRules) = parse(input)

    val results =
      1.to(numberOfIterations).scanLeft(generatePairMap(polymerTemplate)) {
        case (poly, _) =>
          applyRulesOptimized(poly, insertionRules)
      }

    val report = results.map { res =>
      val firstPositionCharCounts = res.toList.map { case (pair, cnt) =>
        Map(pair.head -> cnt)
      }
      //the last char of the original input is never in the first place of a pair. So just add it manually
      val lastCharOfInput = Map(polymerTemplate.last -> 1L)
      val overviewOfSingleElements: Map[Char, Long] =
        Monoid.combineAll(lastCharOfInput :: firstPositionCharCounts)

      val mostCommon = overviewOfSingleElements.toList.maxBy(_._2)
      val leastCommon = overviewOfSingleElements.toList.minBy(_._2)
      (
        res,
        overviewOfSingleElements,
        mostCommon,
        leastCommon,
        mostCommon._2 - leastCommon._2
      )
    }

    val result = report.last._5

    val myTd = td(cls := "border border-gray-300 align-baseline")
    val myTh = th(cls := "border border-gray-300 align-baseline")

    visualDebugger.render(
      div(
        h2("polymer template"),
        p(polymerTemplate),
        h2("results"),
        table(
          cls := "border-collapse border border-gray-400",
          thead(
            tr(
              myTh("idx"),
              myTh("single char overview"),
              myTh("polymer length"),
              myTh("most common"),
              myTh("least common"),
              myTh("result")
            )
          ),
          tbody(
            report.zipWithIndex.lastOption.map {
              case (
                    (
                      _,
                      res,
                      (mostChar, mostCnt),
                      (leastChar, leastCnt),
                      diff
                    ),
                    idx
                  ) =>
                tr(
                  myTd(idx),
                  myTd(pre(res.map(_.toString).mkString("\n"))),
                  myTd(res.values.sum),
                  myTd(s"$mostChar: $mostCnt"),
                  myTd(s"$leastChar: $leastCnt"),
                  myTd(diff)
                )
            }
          )
        ),
        h2("insertion instructions"),
        table(
          thead(tr(myTh("pair"), myTh("inserted"))),
          tbody(
            insertionRules.map { case InsertionRule(pair, inserted) =>
              tr(
                myTd(pair),
                myTd(inserted.toString)
              )
            }
          )
        )
      )
    )

    result.toString

  }

}
