sealed trait Action

sealed trait RotationDirection

object RotationDirection {
  case object RotateCW extends RotationDirection

  case object RotateCCW extends RotationDirection
}

object Action {
  case class Walk(steps: Int) extends Action

  case class Rotation(direction: RotationDirection) extends Action

  private val walkParser: P[Action]                = intParser.map(Action.Walk)
  private val cwP                                  = P.char('R').as(Action.Rotation(RotationDirection.RotateCW))
  private val ccwP                                 = P.char('L').as(Action.Rotation(RotationDirection.RotateCCW))
  private val rotationP: P[Action]                 = cwP | ccwP
  private val actionPairP: P[NonEmptyList[Action]] = (rotationP ~ walkParser).map { case (a1, a2) =>
    NonEmptyList.of(a1, a2)
  }
  val parser: P[NonEmptyList[Action]]              = (walkParser ~ actionPairP.rep).map { case (first, rest) =>
    NonEmptyList.of(first, rest.map(_.toList).toList.flatten: _*)
  }
}

sealed trait Tile

object Tile {
  case object Empty extends Tile

  case object OpenTile extends Tile

  case object Wall extends Tile
}

case class Vec2(x: Int, y: Int) {
  def -(other: Vec2): Vec2 = Vec2(x - other.x, y - other.y)

  def *(factor: Int): Vec2 = Vec2(x * factor, y * factor)

  def +(other: Vec2): Vec2 = Vec2(x + other.x, y + other.y)

  def rotate(direction: RotationDirection) =
    Vec2.rotate(this, direction)
}

object Vec2 {
  val up    = Vec2(0, 1)
  val down  = Vec2(0, -1)
  val right = Vec2(1, 0)
  val left  = Vec2(-1, 0)

  implicit val ordering: Ordering[Vec2] = Ordering.by(v => v.x -> v.y)

  def str2D(v: Vec2): String = f"(${v.x}%1.0f,${v.y}%1.0f)"

  val rotationCcw = Array(Array(0, -1), Array(1, 0))
  val rotationCw  = Array(Array(0, 1), Array(-1, 0))

  def rotate(vec: Vec2, rotation: RotationDirection): Vec2 =
    rotation match {
      case RotationDirection.RotateCW  => rotate(vec, rotationCw)
      case RotationDirection.RotateCCW => rotate(vec, rotationCcw)
    }

  def rotate(vec: Vec2, rotationMatrix: Array[Array[Int]]): Vec2 = {
    val x = rotationMatrix(0)(0) * vec.x + rotationMatrix(0)(1) * vec.y
    val y = rotationMatrix(1)(0) * vec.x + rotationMatrix(1)(1) * vec.y
    Vec2(x, y)
  }
}

case class Vec3(x: Double, y: Double, z: Double) {
  def +(other: Vec3): Vec3 = Vec3(x + other.x, y + other.y, z + other.z)

  def -(other: Vec3): Vec3 = Vec3(x - other.x, y - other.y, z - other.z)

}

object Vec3 {
  val zero = Vec3(0, 0, 0)

  implicit val ordering: Ordering[Vec3] = Ordering.by(v => (v.x, v.y, v.z))

  def str3D(v: Vec3): String = f"(${v.x}%1.0f,${v.y}%1.0f,${v.z}%1.0f)"

  // values taken from unity3d
  val back    = Vec3(0, 0, -1)
  val down    = Vec3(0, -1, 0)
  val forward = Vec3(0, 0, 1)
  val left    = Vec3(-1, 0, 0)
  val right   = Vec3(1, 0, 0)
  val up      = Vec3(0, 1, 0)

  def directionToString(v: Vec3): String = v match {
    case Vec3.back    => "back"
    case Vec3.down    => "down"
    case Vec3.forward => "forward"
    case Vec3.left    => "left"
    case Vec3.right   => "right"
    case Vec3.up      => "up"
    case v            => s"unknown ($v)"
  }

  import math.{cos, sin}

  def rotateCcwX(theta: Double): Array[Array[Double]] = {
    val rad = theta.toRadians
    Array(
      Array(1, 0, 0, 0),
      Array(0, cos(rad), -sin(theta.toRadians), 0),
      Array(0, sin(theta.toRadians), cos(theta.toRadians), 0),
      Array(0, 0, 0, 1),
    )
  }

  def rotateCcwY(theta: Double): Array[Array[Double]] = {
    val rad = theta.toRadians
    Array(
      Array(cos(rad), 0, sin(rad), 0),
      Array(0, 1, 0, 0),
      Array(-sin(rad), 0, cos(rad), 0),
      Array(0, 0, 0, 1),
    )
  }

  def rotateCcwZ(theta: Double): Array[Array[Double]] = {
    val rad = theta.toRadians
    Array(
      Array(cos(rad), -sin(rad), 0, 0),
      Array(sin(rad), cos(rad), 0, 0),
      Array(0, 0, 1, 0),
      Array(0, 0, 0, 1),
    )
  }

  val rotationCcw90X: Array[Array[Int]] = Array(
    Array(1, 0, 0),
    Array(0, 0, -1),
    Array(0, 1, 0),
  )

  val rotationCcw90Y: Array[Array[Int]] = Array(
    Array(0, 0, 1),
    Array(0, 1, 0),
    Array(-1, 0, 0),
  )

  val rotationCcw90Z: Array[Array[Int]] = Array(
    Array(0, -1, 0),
    Array(1, 0, 0),
    Array(0, 0, 1),
  )

  val rotationCw90X: Array[Array[Int]] = Array(
    Array(1, 0, 0),
    Array(0, 0, 1),
    Array(0, -1, 0),
  )

  val rotationCw90Y: Array[Array[Int]] = Array(
    Array(0, 0, -1),
    Array(0, 1, 0),
    Array(1, 0, 0),
  )

  val rotationCw90Z: Array[Array[Int]] = Array(
    Array(0, 1, 0),
    Array(-1, 0, 0),
    Array(0, 0, 1),
  )

  def rotate(vec: Vec3, rotationMatrix: Array[Array[Double]]): Vec3 =
    matMul(vec, rotationMatrix)

  def matMul(vec: Vec3, m: Array[Array[Double]]): Vec3 = {
    val res = matMul(m, vecToMatrix(vec))
    matrixToVec3(res)
  }

  def matrixToVec3(m: Array[Array[Double]]): Vec3 = {
    assert(m.length >= 3)
    assert(m.forall(_.length == 1))
    Vec3(
      m(0)(0),
      m(1)(0),
      m(2)(0),
    )
  }

  def vecToMatrix(v3: Vec3): Array[Array[Double]] = Array(
    Array(v3.x),
    Array(v3.y),
    Array(v3.z),
    Array(1),
  )

  def matMul(a: Array[Array[Double]], b: Array[Array[Double]]): Array[Array[Double]] = {

    val colsA = a.head.length
    val rowsA = a.length

    val colsB = b.head.length
    val rowsB = b.length

    val result = Array.ofDim[Double](rowsA, colsB)

    0.until(rowsA).foreach { i =>
      0.until(colsB).foreach { j =>
        var sum = 0d
        0.until(colsA).foreach { k =>
          sum += a(i)(k) * b(k)(j)
        }
        result(i)(j) = sum
      }
    }
    result
  }
}

case class BoardMap(tiles: Array[Array[Tile]]) {
  def ascii: String =
    tiles
      .map(row =>
        row.map {
          case Tile.Empty    => " "
          case Tile.OpenTile => "."
          case Tile.Wall     => "#"
        }.mkString,
      )
      .mkString("\n")

  def contains(loc: Vec2): Boolean =
    tiles.isDefinedAt(loc.y) && tiles(loc.y).isDefinedAt(loc.x)

  lazy val startingPoint: Vec2 = tiles.last.zipWithIndex
    .find(_._1 != Tile.Empty)
    .map { case (_, idx) =>
      Vec2(idx, numRows - 1)
    }
    .get

  def apply(loc: Vec2): Option[Tile] =
    if (!contains(loc)) None
    else Some(tiles(loc.y)(loc.x))

  lazy val nonEmptyTiles = tiles.map(_.count(_ != Tile.Empty)).sum
  lazy val sideLength    = math.sqrt(nonEmptyTiles / 6).toInt
  lazy val numRows       = tiles.length
  lazy val numCols       = tiles.head.length
}

object BoardMap {

  def fromInput(input: String): BoardMap =
    BoardMap(
      input.linesIterator
        .map(line =>
          line.toArray.map[Tile] {
            case ' ' => Tile.Empty
            case '.' => Tile.OpenTile
            case '#' => Tile.Wall
          },
        )
        .toArray
        .reverse,
    )
}

case class BoardCube(faces: List[Face3D], boardMap: BoardMap)

object BoardCube {

  case class Face3D(faceLoc2D: Vec2, parent: Option[Face3D], vertexMappings: NonEmptyVector[(Vec3, Vec2)]) {
    val vertices     = vertexMappings.map(_._1)
    val origin: Vec3 = vertices.head
  }

  def manhattan(from: Vec2, to: Vec2): Int = {
    val dx = to.x - from.x
    val dy = to.y - from.y
    math.abs(dx) + math.abs(dy)
  }

  def isAdjacent(curr: Vec2, other: Vec2): Boolean = manhattan(curr, other) == 1

  sealed trait Direction

  object Direction {
    case object Up extends Direction

    case object Down extends Direction

    case object Right extends Direction

    case object Left extends Direction
  }

  def fromMap(boardMap: BoardMap): BoardCube = {
    val faces = parseFacesLayout(boardMap)
    assert(faces.size == 6)

    def from2D(face2d: Vec2): Vec3 = Vec3(face2d.x, 0, face2d.y)

    def flatRectFromOrigin(startingLocation: Vec3, loc: Vec2): NonEmptyVector[(Vec3, Vec2)] =
      NonEmptyVector
        .of(
          startingLocation,
          startingLocation + Vec3.forward,
          startingLocation + Vec3.forward + Vec3.right,
          startingLocation + Vec3.right,
        )
        .map(v => v -> Vec2(v.x.toInt, v.z.toInt))

    def recurseAssemble(faces: List[Vec2], result: List[Face3D]): List[Face3D] =
      faces match {
        case Nil  => result.reverse
        case rest =>
          val face2d   = rest.find(f => result.exists(f3d => isAdjacent(f3d.faceLoc2D, f))).get
          val neighbor = result.filter(f => isAdjacent(face2d, f.faceLoc2D)).head

          // me: 2,1  neighbor = 2,0 --> NeighborLocation.Up
          val d                           = face2d - neighbor.faceLoc2D
          val neighborLocation: Direction = d match {
            case Vec2(1, 0)  => Direction.Left
            case Vec2(-1, 0) => Direction.Right
            case Vec2(0, 1)  => Direction.Down
            case Vec2(0, -1) => Direction.Up
          }
          val d3d                         = from2D(d)
          val face3d                      = Face3D(face2d, Some(neighbor), flatRectFromOrigin(neighbor.origin + d3d, face2d))
          recurseAssemble(faces.diff(List(face2d)), face3d :: result)
      }

    // to match my example cutouts, I declare the top face of the leftmost column the top-face of the cube
    val top                       = faces.filter(_.x == 0).maxBy(_.y)
    val startingLocation          = Vec3.zero
    val assembledAndOrientedFaces =
      recurseAssemble(faces.diff(List(top)).toList, List(Face3D(top, None, flatRectFromOrigin(startingLocation, top))))

    BoardCube(assembledAndOrientedFaces, boardMap)

  }

  def parseFacesLayout(boardMap: BoardMap): Vector[Vec2] = {
    val l = boardMap.sideLength
    for {
      x  <- 0.to(5).toVector
      y  <- 0.to(5)
      loc = Vec2(x * l, y * l)
      if !boardMap(loc).forall(_ == Tile.Empty)
    } yield Vec2(x, y)
  }
}

case class State(location: Vec2, viewDirection: Vec2)

object Day22 extends AocDay {
  val day = 22

  def part1PerformAction(map: BoardMap, currentState: State, action: Action): State =
    action match {
      case w: Action.Walk             => part1Walk(map, currentState, w)
      case Action.Rotation(direction) =>
        val newViewDirection = currentState.viewDirection.rotate(direction)
        currentState.copy(viewDirection = newViewDirection)
    }

  def canWalk(tile: Tile) = tile match {
    case Tile.Empty    => false
    case Tile.OpenTile => true
    case Tile.Wall     => false
  }

  def part1Walk(map: BoardMap, currentState: State, walk: Action.Walk): State = {
    val Vec2(dx, dy) = currentState.viewDirection

    var stepsLeft = walk.steps
    val loc       = currentState.location
    val dir       = currentState.viewDirection

    val pathOnAxis =
      if (isVertical(dir)) getCol(map, dir, loc)
      else getRow(map, dir, loc)

    var currentIdx    = pathOnAxis.indexWhere(_._2 == loc)
    if (currentIdx < 0) {
      throw new IllegalArgumentException(s"my location $loc not found on path ${pathOnAxis.map(_._2)}")
    }
    while (stepsLeft > 0 && canWalk(pathOnAxis((currentIdx + 1) % pathOnAxis.size)._1)) {
      val (tile, nextLoc) = pathOnAxis(currentIdx)
      tile match {
        case Tile.Empty    => ???
        case Tile.OpenTile =>
          stepsLeft -= 1
          currentIdx = (currentIdx + 1) % pathOnAxis.size
        case Tile.Wall     =>
      }
    }
    val finalLocation = pathOnAxis(currentIdx)._2

    State(finalLocation, currentState.viewDirection)

  }

  def isVertical(dir: Vec2): Boolean =
    dir.y != 0

  def getRow(map: BoardMap, dir: Vec2, loc: Vec2): Vector[(Tile, Vec2)] = {
    val row = map
      .tiles(loc.y)
      .zipWithIndex
      .map { case (tile, x) => tile -> Vec2(x, loc.y) }
      .filterNot(_._1 == Tile.Empty)
      .toVector

    if (dir.x < 0)
      row.reverse
    else row

  }

  def getCol(map: BoardMap, dir: Vec2, loc: Vec2): Vector[(Tile, Vec2)] = {
    val coords = 0.until(map.numRows).map(y => Vec2(loc.x, y))
    val col    = coords
      .map(coord => map(coord) -> coord)
      .collect { case (Some(tile), coord) => tile -> coord }
      .filterNot(_._1 == Tile.Empty)
      .toVector

    if (dir.y < 0)
      col.reverse
    else col
  }

  def part1NextWalkableTile(map: BoardMap, loc: Vec2, dir: Vec2): (Vec2, Tile) = {
    val Vec2(dx, dy) = dir

    val nextX = math.max(0, (loc.x + dx) % map.numCols)
    val nextY = math.max(0, (loc.y + dy) % map.numRows)
    val next  = Vec2(nextX, nextY)

    map(next) match {
      case Some(Tile.Wall)         =>
        next -> Wall
      case Some(Tile.OpenTile)     =>
        next -> Tile.OpenTile
      case None | Some(Tile.Empty) =>
        part1NextWalkableTile(map, next, dir)
    }

  }

  def calcScore(state: State, map: BoardMap): Int = {
    val State(Vec2(col, row), dir) = state
    val facingScore                = dir match {
      case Vec2.right => 0
      case Vec2.down  => 1
      case Vec2.left  => 2
      case Vec2.up    => 3
    }
    (map.numRows - row) * 1000 + (col + 1) * 4 + facingScore
  }

  def solvePart1(input: String)(implicit visualDebugger: VisualDebugger) = IO {
    val Array(mapStr, directions) = input.split("\n\n")

    val map     = BoardMap.fromInput(mapStr)
    val actions = Action.parser.parse(directions).toOption.get._2

    visualDebugger.log(s"Tile at 11,4: ${map(Vec2(11, 4))}")

    visualDebugger.log(s"Starting Point: ${map.startingPoint}")
    visualDebugger.log(s"Nonempty Tiles: ${map.nonEmptyTiles}")
    visualDebugger.log(s"Side Length: ${map.sideLength}")

    val states = actions.toList.zipWithIndex.scanLeft(State(map.startingPoint, Vec2.right)) { case (s, (a, idx)) =>
      part1PerformAction(map, s, a)
    }

    val result: Int = calcScore(states.last, map)

    visualDebugger.render(renderStates(states, actions.toList))

    result.toString
  }

  def renderMap(map: BoardMap): VModifier = {
    import adventOfCode.util.ui.Helpers.FormattedTable._
    val header =
      tr(
        th("") :: 0.until(map.numCols).map(x => th(x.toString)).toList,
      )

    val rows = 0.until(map.numRows).toList.map { y =>
      val cells = 0.until(map.numCols).toList.map { x =>
        val loc = Vec2(x, y)
        td(VModifier(map(loc).map(_.toString).getOrElse("")))
      }
      tr(th(y) :: cells)
    }
    table(
      thead(header),
      tbody(rows),
    )
  }

  def renderStates(states: List[State], actions: List[Action]) = {
    import adventOfCode.util.ui.Helpers.FormattedTable._
    import cats.implicits._
    val paddedActions = actions.map(_.some).appended(Option.empty[Action])

    val rows = states.zip(paddedActions).map { case (s, a) =>
      tr(
        td(s.toString),
        td(a.map(_.toString)),
      )
    }
    table(
      thead(
        tr(th("State"), th("Action")),
      ),
      tbody(rows),
    )
  }

  def solvePart2(input: String)(implicit visualDebugger: VisualDebugger) = IO {
    val Array(mapStr, directions) = input.split("\n\n")

    val map     = BoardMap.fromInput(mapStr)
    val actions = Action.parser.parse(directions).toOption.get._2

    val cube            = BoardCube.fromMap(map)
    val allFaceVertices = cube.faces.flatMap(_.vertices.toVector).toSet

    visualDebugger.log("Layout of cube")
    visualDebugger.log(s"${cube.faces.map(_.faceLoc2D)}")

    val expected = (for {
      x <- List(0, 1)
      y <- List(0, -1)
      z <- List(0, 1)
    } yield Vec3(x, y, z)).toSet

    visualDebugger.log(s"cube has ${allFaceVertices.size} vertices. Expected: ${expected.size}:")
    visualDebugger.log(s"${allFaceVertices.toList.sortBy(v3 => (v3.x, v3.y, v3.z))}")
    val wrongVertices = allFaceVertices.diff(expected).toList
    visualDebugger.log(s"${wrongVertices.size} wrong vertices: ${wrongVertices.sortBy(v3 => (v3.x, v3.y, v3.z))}")

    val transformedFirstStep = transformFirstStep(cube)

    visualDebugger.render {
      //      val alternatingStyle = VModifier(cls := "odd:bg-white even:bg-slate-100 py-8")
      val alternatingStyle = VModifier(cls := "py-8")

      div(
        cls := "flex flex-col divide-y",
        renderCubeAndCurrentSetup(cube)(alternatingStyle),
        renderCubeAndCurrentSetup(transformedFirstStep)(alternatingStyle),
        renderCubeAndCurrentSetup(transformSecondStep(transformedFirstStep))(alternatingStyle),
      )
    }

    Camera3D.debugCamera()

    "???"
  }

  def renderCubeAndCurrentSetup(cube: BoardCube) = {
    val faceMapping = cube.faces.map { face =>
      val mapping = face.vertexMappings.toVector.map { case (v3, v2) =>
        s"${Vec2.str2D(v2)}  --> ${Vec3.str3D(v3)}"
      }.mkString("\n")

      s"""${Vec2.str2D(face.faceLoc2D)}
         |$mapping
         |""".stripMargin
    }.mkString("\n")
    div(
      cls := "flex flex-row",
      SvgDraw.visualizeCubeNet(cube)(cls := "w-1/3", display.inline),
      SvgDraw.visualizeCube3D(cube)(cls  := "w-1/3 aspect-[4/3]", display.inline),
      div(
        cls                              := "whitespace-pre-line font-mono text-xs",
        faceMapping,
      ),
    )
  }

  def transformFirstStep(cube: BoardCube) = {
    val cubeFaces = cube.faces

    val origin = cubeFaces.head
    val others = cubeFaces.tail

    val rotationOrigin = origin.vertexMappings.last

    println(s"rotating around origin $rotationOrigin")

    val translateMatrix: Array[Array[Double]] = Array(
      Array(1, 0, 0, rotationOrigin._1.x * -1),
      Array(0, 1, 0, rotationOrigin._1.y * -1),
      Array(0, 0, 1, rotationOrigin._1.z * -1),
      Array(0, 0, 0, 1),
    )

    val translateBackMatrix: Array[Array[Double]] = Array(
      Array(1, 0, 0, rotationOrigin._1.x),
      Array(0, 1, 0, rotationOrigin._1.y),
      Array(0, 0, 1, rotationOrigin._1.z),
      Array(0, 0, 0, 1),
    )

    val superMatrix = Vec3.matMul(translateBackMatrix, Vec3.matMul(Vec3.rotateCcwZ(90), translateMatrix))
    val rotatedRest = others.map { face =>
      val transformedVertrixMapping = face.vertexMappings.map { case (origV3, origV2) =>
        val transformed = Vec3.matMul(origV3, superMatrix)
        transformed -> origV2
      }
      face.copy(vertexMappings = transformedVertrixMapping)

    }

    val transformedCubeFaces = origin :: rotatedRest
    cube.copy(faces = transformedCubeFaces)
  }

  def transformSecondStep(cube: BoardCube) = {
    val cubeFaces = cube.faces

    val origin = cubeFaces(1)
    val others = cubeFaces.drop(2)

    val rotationOrigin = origin.vertexMappings.last

    println(s"rotating around origin $rotationOrigin")

    val translateMatrix: Array[Array[Double]] = Array(
      Array(1, 0, 0, rotationOrigin._1.x * -1),
      Array(0, 1, 0, rotationOrigin._1.y * -1),
      Array(0, 0, 1, rotationOrigin._1.z * -1),
      Array(0, 0, 0, 1),
    )

    val translateBackMatrix: Array[Array[Double]] = Array(
      Array(1, 0, 0, rotationOrigin._1.x),
      Array(0, 1, 0, rotationOrigin._1.y),
      Array(0, 0, 1, rotationOrigin._1.z),
      Array(0, 0, 0, 1),
    )

    val superMatrix = Vec3.matMul(translateBackMatrix, Vec3.matMul(Vec3.rotateCcwZ(90), translateMatrix))
    val rotatedRest = others.map { face =>
      val transformedVertrixMapping = face.vertexMappings.map { case (origV3, origV2) =>
        val transformed = Vec3.matMul(origV3, superMatrix)
        transformed -> origV2
      }
      face.copy(vertexMappings = transformedVertrixMapping)

    }

    val transformedCubeFaces = cubeFaces.take(2) ::: rotatedRest
    cube.copy(faces = transformedCubeFaces)
  }

  object SvgDraw {

    // these colors are made available for stroke and fill in the tailwind-config using a so called safelist
    // if not listed these, the exact string (e.g. stroke-purple-600) has to be in the sourcecode in order for tailwind to export the css class.
    val cubeColors = "red,yellow,green,blue,purple,pink".split(",")

    def visualizeCube3D(cube: BoardCube): VNode = {
      import svg._

      val numHorizontal = cube.faces.map(_.faceLoc2D.x).max + 1
      val numVertical   = cube.faces.map(_.faceLoc2D.y).max + 1

      val projectedFaceVertices = cube.faces.map { face =>
        val projected = face.vertices.map { v =>
          Vec3.matMul(v, Camera3D.CameraMatrix)
        }

        (face, projected)
      }

      val faces =
        projectedFaceVertices
          .zip(cubeColors)
          .sortBy { case ((_, projection), _) =>
            (projection.toVector.maxBy(_.z).z * -1, projection.toVector.maxBy(_.y).y)
          }
          .map { case ((face, projected), color) =>
            val polygonPointsStr = projected.map(v => s"${v.x},${v.y}").toVector.mkString(" ")
            val strokeWidthValue = 1
            polygon(
              points      := polygonPointsStr,
              cls         := s"stroke-$color-800 fill-none",
              strokeWidth := strokeWidthValue.toString,
            )
          }

      val allProjected = projectedFaceVertices.flatMap(_._2.toVector)
      val maxX         = allProjected.map(_.x).max
      val minX         = allProjected.map(_.x).min
      val maxY         = allProjected.map(_.y).max
      val minY         = allProjected.map(_.y).min
      val maxZ         = allProjected.map(_.z).max
      val minZ         = allProjected.map(_.z).min

      val width  = math.abs(maxX - minX)
      val height = math.abs(maxY - minY)

      val offsetX = 0.1 * width
      val offsetY = 0.1 * height

      val canvasWidth  = (2 * offsetX) + width
      val canvasHeight = (2 * offsetY) + height

      svg(
        shapeRendering := "crisp-edges",
        viewBox        := s"${minX - offsetX} ${minY - offsetY} $canvasWidth $canvasHeight",
        g(faces),
      )
    }

    def visualizeCubeNet(cube: BoardCube): VNode = {
      import svg._

      val numHorizontal = cube.faces.map(_.faceLoc2D.x).max + 1
      val numVertical   = cube.faces.map(_.faceLoc2D.y).max + 1

      val sideLength   = cube.boardMap.sideLength
      val offset       = 0.1 * sideLength
      val canvasWidth  = (2 * offset) + sideLength * numHorizontal
      val verticalSize = sideLength * numVertical
      val canvasHeight = (2 * offset) + verticalSize

      val faces = cube.faces.zip(cubeColors).map { case (face, color) =>
        val loc              = face.faceLoc2D
        val svgLoc           = face.faceLoc2D.copy(y = numVertical - face.faceLoc2D.y - 1)
        val topLeft          = svgLoc * sideLength
        val topRight         = topLeft + Vec2(sideLength, 0)
        val bottomLeft       = topLeft + Vec2(0, -sideLength)
        val bottomRight      = topRight + Vec2(0, -sideLength)
        val strokeWidthValue = sideLength * 0.02
        val locStr           = s"(${loc.x},${loc.y})"

        val faceInfos = List(
          s"origin: ${str3D(face.origin)}",
          // s"x-direction: ${str3D(face.xVec)} (${Vec3.directionToString(face.xVec)})",
          // s"y-direction: ${str3D(face.yVec)} (${Vec3.directionToString(face.yVec)})",
        )
        // s"vertices (cw from orig):",
        // ) ::: face.vertices.map(str3D)

        val fontSizeAttr = fontSize := (sideLength * 0.075).toString
        svg(
          idAttr := s"face-${loc.x}-${loc.y}",
          x      := (offset + topLeft.x).toString,
          y      := (offset + topLeft.y).toString,
          rect(
            x           := strokeWidthValue.toString,
            y           := strokeWidthValue.toString,
            width       := sideLength.toString,
            height      := sideLength.toString,
            cls         := s"stroke-$color-400 fill-$color-300",
            strokeWidth := strokeWidthValue.toString,
          ),
          text( // bottom-left (origin)
            x                 := (sideLength * 0.05).toString,
            y                 := (sideLength * 0.975).toString,
            alignmentBaseline := "auto",
            str3D(face.vertices.head),
            fontSizeAttr,
          ),
          text( // top-left
            x                 := (sideLength * 0.05).toString,
            y                 := (sideLength * 0.05).toString,
            alignmentBaseline := "hanging",
            str3D(face.vertices.getUnsafe(1)),
            fontSizeAttr,
          ),
          text( // top-right
            x                 := (sideLength * 0.975).toString,
            y                 := (sideLength * 0.05).toString,
            alignmentBaseline := "hanging",
            textAnchor        := "end",
            str3D(face.vertices.getUnsafe(2)),
            fontSizeAttr,
          ),
          text( // bottom-right
            x                 := (sideLength * 0.975).toString,
            y                 := (sideLength * 0.975).toString,
            alignmentBaseline := "auto",
            textAnchor        := "end",
            str3D(face.vertices.last),
            fontSizeAttr,
          ),
          text(
            x                 := (sideLength / 2).toString,
            y                 := (sideLength / 2.5).toString,
            transform         := s"translate(${sideLength / 2})",
            fontSize          := (sideLength * 0.1).toString,
            textAnchor        := "middle",
            alignmentBaseline := "central",
            tspan(textAnchor := "middle", x := "0", locStr),
            faceInfos.map(info =>
              tspan(
                textAnchor := "middle",
                fontSizeAttr,
                x          := "0",
                dy         := "1.5em",
                info,
              ),
            ),
          ),
        )
      }

      svg(
        shapeRendering := "crisp-edges",
        cls            := "bg-gray-0",
        viewBox        := s"0 0 $canvasWidth $canvasHeight",
        g(faces),
      )
    }
  }

  object Camera3D {
    val viewDirection  = Vec3.matMul(Vec3.rotateCcwY(15), Vec3.rotateCcwX(-80))
    // Vec3.matMul(Vec3.rotateCcwX(-20), Vec3.rotateCcwY(45))

    //https://www.3dgep.com/understanding-the-view-matrix/
    val cameraLocation = Vec3(-10, -10, -10)

    val cameraLocationTranslationMatrix: Array[Array[Double]] = Array(
      Array(1, 0, 0, cameraLocation.x),
      Array(0, 1, 0, cameraLocation.y),
      Array(0, 0, 1, cameraLocation.z),
      Array(0, 0, 0, 1),
    )

    private val M: Array[Array[Double]] =
      Vec3.matMul(cameraLocationTranslationMatrix, viewDirection) // first rotate, then offset
    private val MInverted: Array[Array[Double]] = Matrix4d(M).inverted

    private val near   = -5.0
    private val far    = 5
    private val right  = 10
    private val left   = 0
    private val top    = 10
    private val bottom = 0

    private val n = near
    private val f = far
    private val r = right
    private val l = left
    private val t = top
    private val b = bottom

    private val width  = 20
    private val height = 20
    private val w      = width
    private val h      = height

    private val V: Array[Array[Double]] = Array(
      // @formatter:off
      Array(2*n/(r-l), 0        , (r+l)/(r-l), 0             ),
      Array(0        , 2*n/(b-t), (b+t)/(b-t), 0             ),
      Array(0        , 0        , (f+n)/(f-n), (-2*n*f)/(f-n)),
      Array(0        , 0        , 1          , 0             ),
      // @formatter:on
    )

    private val S: Array[Array[Double]] = Array(
      // @formatter:off
      Array(w/2, 1   , 1 , 1 ),
      Array(1  , -h/2, 1 , 1 ),
      Array(1  , 1   , 1 , 1 ),
      Array(1  , 1   , 1 , 1 ),
      // @formatter:on
    )

    private val B: Array[Array[Double]] = Array(
      // @formatter:off
      Array(1  , 1   , 1 , w/2 ),
      Array(1  , -1  , 1 , h/2 ),
      Array(1  , 1   , 1 , 1   ),
      Array(1  , 1   , 1 , 1   ),
      // @formatter:on
    )

    val CameraMatrix: Array[Array[Double]] =
      Vec3.matMul(B, Vec3.matMul(S, Vec3.matMul(V, MInverted)))

    def debugCamera(): Unit = {
      println("Camera View Matrix:")
      M.foreach(r => println(s"${r.mkString(",")}"))

      println("\nInverted Camera View Matrix:")
      MInverted.foreach(r => println(s"${r.mkString(",")}"))

      println("\nFinal Camera Matrix Camera View Matrix:")
      CameraMatrix.foreach(r => println(s"${r.mkString(",")}"))
    }
  }

  case class Matrix4d(m: Array[Array[Double]]) {
    lazy val inverted: Array[Array[Double]] = {
      // https://www.euclideanspace.com/maths/algebra/matrix/functions/inverse/fourD/index.htm
      val newM   = Array(
        // @formatter:off
        Array(
          m(1)(2) * m(2)(3) * m(3)(1) - m(1)(3) * m(2)(2) * m(3)(1) + m(1)(3) * m(2)(1) * m(3)(2) - m(1)(1) * m(2)(3) * m(3)(2) - m(1)(2) * m(2)(1) * m(3)(3) + m(1)(1) * m(2)(2) * m(3)(3), //00
          m(0)(3) * m(2)(2) * m(3)(1) - m(0)(2) * m(2)(3) * m(3)(1) - m(0)(3) * m(2)(1) * m(3)(2) + m(0)(1) * m(2)(3) * m(3)(2) + m(0)(2) * m(2)(1) * m(3)(3) - m(0)(1) * m(2)(2) * m(3)(3), //01
          m(0)(2) * m(1)(3) * m(3)(1) - m(0)(3) * m(1)(2) * m(3)(1) + m(0)(3) * m(1)(1) * m(3)(2) - m(0)(1) * m(1)(3) * m(3)(2) - m(0)(2) * m(1)(1) * m(3)(3) + m(0)(1) * m(1)(2) * m(3)(3), //02
          m(0)(3) * m(1)(2) * m(2)(1) - m(0)(2) * m(1)(3) * m(2)(1) - m(0)(3) * m(1)(1) * m(2)(2) + m(0)(1) * m(1)(3) * m(2)(2) + m(0)(2) * m(1)(1) * m(2)(3) - m(0)(1) * m(1)(2) * m(2)(3),  //03
        ),
        Array(
          m(1)(3) * m(2)(2) * m(3)(0) - m(1)(2) * m(2)(3) * m(3)(0) - m(1)(3) * m(2)(0) * m(3)(2) + m(1)(0) * m(2)(3) * m(3)(2) + m(1)(2) * m(2)(0) * m(3)(3) - m(1)(0) * m(2)(2) * m(3)(3), //10
          m(0)(2) * m(2)(3) * m(3)(0) - m(0)(3) * m(2)(2) * m(3)(0) + m(0)(3) * m(2)(0) * m(3)(2) - m(0)(0) * m(2)(3) * m(3)(2) - m(0)(2) * m(2)(0) * m(3)(3) + m(0)(0) * m(2)(2) * m(3)(3), //11
          m(0)(3) * m(1)(2) * m(3)(0) - m(0)(2) * m(1)(3) * m(3)(0) - m(0)(3) * m(1)(0) * m(3)(2) + m(0)(0) * m(1)(3) * m(3)(2) + m(0)(2) * m(1)(0) * m(3)(3) - m(0)(0) * m(1)(2) * m(3)(3), //12
          m(0)(2) * m(1)(3) * m(2)(0) - m(0)(3) * m(1)(2) * m(2)(0) + m(0)(3) * m(1)(0) * m(2)(2) - m(0)(0) * m(1)(3) * m(2)(2) - m(0)(2) * m(1)(0) * m(2)(3) + m(0)(0) * m(1)(2) * m(2)(3), //13
        ),
        Array(
          m(1)(1) * m(2)(3) * m(3)(0) - m(1)(3) * m(2)(1) * m(3)(0) + m(1)(3) * m(2)(0) * m(3)(1) - m(1)(0) * m(2)(3) * m(3)(1) - m(1)(1) * m(2)(0) * m(3)(3) + m(1)(0) * m(2)(1) * m(3)(3), //20
          m(0)(3) * m(2)(1) * m(3)(0) - m(0)(1) * m(2)(3) * m(3)(0) - m(0)(3) * m(2)(0) * m(3)(1) + m(0)(0) * m(2)(3) * m(3)(1) + m(0)(1) * m(2)(0) * m(3)(3) - m(0)(0) * m(2)(1) * m(3)(3), //21
          m(0)(1) * m(1)(3) * m(3)(0) - m(0)(3) * m(1)(1) * m(3)(0) + m(0)(3) * m(1)(0) * m(3)(1) - m(0)(0) * m(1)(3) * m(3)(1) - m(0)(1) * m(1)(0) * m(3)(3) + m(0)(0) * m(1)(1) * m(3)(3), //22
          m(0)(3) * m(1)(1) * m(2)(0) - m(0)(1) * m(1)(3) * m(2)(0) - m(0)(3) * m(1)(0) * m(2)(1) + m(0)(0) * m(1)(3) * m(2)(1) + m(0)(1) * m(1)(0) * m(2)(3) - m(0)(0) * m(1)(1) * m(2)(3), //23
        ),
        Array(
          m(1)(2) * m(2)(1) * m(3)(0) - m(1)(1) * m(2)(2) * m(3)(0) - m(1)(2) * m(2)(0) * m(3)(1) + m(1)(0) * m(2)(2) * m(3)(1) + m(1)(1) * m(2)(0) * m(3)(2) - m(1)(0) * m(2)(1) * m(3)(2), //30
          m(0)(1) * m(2)(2) * m(3)(0) - m(0)(2) * m(2)(1) * m(3)(0) + m(0)(2) * m(2)(0) * m(3)(1) - m(0)(0) * m(2)(2) * m(3)(1) - m(0)(1) * m(2)(0) * m(3)(2) + m(0)(0) * m(2)(1) * m(3)(2), //31
          m(0)(2) * m(1)(1) * m(3)(0) - m(0)(1) * m(1)(2) * m(3)(0) - m(0)(2) * m(1)(0) * m(3)(1) + m(0)(0) * m(1)(2) * m(3)(1) + m(0)(1) * m(1)(0) * m(3)(2) - m(0)(0) * m(1)(1) * m(3)(2), //32
          m(0)(1) * m(1)(2) * m(2)(0) - m(0)(2) * m(1)(1) * m(2)(0) + m(0)(2) * m(1)(0) * m(2)(1) - m(0)(0) * m(1)(2) * m(2)(1) - m(0)(1) * m(1)(0) * m(2)(2) + m(0)(0) * m(1)(1) * m(2)(2), //33
        )
      // @formatter:on
      )
      val factor = 1 / determinant(newM)

      newM.map(row => row.map(cell => cell * factor))
    }
    def determinant(m: Array[Array[Double]]): Double =

      // @formatter:off
      m(0)(3) * m(1)(2) * m(2)(1) * m(3)(0) - m(0)(2) * m(1)(3) * m(2)(1) * m(3)(0) - m(0)(3) * m(1)(1) * m(2)(2) * m(3)(0) + m(0)(1) * m(1)(3) * m(2)(2) * m(3)(0) +
      m(0)(2) * m(1)(1) * m(2)(3) * m(3)(0) - m(0)(1) * m(1)(2) * m(2)(3) * m(3)(0) - m(0)(3) * m(1)(2) * m(2)(0) * m(3)(1) + m(0)(2) * m(1)(3) * m(2)(0) * m(3)(1) +
      m(0)(3) * m(1)(0) * m(2)(2) * m(3)(1) - m(0)(0) * m(1)(3) * m(2)(2) * m(3)(1) - m(0)(2) * m(1)(0) * m(2)(3) * m(3)(1) + m(0)(0) * m(1)(2) * m(2)(3) * m(3)(1) +
      m(0)(3) * m(1)(1) * m(2)(0) * m(3)(2) - m(0)(1) * m(1)(3) * m(2)(0) * m(3)(2) - m(0)(3) * m(1)(0) * m(2)(1) * m(3)(2) + m(0)(0) * m(1)(3) * m(2)(1) * m(3)(2) +
      m(0)(1) * m(1)(0) * m(2)(3) * m(3)(2) - m(0)(0) * m(1)(1) * m(2)(3) * m(3)(2) - m(0)(2) * m(1)(1) * m(2)(0) * m(3)(3) + m(0)(1) * m(1)(2) * m(2)(0) * m(3)(3) +
      m(0)(2) * m(1)(0) * m(2)(1) * m(3)(3) - m(0)(0) * m(1)(2) * m(2)(1) * m(3)(3) - m(0)(1) * m(1)(0) * m(2)(2) * m(3)(3) + m(0)(0) * m(1)(1) * m(2)(2) * m(3)(3)
      // @formatter:on
  }
}

