package adventOfCode.year2021

import adventOfCode.{AocDay, VisualDebugger}
import cats.effect.IO
import outwatch.dsl._

object Day08 extends AocDay {
  val day = 8

  val originalDigitConfig = Map(
    0 -> "abcefg",
    1 -> "cf",
    2 -> "acdeg",
    3 -> "acdfg",
    4 -> "bcdf",
    5 -> "abdfg",
    6 -> "abdefg",
    7 -> "acf",
    8 -> "abcdefg",
    9 -> "abcdfg"
  )

  val originalWireMappingOfDigit8 = "abcdefg"

  def solvePart1(input: String)(implicit visualDebugger: VisualDebugger) = IO{
    val lines = input.linesIterator.toList.filterNot(_.trim.isEmpty)
    val digitsWithUniqueLengthOfSegments: Map[Int, String] =
      originalDigitConfig.toList
        .groupBy(_._2.length)
        .filter(_._2.length == 1)
        .view
        .map { case (i, grp) => i -> grp.head._2 }
        .toMap

    val outputValues = lines.map(_.split('|').last.trim)
    val relevantOutputValues = outputValues
      .flatMap(ov => ov.split(" "))
      .filter(v => digitsWithUniqueLengthOfSegments.keySet.contains(v.length))

    visualDebugger.log("output values")
    outputValues.foreach(o => visualDebugger.log(o))

    visualDebugger.log("\nrelevant output values")
    relevantOutputValues.foreach(o => visualDebugger.log(o))

    relevantOutputValues.size.toString
  }

  def findOutputValue(
      inputLine: String
  )(implicit visualDebugger: VisualDebugger): Int = {

    val patterns =
      inputLine.split('|').toList.map(_.trim)

    val outputSignalPatterns = patterns.last.trim.split(" ")

    val inputSignalPatterns = patterns.head.trim.split(" ")
    val segmentLengths = inputSignalPatterns.map(p => p -> p.length)

    //unique digits
    val digit1 = segmentLengths.find(_._2 == 2).get._1
    val digit7 = segmentLengths.find(_._2 == 3).get._1
    val digit4 = segmentLengths.find(_._2 == 4).get._1
    val digit8 = segmentLengths.find(_._2 == 7).get._1

    //derived digits
    // length 6
    val length6Digits = segmentLengths.filter(_._2 == 6)
    val digit6 = length6Digits.find(_._1.intersect(digit1).length < 2).get._1

    val length5Digits = segmentLengths.filter(_._2 == 5)
    val digit3 = length5Digits.find(_._1.intersect(digit1).length == 2).get._1
    val lowerPartOf1 = digit1.intersect(digit6)
    val digit5 = length5Digits
      .filterNot(_._1 == digit3)
      .find(_._1.contains(lowerPartOf1))
      .get
      ._1

    val digit9Str = digit3.concat(digit5).distinct.sorted
    val digit9 = length6Digits.find(_._1.sorted == digit9Str).get._1
    val digit0 =
      length6Digits.filterNot(t => t._1 == digit6 || t._1 == digit9).head._1
    val digit2 = length5Digits
      .filterNot(_._1 == digit3)
      .find(_._1.contains(lowerPartOf1) == false)
      .get
      ._1

    val currentSetup = Map(
      0 -> digit0,
      1 -> digit1,
      2 -> digit2,
      3 -> digit3,
      4 -> digit4,
      5 -> digit5,
      6 -> digit6,
      7 -> digit7,
      8 -> digit8,
      9 -> digit9
    )

    val outputNumbers = outputSignalPatterns.map { p =>
      val sortedPattern = p.sorted
      val lookedUpDigit =
        currentSetup.find(_._2.sorted == sortedPattern).map(_._1)
      (p, sortedPattern, lookedUpDigit.get)
    }

    val result = outputNumbers.map(_._3.toString).mkString.toInt

    visualDebugger.render(
      div(
        h2("resolved setup with test input"),
        table(
          thead(
            tr(
              th("digit"),
              th("segments"),
              th("sorted segments")
            )
          ),
          tbody(currentSetup.toList.sorted.map { case (d, str) =>
            tr(td(d), td(str), td(str.sorted))
          })
        )
      )
    )

    visualDebugger.render(
      div(
        h2("resolved output digits"),
        table(
          thead(
            tr(
              th("segment"),
              th("sorted"),
              th("resolved")
            )
          ),
          tbody(outputNumbers.toList.map { case (p, pSorted, digit) =>
            tr(td(p), td(pSorted), td(digit))
          })
        )
      )
    )

    result

    /*
    unique candidates

    digit  segments  length
    1      cf        2
    7      acf       3
    4      bcdf      4
    8      abcdefg   7

    derivced candidates
      length 6 (3x)
        6 is the only digit that doesn't contain all of 1
        9 is the union of 3 and 5
        0 is the last of the length-6-group
      length 5 (3x)
        5, 2, 3
        3 contains all of 1
        5 vs 2
          intersection of 6 and 1 yields f (the lower part of the 1)
          5 contains the f segment
          2 doesn't contain the f segment
     */

  }

  def solvePart2(input: String)(implicit visualDebugger: VisualDebugger) = IO{

    val lines = input.linesIterator.toList.filterNot(_.trim.isEmpty)
    findOutputValue(
      "acedgfb cdfbe gcdfa fbcad dab cefabd cdfgeb eafb cagedb ab | cdfeb fcadb cdfeb cdbaf"
    ).toString
    val result =
      lines.map(findOutputValue(_)(VisualDebugger.noOpLoggingDebugger)).sum

    result.toString

  }
}

