package adventOfCode.year2021

import Day16.Packet.{Literal, Operator}
import adventOfCode.util.Util.clamp
import adventOfCode.{AocDay, VisualDebugger}
import cats.data.StateT
import cats.effect.IO
import cats.implicits._
import colibri.{Observable, Subject}
import outwatch._
import outwatch.dsl._

import scala.util.Try

object Day16 extends AocDay {

  def hexToBinary(str: String) =
    str.map { char =>
      val bitValue = scala.math.BigInt(char.toString, 16).toString(2)
      ("0000" + bitValue).takeRight(4)
    }.mkString

  def binaryToInt(str: String): Int =
    scala.math.BigInt(str, 2).toInt

  val day = 16

  def solvePart1(input: String)(implicit visualDebugger: VisualDebugger): IO[String] =
    packet.runA(initialState(input.trim)).attempt.map {
      case Left(_)         => "broken"
      case Right(exprTree) => exprTree.sumVersions.toString
    }

  def solvePart2(input: String)(implicit visualDebugger: VisualDebugger) = {

    visualDebugger.render {
      val text                             = Subject.behavior("C200B40A82")
      val bits                             = text.map(t => Try(hexToBinary(t)).toOption)
      val expression                       = bits.mapAsync {
        case Some(bits) =>
          packet.runA(ParseState(bits, 0)).option
        case None       => None.pure[IO]
      }
      val result: Observable[Option[Long]] = expression.map(_.map(_.eval))

      div(
        h3("Manual experiments", cls := "text-2xl font-bold mb-2"),
        dsl.input(
          cls                        := "border-2 border-gray-800 focus:border-blue-400 w-full font-mono",
          tpe                        := "text",
          value <-- text,
          onInput.value --> text,
        ),
        h4("bits", cls               := "text-xl font-bold mb-2"),
        bits.map(_.getOrElse("invalid hex string")).map { bitsString =>
          val bitsWithNewlines = bitsString.grouped(60).mkString("\n")
          div(
            textArea(
              cls        := "font-mono",
              cols       := 60,
              whiteSpace := "pre",
              rows       := clamp(bitsWithNewlines.linesIterator.size, 1, 40),
              readOnly   := true,
              bitsWithNewlines,
            ),
          )
        },
        h4("expression", cls         := "text-xl font-bold mb-2"),
        p(cls                        := "font-mono", expression.map(_.map(_.toString).getOrElse("invalid packet"))),
        h4("result", cls             := "text-xl font-bold mb-2"),
        p(cls                        := "font-mono", result.map(_.map(_.toString).getOrElse("invalid packet"))),
      )
    }
    packet.runA(initialState(input.trim)).attempt.map {
      case Left(_)         => "broken"
      case Right(exprTree) => exprTree.eval.toString
    }
  }

  def packet: Parsed[Packet] =
    for {
      version <- int(3)
      typeId  <- int(3)
      packet  <- if (typeId == 4) literal(version) else operator(version, typeId)
    } yield packet

  def int(bits: Int): Parsed[Int] =
    get(bits).map(binaryToInt)

  def get(bits: Int): Parsed[String] =
    StateT { s =>
      val (output, newBits) = s.bits.splitAt(bits)
      val newPos            = s.pos + bits
      IO((ParseState(newBits, newPos), output))
    }

  def literal(version: Int): Parsed[Literal] =
    nibbles().map(value => Packet.Literal(version, value))

  def operator(version: Int, typeId: Int): Parsed[Operator] =
    for {
      usingCount <- bool
      n          <- if (usingCount) int(11) else int(15)
      subPackets <- if (usingCount) packet.replicateA(n) else packetsByLength(n)
    } yield Packet.Operator(version, typeId, subPackets)

  def packetsByLength(n: Int): Parsed[List[Packet]] =
    for {
      startPos <- pos
      packets  <- packet.whileM[Vector](pos.map(_ < startPos + n))
    } yield packets.toList

  def pos: Parsed[Int] = StateT.inspect(_.pos)

  def nibbles(acc: Long = 0): Parsed[Long] =
    for {
      keepGoing <- bool
      digit     <- int(4)
      value     <- StateT.pure[IO, ParseState, Long]((acc << 4) + digit.toLong)
      result    <- if (keepGoing) nibbles(value) else StateT.pure[IO, ParseState, Long](value)
    } yield result

  def bool: Parsed[Boolean] = get(1).map(_ == "1")

  sealed trait Packet {
    def sumVersions: Int = this match {
      case Packet.Literal(version, _)              => version
      case Packet.Operator(version, _, subPackets) => version + subPackets.map(_.sumVersions).sum
    }

    def eval: Long = this match {
      case Packet.Literal(_, value)               => value
      case Packet.Operator(_, typeId, subPackets) =>
        typeId match {
          case 0 => subPackets.map(_.eval).sum
          case 1 => subPackets.map(_.eval).product
          case 2 => subPackets.map(_.eval).min
          case 3 => subPackets.map(_.eval).max
          case 5 =>
            val List(p1, p2) = subPackets
            if (p1.eval > p2.eval) 1L else 0L
          case 6 =>
            val List(p1, p2) = subPackets
            if (p1.eval < p2.eval) 1L else 0L
          case 7 =>
            val List(p1, p2) = subPackets
            if (p1.eval == p2.eval) 1L else 0L
        }
    }
  }

  object Packet {
    case class Literal(version: Int, value: Long)                            extends Packet
    case class Operator(version: Int, typeId: Int, subPackets: List[Packet]) extends Packet
  }

  case class ParseState(bits: String, pos: Int)
  type Parsed[A] = StateT[IO, ParseState, A]

  def initialState(hex: String): ParseState =
    ParseState(hexToBinary(hex), 0)

}
