package adventOfCode.year2022

import adventOfCode.{AocDay, VisualDebugger}
import cats.effect.IO
import adventOfCode.util.Parsers._
import cats.parse.{Numbers, Parser => P}

object Day08 extends AocDay {
  val day = 8

  val parser                  = identityStringParser
  def parseLine(line: String) = parser.parse(line).toOption.get._2

  type Grid = Vector[Vector[Int]]
  def parseGrid(input: String): Grid =
    input.trim.linesIterator.map { line =>
      line.toVector.map(char => char.asDigit)
    }.toVector

  def countNumberOfVisibleTrees(grid: Grid, loc: (Int, Int), direction: Direction): Set[(Int, Int)] = {

    var location = loc

    var compareValue = -1
    val result       = collection.mutable.Set.empty[(Int, Int)]
    while (compareValue <= 9 && grid.isDefinedAt(location._1) && grid(location._1).isDefinedAt(location._2)) {
      val (r, c)       = location
      val currentValue = grid(location._1)(location._2)
      if (currentValue > compareValue) {
        compareValue = currentValue
        result.addOne(location)
      }

      location = (r + direction.rowDiff, c + direction.colDiff)
    }
    result.toSet

  }

  def solvePart1(input: String)(implicit visualDebugger: VisualDebugger) = IO {
    val grid = parseGrid(input)

    val leftEdgeResults = for {
      row     <- 0.until(grid.head.length)
      location = (row, 0)
    } yield countNumberOfVisibleTrees(grid, location, right)

    val rightEdgeResults = for {
      row     <- 0.until(grid.head.length).reverse
      location = (row, grid.head.length - 1)
    } yield countNumberOfVisibleTrees(grid, location, left)

    val topEdgeResults = for {
      col     <- 0.until(grid.length)
      location = (0, col)
    } yield countNumberOfVisibleTrees(grid, location, down)

    val bottomEdgeResults = for {
      col     <- 0.until(grid.length).reverse
      location = (grid.length - 1, col)
    } yield countNumberOfVisibleTrees(grid, location, up)

    val allVisibleLocations =
      (leftEdgeResults ++ rightEdgeResults ++ topEdgeResults ++ bottomEdgeResults).reduce(_ ++ _)
    allVisibleLocations.size.toString
  }

  def calcScenicScore(grid: Grid, loc: (Int, Int)): Int = {

    // stop if you reach an edge or at the first tree that is the same height or taller than the tree under consideration

    val compareValue = grid(loc._1)(loc._2)
    val scores       = allDirections.map { direction =>
      var location       = direction.walk(loc)
      var directionScore = 0
      var currentValue   = -1
      while (
        currentValue < compareValue && grid.isDefinedAt(location._1) && grid(location._1).isDefinedAt(location._2)
      ) {
        currentValue = grid(location._1)(location._2)
        directionScore += 1
        location = direction.walk(location)
      }

      direction -> directionScore

    }

    val scenicScore = scores.map(_._2).product
    scenicScore

  }

  def solvePart2(input: String)(implicit visualDebugger: VisualDebugger) = IO {

    val grid               = parseGrid(input)
    val potentialLocations = for {
      r    <- grid.indices.drop(1).dropRight(1).toVector
      c    <- grid.headOption.getOrElse(Vector.empty).drop(1).dropRight(1).indices
      score = calcScenicScore(grid, (r, c))
    } yield ((r, c), score)

    val scenicScoreOfEx1 = calcScenicScore(grid, (1, 2))
    visualDebugger.log("Scenic score of example 1 should be 4")
    visualDebugger.log("(height of 5 at row #1 col #2) should be 4")
    visualDebugger.log(scenicScoreOfEx1)

    visualDebugger.log(s"found ${potentialLocations.size} potential locations. Top 10:")
    val top10 = potentialLocations.sortBy(_._2 * -1).take(10)
    top10.foreach(visualDebugger.log(_))

    val result = top10.headOption.map(_._2).getOrElse(-1)
    result.toString
  }

  case class Direction(rowDiff: Int, colDiff: Int) {
    def walk(loc: (Int, Int)): (Int, Int) = (loc._1 + rowDiff, loc._2 + colDiff)
  }
  val up = Direction(rowDiff = -1, colDiff = 0)
  val down          = Direction(rowDiff = 1, colDiff = 0)
  val left          = Direction(rowDiff = 0, colDiff = -1)
  val right         = Direction(rowDiff = 0, colDiff = 1)
  val allDirections = List(up, down, left, right)
}
