package adventOfCode.year2022

import adventOfCode.year2022.Day02.RPS.{fromStr, winMap, Draw, Game, MeWin, OppWin, Result}
import adventOfCode.{AocDay, VisualDebugger}
import cats.effect.IO
import outwatch.dsl.tr
import adventOfCode.{AocDay, VisualDebugger}
import cats.effect.IO
import outwatch.dsl._

object Day02 extends AocDay {
  val day = 2

  sealed trait RPS

  object RPS {
    case object Rock     extends RPS
    case object Paper    extends RPS
    case object Scissors extends RPS

    def fromStr(str: String): RPS =
      str match {
        case "A" => Rock
        case "B" => Paper
        case "C" => Scissors
        case "X" => Rock
        case "Y" => Paper
        case "Z" => Scissors
      }

    sealed trait Result
    case object MeWin  extends Result
    case object OppWin extends Result
    case object Draw   extends Result

    case class Game(me: RPS, opp: RPS) {
      val result: Result = calcResult(me, opp)
      val score: Int     = calcScore(me, result)
    }

    def calcResult(me: RPS, opp: RPS): Result =
      winMap((me, opp))

    val winMap: Map[(RPS, RPS), Result] = Map(
      (Rock, Rock)         -> Draw,
      (Rock, Paper)        -> OppWin,
      (Rock, Scissors)     -> MeWin,
      (Paper, Rock)        -> MeWin,
      (Paper, Paper)       -> Draw,
      (Paper, Scissors)    -> OppWin,
      (Scissors, Rock)     -> OppWin,
      (Scissors, Paper)    -> MeWin,
      (Scissors, Scissors) -> Draw,
    )

    def calcScore(me: RPS, result: Result): Int = {
      // The score for a single round is the score for the shape you selected
      // (1 for Rock, 2 for Paper, and 3 for Scissors)
      //
      // plus the score for the outcome of the round
      // (0 if you lost, 3 if the round was a draw, and 6 if you won).
      val shapeScore   = me match {
        case Rock     => 1
        case Paper    => 2
        case Scissors => 3
      }
      val outcomeScore = result match {
        case OppWin => 0
        case Draw   => 3
        case MeWin  => 6
      }

      shapeScore + outcomeScore
    }
  }
  def solvePart1(input: String)(implicit visualDebugger: VisualDebugger) = IO {
    val moves: List[Game] = input.linesIterator.map { line =>
      line.split(" ").toList.map(RPS.fromStr) match {
        case List(opponentMove, myMove) =>
          Game(myMove, opponentMove)
      }
    }.toList

    val total = moves.map(_.score).sum

    visualize(moves, Some(10))

    total.toString
  }

  def solvePart2(input: String)(implicit visualDebugger: VisualDebugger) = IO {

    val myMoveLookup: Map[(RPS, Result), RPS] = winMap.map { case ((me, opp), res) =>
      (opp, res) -> me
    }

    val moves: List[Game] = input.linesIterator.map { line =>
      line.split(" ").toList match {
        case List(oppStr, myStr) =>
          val opp            = fromStr(oppStr)
          val expectedResult = myStr match {
            case "X" => OppWin
            case "Y" => Draw
            case "Z" => MeWin
          }

          val myMove = myMoveLookup((opp, expectedResult))

          Game(myMove, opp)
      }
    }.toList

    val total = moves.map(_.score).sum

    visualize(moves, Some(10))

    total.toString
  }

  import adventOfCode.util.ui.Helpers.FormattedTable._

  def visualize(games: List[Game], limit: Option[Int] = None)(implicit visualDebugger: VisualDebugger) = {

    val header    = limit.map(l => s"First $l games").getOrElse("All Games")
    val selection = limit.map(games.take).getOrElse(games)
    visualDebugger.render(
      div(
        h3(cls := "font-medium leading-tight text-2xl mt-0 mb-2", header),
        table(
          thead(
            tr(
              th("move"),
              th("me"),
              th("opp"),
              th("result"),
              th("score"),
            ),
          ),
          tbody(
            selection.zipWithIndex.map { case (g @ Game(my, opp), idx) =>
              tr(
                td(idx + 1),
                td(my.toString),
                td(opp.toString),
                td(g.result.toString),
                td(cls := "text-right", g.score),
              )
            },
          ),
        ),
      ),
    )
  }

}
