package adventOfCode.year2022

import adventOfCode.{AocDay, VisualDebugger}
import cats.effect.IO
import outwatch.dsl._

object Day03 extends AocDay {
  val day = 3

  def parseRucksacks(input: String): Vector[Rucksack] =
    input.linesIterator.map { line =>
      val (compartment1, compartment2) = line.splitAt(line.length / 2)
      val intersection                 = compartment1.toSet.intersect(compartment2.toSet)
      assert(intersection.size == 1)
      Rucksack(compartment1, compartment2, intersection.head, scoreMap(intersection.head))
    }.toVector

  case class Rucksack(compartment1: String, compartment2: String, intersection: Char, score: Int)
  def solvePart1(input: String)(implicit visualDebugger: VisualDebugger) = IO {
    val rucksacks = parseRucksacks(input)

    val result = rucksacks.map(_.score).sum

    visualize(rucksacks)
    result.toString
  }

  def solvePart2(input: String)(implicit visualDebugger: VisualDebugger) = IO {

    val rucksacks                                     = parseRucksacks(input)
    val groups: Vector[(Vector[Rucksack], Char, Int)] = rucksacks.grouped(3).toVector.map { rucksacks =>
      // find items that are in all three rucksacks
      val intersectionOfGroup = rucksacks
        .map(r => (r.compartment1 + r.compartment2).toSet)
        .reduce(_.intersect(_))

      assert(intersectionOfGroup.size == 1)
      (rucksacks, intersectionOfGroup.head, scoreMap(intersectionOfGroup.head))
    }

    visualizePart2(groups)
    val result = groups.map(_._3).sum

    result.toString
  }

  val scoreMap = {
    // Lowercase item types a through z have priorities 1 through 26.
    // Uppercase item types A through Z have priorities 27 through 52.
    val scoreMapLower = 'a'.to('z').zipWithIndex.map { case (c, idx) => (c, idx + 1) }
    val scoreMapUpper = 'A'.to('Z').zipWithIndex.map { case (c, idx) => (c, idx + 27) }
    (scoreMapLower ++ scoreMapUpper).toMap
  }

  import adventOfCode.util.ui.Helpers.FormattedTable._

  def visualizePart2(groups: Vector[(Vector[Rucksack], Char, Int)])(implicit
    visualDebugger: VisualDebugger,
  ) =
    visualDebugger.render(
      div(
        h3(cls := "font-medium leading-tight text-2xl mt-0 mb-2", "Groups of 3"),
        table(
          thead(
            tr(
              th("Group"),
              th("intersection"),
              th("score"),
            ),
          ),
          tbody(
            groups.zipWithIndex.map { case ((_, intersection, score), idx) =>
              tr(
                td(idx + 1),
                td(intersection.toString),
                td(score),
              )
            },
          ),
        ),
      ),
    )

  def visualize(rucksacks: Vector[Rucksack], limit: Option[Int] = None)(implicit
    visualDebugger: VisualDebugger,
  ) = {

    val header    = limit.map(l => s"First $l rucksacks").getOrElse("All rucksacks")
    val selection = limit.map(rucksacks.take).getOrElse(rucksacks)
    visualDebugger.render(
      div(
        h3(cls := "font-medium leading-tight text-2xl mt-0 mb-2", header),
        table(
          thead(
            tr(
              th("Rucksack"),
              th("compartment 1"),
              th("length 1"),
              th("compartment 2"),
              th("length 2"),
              th("intersection"),
              th("score"),
            ),
          ),
          tbody(
            selection.zipWithIndex.map { case (Rucksack(compartment1, compartment2, intersection, score), idx) =>
              tr(
                td(idx + 1),
                td(compartment1),
                td(compartment1.length),
                td(compartment2),
                td(compartment2.length),
                td(intersection.toString),
                td(score),
              )
            },
          ),
        ),
      ),
    )
  }

}
