package adventOfCode.year2021

import Day10.ParsingResult.{Corrupt, Incomplete}
import adventOfCode.{AocDay, VisualDebugger}
import cats.effect.IO
import outwatch.dsl._

object Day10 extends AocDay {
  val day = 10

  sealed trait ParsingResult

  object ParsingResult {
    case object Valid extends ParsingResult
    case class Corrupt(firstIllegalCharacter: Char, chunkType: ChunkType)
        extends ParsingResult
    case class Incomplete(res: List[ChunkType]) extends ParsingResult {
      def score: Long = {
        res.foldLeft(0L) { case (total, ct) =>
          val completionValue = ct match {
            case ChunkType.Paren         => 1
            case ChunkType.SquareBracket => 2
            case ChunkType.CurlyBracket  => 3
            case ChunkType.AngleBracket  => 4
          }
          total * 5 + completionValue
        }
      }
    }
  }

  sealed trait ChunkType {
    val errorScore: Int
  }

  object ChunkType {
    case object Paren extends ChunkType { val errorScore = 3 }
    case object SquareBracket extends ChunkType { val errorScore = 57 }
    case object CurlyBracket extends ChunkType { val errorScore = 1197 }
    case object AngleBracket extends ChunkType { val errorScore = 25137 }
  }

  sealed trait OpenClose

  object OpenClose {
    case object Open extends OpenClose
    case object Close extends OpenClose
  }

  def parse(line: String): ParsingResult = {
    import ChunkType._
    import OpenClose._
    import ParsingResult._

    val res = line.foldLeft[(ParsingResult, List[ChunkType])](
      (ParsingResult.Valid, List.empty)
    ) { case ((pr, chunks), char) =>
      pr match {
        case ParsingResult.Valid =>
          val (chunkType, openClose) = char match {
            case '{' => (CurlyBracket, Open)
            case '}' => (CurlyBracket, Close)
            case '[' => (SquareBracket, Open)
            case ']' => (SquareBracket, Close)
            case '<' => (AngleBracket, Open)
            case '>' => (AngleBracket, Close)
            case '(' => (Paren, Open)
            case ')' => (Paren, Close)
          }

          openClose match {
            case Close =>
              // now the chunk type has to match
              if (!chunks.headOption.contains(chunkType))
                (Corrupt(char, chunkType), chunks)
              else (Valid, chunks.tail)
            case Open =>
              (Valid, chunkType :: chunks)
          }
        case _ => (pr, chunks)
      }
    }
    res._1 match {
      case Valid if res._2.nonEmpty => Incomplete(res._2)
      case Valid                    => Valid
      case c: Corrupt               => c
      case i: Incomplete            => i
    }
  }

  def solvePart1(input: String)(implicit visualDebugger: VisualDebugger) = IO{
    val lines = input.linesIterator.filterNot(_.trim.isEmpty).toList

    val corruptLines: List[(String, Corrupt, Int)] = lines
      .map(l => l -> parse(l))
      .zipWithIndex
      .collect { case ((line, pr: Corrupt), idx) => (line, pr, idx) }

    visualDebugger.render(
      div(
        h2("corrupt lines"),
        table(
          thead(tr(th("idx"), th("1st illegal char"), th("score"))),
          tbody(corruptLines.map { case (_, Corrupt(_, chunkType), idx) =>
            tr(
              td(idx),
              td(chunkType.toString),
              td(chunkType.errorScore)
            )
          })
        )
      )
    )

    val result = corruptLines.map(_._2.chunkType.errorScore).sum

    result.toString

  }

  def solvePart2(input: String)(implicit visualDebugger: VisualDebugger) = IO{

    val lines = input.linesIterator.filterNot(_.trim.isEmpty).toList

    val incompleteLines: Vector[(String, Incomplete, Int)] = lines
      .map(l => l -> parse(l))
      .zipWithIndex
      .collect { case ((line, i: Incomplete), idx) => (line, i, idx) }
      .toVector

    val sorted = incompleteLines.sortBy(_._2.score)
    //3
    // 0, 1, 2

    val winnerIndex = sorted.size / 2

    val result = sorted(winnerIndex)._2.score

    visualDebugger.render(
      div(
        h2("winner"),
        p(s"sortIndex: #$winnerIndex"),
        p(s"completion score: $result"),
        h2("incomplete lines"),
        table(
          thead(tr(th("sort idx"), th("line idx"), th("completion score"))),
          tbody(sorted.zipWithIndex.map {
            case ((_, incomplete, lineIdx), sortIdx) =>
              tr(
                td(sortIdx),
                td(lineIdx),
                td(incomplete.score)
              )
          })
        )
      )
    )

    result.toString
  }
}
