package adventOfCode

import adventOfCode.Page.{current, yearDayPages, DayPage, Landing, LandingYear}
import adventOfCode.util.Util.clamp
import adventOfCode.util.ui.Helpers.toggleSwitchLabelRight
import adventOfCode.year2021.Day18DebugPage
import cats.effect.{ExitCode, IO, IOApp}
import colibri.{BehaviorSubject, Observable, Subject}
import org.scalajs.dom
import org.scalajs.dom.intl.DateTimeFormatOptions
import outwatch._
import outwatch.dsl._

import scala.concurrent.ExecutionContext.Implicits.global
import scala.concurrent.Future
import scala.scalajs.js
import scala.scalajs.js.Date
import scala.scalajs.js.annotation.JSImport

@js.native
@JSImport("../../../../src/main/css/index.css", JSImport.Namespace)
object Css extends js.Object
//
@js.native
@JSImport("../../../../src/main/css/tailwind.css", JSImport.Namespace)
object TailwindCss extends js.Object

object Main extends IOApp {

  // keep these references even though IntelliJ thinks they're not used
  // noinspection ScalaUnusedExpression
  TailwindCss
  // noinspection ScalaUnusedExpression
  Css // load css

  private val assetLocation = s"${dom.window.location.origin}/${if (
    dom.window.location.host == "localhost" || dom.window.location.host
      .startsWith("localhost")
  ) ""
  else "adventofcode/"}"

  def run(args: List[String]): IO[ExitCode] = {

    val app = div(
      headerBar(Page.current),
      div(
        cls := "m-8",
        // menu,
        Page.current.map {
          case Page.Day18Debug =>
            Day18DebugPage.render

          case Page.LandingYear(year) =>
            yearOverview(year)

          case Page.Landing =>
            val yearLinks = yearDayPages.keys.toList.sorted.map { year =>
              val link = a(LandingYear(year).href, year.toString)
              if (isCurrentYear(year)) {
                link(cls := "text-4xl font-extrabold text-purple-800")
              }
              else link(cls := "text-2xl font-bold text-purple-400")
            }

            ul(
              cls := "flex flex-col gap-2",
              yearLinks.map(aTag => li(aTag)),
            )

          case p: Page.DayPage =>
            div(dayRenderer(p))

        },
      ),
    )

    Outwatch.renderInto[IO]("#app", app).as(ExitCode.Success)
  }

  def yearOverview(year: Int) =
    yearDayPages.get(year) match {
      case None           => VModifier("page not found")
      case Some(dayPages) =>
        val firstOFMonth = new Date(year, 11, 1) // Yes, months are 0-based
        val dayOfWeek    = firstOFMonth.getDay.toInt
        println(s"DayOfWeek: $dayOfWeek of $firstOFMonth")

        val dayCards = 1.to(25).map { day =>
          val maybeDaypage = dayPages.get(day).flatten

          val dayContent = maybeDaypage match {
            case Some(_) => a(DayPage(year, day).href, day)
            case None    => span(day)
          }

          val isCurrentDay     = isToday(year, day)
          val styleGridColDay1 = VModifier.style("grid-column-start") := dayOfWeek

          val styleSolvedOtherDay = cls := "text-purple-400"
          val styleToday          = cls := "text-purple-800"
          val styleNotSolvedDay   = cls := "text-purple-100"

          val notSolved = maybeDaypage.isEmpty
          val isSolved  = maybeDaypage.isDefined

          div(cls          := "p-2 border border-onSurface-subdued rounded-lg bg-surface text-right")(
            VModifier.ifTrue(day == 1)(styleGridColDay1),
            dayContent(cls := "text-2xl font-extrabold"),
            VModifier.ifTrue(isCurrentDay)(styleToday),
            VModifier.ifTrue(!isCurrentDay && notSolved)(styleNotSolvedDay),
            VModifier.ifTrue(!isCurrentDay && isSolved)(styleSolvedOtherDay),
          )
        }

        val gridStyle      = VModifier(
          VModifier.style("display")               := "grid",
          VModifier.style("grid-template-columns") := "repeat(7, 1fr)",
        )
        val dayOfWeekStyle = VModifier(
          cls                             := "text-right text-2xl text-purple-400 px-2 py-1",
          VModifier.style("font-variant") := "small-caps",// tailwind doesn't know that
        )
        div(
          cls := "w-full sm:w-1/5", // Dont think of sm: as meaning on small screens, think of it as at the small breakpoint.
          gridStyle,
          div(dayOfWeekStyle, "Mo"),
          div(dayOfWeekStyle, "Tu"),
          div(dayOfWeekStyle, "We"),
          div(dayOfWeekStyle, "Th"),
          div(dayOfWeekStyle, "Fr"),
          div(dayOfWeekStyle, "Sa"),
          div(dayOfWeekStyle, "Su"),
          dayCards,
        )
    }

  def yearOverviewDropdown(dayPage: DayPage): VNode = {

    val daySelectionOptions = yearDayPages.toList.sortBy(_._1) map { case (year, dayPages) =>
      optGroup(
        VModifier.prop("label") := year.toString,
        dayPages.filter(_._2.isDefined).keys.toList.sorted.map { day =>
          option(value := s"$year,${day}", s"Day $day", selected := day == dayPage.day)
        },
      )
    }

    div(
      label(
        cls := "block mb-2 text-sm font-medium text-gray-900",
        select(
          cls := "bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 appearance-none",
          option("Select another Day"),
          VModifier(daySelectionOptions),
          onChange.value.map { yearDateCsv =>
            val List(year, day) = yearDateCsv.split(",").toList.map(_.toInt)
            DayPage(year, day)
          } --> current,
        ),
      ),
    )

  }

  val dateTimeFormatAtEriksPlace = new org.scalajs.dom.intl.DateTimeFormat(
    "en-US",
    new DateTimeFormatOptions {
      timeZone = "EST"
    },
  )

  def isToday(year: Int, aocDay: Int) = {
    val date = new Date

    //new puzzles get released at 00:00 EST (that's where Erik lives)
    val aocDate = new Date(dateTimeFormatAtEriksPlace.format(date))

    val currentDay = aocDate.getDate().toInt
    currentDay == aocDay && date.getFullYear().toInt == year && date.getMonth().toInt == 11 // Yes, months are 0-based
  }

  def isCurrentYear(year: Int) = {
    val date = new Date
    date.getFullYear().toInt == year
  }

  def headerBar(subject: Subject[Page]) =
    subject.map { p =>
      val (selectedDayString, pageToRedirectTo) = p match {
        case Page.Landing           => ("Advent of Code", Landing)
        case Page.LandingYear(year) => (s"Advent of Code $year", Landing)
        case Page.Day18Debug        => ("Advent of Code 2021 - Day 18 Debug", Landing)
        case DayPage(2021, day)     => (s"Advent of Code 2021 - Day $day", LandingYear(2021))
        case DayPage(2022, day)     => (s"Advent of Code 2022 - Day $day", LandingYear(2022))
      }
      div(
        cls := "w-full h-20 flex flex-row items-center px-8 bg-purple-600",
        h1(
          a(pageToRedirectTo.href, s"$selectedDayString"),
          cls := "text-4xl text-gray-100",
        ),
      )
    }

  def getPage(dayPage: DayPage): Option[AocDay] =
    yearDayPages.get(dayPage.year).flatMap(_.get(dayPage.day)).flatten

  def dayRenderer(dayPage: DayPage): VModifier = {

    getPage(dayPage) match {
      case None       => VModifier("Page not found")
      case Some(page) =>
        val dayPath                       = s"${dayPage.year}/day-${("0" + dayPage.day).takeRight(2)}"
        val localStorageKeyForCurrentFile = s"selected-file/${dayPage.year}/${dayPage.day}"

        allTestFiles.map {
          case None            =>
            div("???")
          case Some(testFiles) =>
            val testFilesOfDay: List[DayInputModel.FileEntry] = DayInputModel
              .flatten(testFiles)
              .filter(_.path.startsWith(dayPath))

            val localStorageInputFile: Option[DayInputModel.FileEntry] =
              Option(
                dom.window.localStorage.getItem(localStorageKeyForCurrentFile),
              )
                .flatMap(path => testFilesOfDay.find(_.path == path))

            localStorageInputFile.fold(
              dom.window.localStorage.removeItem(localStorageKeyForCurrentFile),
            )(_ => ())

            val selectedFile: BehaviorSubject[Option[DayInputModel.FileEntry]] =
              Subject.behavior(localStorageInputFile)

            // write changes to the selected file back to localstorage
            selectedFile.foreach {
              case None    =>
                dom.window.localStorage.removeItem(
                  localStorageKeyForCurrentFile,
                )
              case Some(f) =>
                dom.window.localStorage
                  .setItem(localStorageKeyForCurrentFile, f.path)
            }

            val selectedTestfileUri: Observable[Option[String]] =
              selectedFile.map(_.map { f =>
                s"${assetLocation}/input/${f.path}"
              })

            val selectedPageLayout      = Subject.behavior(page.pageLayout)
            val checkedValue            = PageLayout.Vertical
            val uncheckedValue          = PageLayout.Horizontal
            val isVerticalLayoutChecked =
              selectedPageLayout.imapSubject(if (_) checkedValue else uncheckedValue)(_ == checkedValue)
            val isShowInputChecked      = Subject.behavior(true)

            def solutionsDiv(uri: String): VModifier = div(getData(uri).map {
              case None              => div(s"no input found at '$uri'")
              case Some(inputString) =>
                val part1Debugger = new VisualDebugger
                val part2Debugger = new VisualDebugger

                val clearDebuggers: IO[Unit] = IO {
                  part1Debugger.clear()
                  part2Debugger.clear()
                }

                val part1Div =
                  clearDebuggers *> page.solvePart1(inputString)(part1Debugger).map { solution =>
                    div(
                      cls := "rounded-lg",
                      h2("Solution for Part1", cls      := "text-4xl mb-8"),
                      solution,
                      h3("Visual-Debugging-Output", cls := "text-2xl mt-8 mb-8"),
                      part1Debugger.getRenderContent,
                      h3("Debug-Output", cls            := "text-2xl mt-8 mb-8"),
                      part1Debugger.getLogContent,
                    )
                  }
                val part2Div =
                  clearDebuggers *> page.solvePart2(inputString)(part2Debugger).map { solution =>
                    div(
                      cls := "rounded-lg",
                      h2("Solution for Part2", cls      := "text-4xl mb-8"),
                      solution,
                      h3("Visual-Debugging-Output", cls := "text-2xl mt-8 mb-8"),
                      part2Debugger.getRenderContent,
                      h3("Debug-Output", cls            := "text-2xl mt-8 mb-8"),
                      part2Debugger.getLogContent,
                    )
                  }
                val inputDiv = div(
                  cls := "rounded-lg",
                  h2("Input", cls := "text-4xl mb-8"),
                  textArea(
                    cls           := "font-mono",
                    cols          := 60,
                    whiteSpace    := "pre",
                    rows          := clamp(inputString.linesIterator.size, 2, 40),
                    readOnly      := true,
                    inputString,
                  ),
                )

                val solutionsDisplayDiv = selectedPageLayout.map {
                  case PageLayout.Vertical   =>
                    div(cls := "flex flex-col gap-4", part1Div, part2Div)
                  case PageLayout.Horizontal =>
                    div(cls := "flex flex-row gap-4", part1Div.map(_(cls := "w-1/2")), part2Div.map(_(cls := "w-1/2")))

                }

                div(
                  cls := "mt-8",
                  isShowInputChecked.combineLatest(solutionsDisplayDiv).map {
                    case (true, solutions)  =>
                      div(cls := "flex flex-row gap-4", solutions(cls := "w-4/5"), inputDiv(cls := "w-1/5"))
                    case (false, solutions) => div(cls := "flex flex-row", solutions)
                  },
                )

            })

            val settingsDiv          = div(
              toggleSwitchLabelRight(isVerticalLayoutChecked, checkedValue.toString, uncheckedValue.toString),
              toggleSwitchLabelRight(isShowInputChecked, "display input", "display input"),
            )
            val testfileSelectionDiv = {

              val testFileSelectionOptions = testFilesOfDay.map { f =>
                option(value := f.path, f.path, selected <-- selectedFile.map(_.contains(f)))
              }
              div(
                label(
                  cls := "block mb-2 text-sm font-medium text-gray-900",
                  "Testfile: ",
                  select(
                    cls := "bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 appearance-none",
                    option("Select a testfile"),
                    VModifier(testFileSelectionOptions),
                    onChange.value.map(path => testFilesOfDay.find(_.path == path)) --> selectedFile,
                  ),
                ),
//                ul(
//                  testFilesOfDay.map { f =>
//                    li(
//                      input(
//                        tpe       := "radio",
//                        value     := f.path,
//                        idAttr    := f.path,
//                        checked <-- selectedFile.map(_.contains(f)),
//                        onClick.as(Some(f)) --> selectedFile,
//                      ),
//                      label(`for` := f.path, f.path),
//                    )
//                  },
//                ),
              )
            }

            val navigationDiv = {
              val link = s"https://adventofcode.com/${dayPage.year}/day/${dayPage.day}"
              div(
                yearOverviewDropdown(dayPage),
                a(
                  cls    := "underline text-base underline-offset-2",
                  href   := link,
                  target := "_blank",
                  rel    := "noopener noreferrer",
                  "Open on adventofcode.com",
                ),
              )
            }

            div(
              div(
                cls := "flex flex-row justify-between",
                testfileSelectionDiv,
                div(settingsDiv, navigationDiv),
              ),
              selectedTestfileUri.map {
                case None      => VModifier.empty
                case Some(uri) =>
                  solutionsDiv(uri)

              },
            )
        }
    }
  }

  def allTestFiles: Future[Option[DayInputModel.FileOrDirectoryEntry]] = {
    import sttp.client3.quick._
    // Circe integration: `asJson` response description.
    import io.circe.generic.auto._
    import sttp.client3.circe._

    val request = basicRequest
      .get(
        uri"${assetLocation}/input/listing.json",
      )
      .response(asJson[DayInputModel.FileOrDirectoryEntry])

    request
      .send(backend)
      .map(_.body.toOption)
  }

  def getData(uri: String): Future[Option[String]] = {
    import sttp.client3.quick._
    // Circe integration: `asJson` response description.

    val request = basicRequest
      .get(uri"$uri")
      .response(asString)

    request
      .send(backend)
      .map(_.body.toOption)

  }
}
