package adventOfCode.year2021

import adventOfCode.{AocDay, VisualDebugger}
import cats.effect.IO
import outwatch.dsl._

object Day07 extends AocDay {
  val day = 7

  def solvePart1(input: String)(implicit visualDebugger: VisualDebugger) = IO{
    solve(input, (start, end) => math.abs(end - start))
  }

  def solvePart2(input: String)(implicit visualDebugger: VisualDebugger) = IO{
    solve(input, calcFuelCostsPart2)
  }

  def solve(input: String, fuelCostFn: (Int, Int) => Int)(implicit
      visualDebugger: VisualDebugger
  ) = {
    val crabPositions =
      input.linesIterator
        .filterNot(_.trim.isEmpty)
        .toList
        .head
        .split(",")
        .map(_.toInt)
        .toVector

    val minPos = crabPositions.min
    val maxPos = crabPositions.max

    val allCosts = minPos.to(maxPos).map { globalPos =>
      globalPos -> crabPositions.map { cp =>
        fuelCostFn(cp, globalPos)
      //calcFuelCosts(cp, globalPos)
      }.sum
    }

    val optimalPosition = allCosts.minBy(_._2)

    visualDebugger.render(
      table(
        thead(
          tr(
            th("position"),
            th("dist. to opt."),
            th("total fuel")
          )
        ),
        tbody(
          allCosts.map { case (pos, costs) =>
            tr(
              td(pos),
              td(pos - optimalPosition._1),
              td(costs)
            )
          }
        )
      )
    )

    optimalPosition._2.toString

  }

  def calcFuelCostsPart2(start: Int, end: Int): Int = {
    val distance = math.abs(end - start)
    val cost = distance * (distance + 1) / 2 //Gauss Triangle formula

    cost
  }
}
