package adventOfCode.util.ui

import cats.effect.IO
import colibri.Observable.source
import colibri.Subject
import org.scalajs.dom.window
import outwatch._
import outwatch.dsl._

object Helpers {
  def toggleSwitch(isChecked: Subject[Boolean], checkedLabel: String, uncheckedLabel: String): VNode = {
    val uncheckedOpacity = 0.3
    label(
      cls := "relative flex justify-between items-center",
      span(
        uncheckedLabel,
        isChecked.map(VModifier.ifTrue(_)(opacity := uncheckedOpacity)),
        cls := "cursor-pointer",
      ),
      input(
        tpe := "checkbox",
        checked <-- isChecked, // type annotation to make IDEA happy
        onChange.checked --> isChecked,
        cls := "absolute left-1/2 -translate-x-1/2 w-full h-full peer appearance-none rounded-md cursor-pointer",
      ),
      span(
        cls := "w-10 h-5 flex items-center flex-shrink-0 mx-2 p-0.5 bg-gray-300 rounded-full duration-300 ease-in-out after:w-4 after:h-4 after:bg-white after:rounded-full after:shadow-md after:duration-300 peer-checked:after:translate-x-5 cursor-pointer",
      ),
      span(
        checkedLabel,
        isChecked.map(VModifier.ifNot(_)(opacity := uncheckedOpacity)),
        cls := "cursor-pointer",
      ),
    )
  }

  def toggleSwitchLabelRight(isChecked: Subject[Boolean], checkedLabel: String, uncheckedLabel: String): VNode = {
    val uncheckedOpacity = 0.3
    label(
      cls := "relative flex justify-between items-center",
      input(
        tpe := "checkbox",
        checked <-- isChecked, // type annotation to make IDEA happy
        onChange.checked --> isChecked,
        cls := "absolute left-1/2 -translate-x-1/2 w-full h-full peer appearance-none rounded-md cursor-pointer",
      ),
      span(
        cls := "w-10 h-5 flex items-center flex-shrink-0 mx-2 p-0.5 bg-gray-300 rounded-full duration-300 ease-in-out after:w-4 after:h-4 after:bg-white after:rounded-full after:shadow-md after:duration-300 peer-checked:after:translate-x-5 cursor-pointer",
      ),
      isChecked.map(isChecked =>
        span(
          if (isChecked) checkedLabel else uncheckedLabel,
          VModifier.ifNot(isChecked)(opacity := uncheckedOpacity),
          cls := "cursor-pointer",
        ),
      ),
    )
  }

  def renderPreDiv(content: String) =
    div(cls := "font-mono", whiteSpace.preWrap, content)

  def expandableDivButton(
    title: String,
    initialIsExpanded: Boolean,
    contentStr: String,
    contentRender: String => VModifier = renderPreDiv,
  ): VModifier = {
    val expand = Subject.behavior(initialIsExpanded)

    expand.map { isExpanded =>
      val buttonText = if (isExpanded) "collapse" else "expand"
      div(
        cls := "flex flex-col gap-2 items-start",
        h3(cls := "font-medium leading-tight text-2xl mt-0 mb-2", title),
        div(
          cls  := "flex flex-row gap-2",
          button(
            tpe := "button",
            cls := "bg-purple-400 hover:bg-purple-700 px-5 py-2 text-sm leading-5 rounded font-semibold text-white",
            buttonText,
            onClick.as(!isExpanded) --> expand,
          ),
          button(
            tpe := "button",
            cls := "bg-purple-400 hover:bg-purple-700 px-5 py-2 text-sm leading-5 rounded font-semibold text-white",
            "copy to clipboard",
            onClick.doEffect {
              IO.fromFuture(
                IO(window.navigator.clipboard.writeText(contentStr).toFuture),
              )
            },
          ),
        ),
        VModifier.ifTrue(isExpanded)(contentRender(contentStr)),
      )
    }

  }

  object FormattedTable {

    val td: VNode    = dsl.td(cls := "px-2 py-1 border border-gray-300 align-baseline")
    val th: VNode    = dsl.th(cls := "px-2 py-1 border border-gray-300 align-baseline")
    val table: VNode = dsl.table(cls := "table-auto border-collapse border border-gray-400")

  }
}
