package adventOfCode.util

import scala.collection.mutable

/** Implementation of Dijkstra\s algorithm using mutable maps. */
object DijkstraMutable extends Dijkstra {
  def dijkstra[N](g: Graph[N])(source: N): (Map[N, Int], Map[N, N]) = {
    val active = new BinaryHeap[N]
    active.+=(source, 0)
    val res = mutable.Map(source -> 0)
    val pred = mutable.Map.empty[N, N]
    while (active.nonEmpty) {
      val (node, cost) = active.pop
      //active -= node
      //val cost = res(node)
      for ((n, c) <- g(node)) {
        val cost1 = cost + c
        if (cost1 < res.getOrElse(n, Int.MaxValue)) {
          active += (n, cost1)
          res += (n -> cost1)
          pred += (n -> node)
        }
      }
    }
    (res.toMap, pred.toMap)
  }

  override def toString = "DijkstraMutable"
}

import scala.language.implicitConversions

/** A trait for Dijkstra's algorithm. */
trait Dijkstra {

  import Util._

  /** Runs Dijkstra's algorithm on the given graph from the given source node.
    *
    * Returns a map with distances from the source and another map that maps
    * each node reachable from the source to a predecessor that lies on the
    * shortest path from the source.
    *
    * If a node is unreachable from the source, neither of the two returned maps
    * contains that node as a key.
    */
  def dijkstra[N](g: Graph[N])(source: N): (Map[N, Int], Map[N, N])

  /** Optionally computes a shortest path between two given nodes in a graph.
    *
    * Returns a `Some` with a shortest path from `source` to `target` if
    * `target` is reachable from `source`; returns `None` otherwise.
    */
  def shortestPath[N](g: Graph[N])(source: N, target: N): Option[List[N]] = {
    val pred = dijkstra(g)(source)._2
    if (pred.contains(target) || source == target)
      Some(iterateRight(target)(pred.get))
    else None
  }
}

/** Companion object for the Dijkstra trait.
  *
  * Defines an implicit conversion which can be used to call `dijkstra` and
  * `shortestPath` as methods on `Graph` instances.
  *
  * To enable the implicit conversion, you can either mix in the trait
  * `Dijkstra.Syntax` or import `Dijkstra.syntax._`.
  */
object Dijkstra {
  case class DijkstraOps[N](graph: Graph[N], instance: Dijkstra) {
    def dijkstra(source: N): (Map[N, Int], Map[N, N]) =
      instance.dijkstra(graph)(source)

    def shortestPath(source: N, target: N): Option[List[N]] =
      instance.shortestPath(graph)(source, target)
  }

  trait Syntax {
    implicit def toDijkstraOps[N](graph: Graph[N])(implicit
        d: Dijkstra
    ): DijkstraOps[N] =
      DijkstraOps(graph, d)
  }

  object syntax extends Syntax
}

/** A trait for weighted graphs.
  */
trait Graph[N] { self =>

  /** Returns a map with the successors of `n` and their costs. */
  def apply(n: N): Map[N, Int]

  /** Returns a new graph with the given node `n` removed. */
  def -(n1: N): Graph[N] = (n: N) => if (n == n1) Map.empty else self(n) - n1

  /** Returns a new graph with the edge from `n1` to `n2` removed. */
  def -(n1: N, n2: N): Graph[N] = (n: N) =>
    if (n == n1) self(n) - n2 else self(n)

  /** Performs a breadth-first search on this graph.
    *
    * Returns the set of nodes reachable from the given source node.
    *
    * Does not terminate if infinitely many nodes are reachable.
    */
  def bfs(source: N): Set[N] = {
    def go(initial: Set[N], acc: Set[N]): Set[N] =
      if (initial.isEmpty) acc
      else {
        val explored = acc ++ initial
        val neighbours = for {
          node <- initial
          (n, _) <- apply(node) if !explored(n)
        } yield n
        go(neighbours, explored)
      }

    go(Set(source), Set.empty)
  }

  /** Returns whether there is a path from `source` to `target` in this graph.
    */
  def isReachable(source: N, target: N): Boolean =
    bfs(source) contains target
}
