package adventOfCode.year2021

import Day18.InputParser.{expr, initialState}
import Day18.{Expr, reduceWithRecording}
import adventOfCode.util.Util.withTime
import colibri._
import outwatch._
import outwatch.dsl._

object Day18DebugPage {

  def render: VModifier = {

    println("day-18-debug")

    val text = Subject.behavior(
      "[[[[0,[4,5]],[0,0]],[[[4,5],[2,6]],[9,5]]],[7,[[[3,7],[4,3]],[[6,3],[8,8]]]]]"
    )

    val expression = text.map { line =>
      expr.runA(initialState(line.trim))
    }

    div(
      label(`for` := "expr", "Expression"),
      input(
        idAttr := "expr",
        tpe := "text",
        cls := "font-mono shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline",
        value <-- text,
        onInput.value --> text
      ),
      h2("reduce"),
      expression.map {
        case None => VModifier.empty
        case Some(exp) =>
          val ((_, zipperReductionExpressions), durationZipper) = withTime(reduceWithRecording(exp, Nil))
          div(
            h3("original"),
            Day18.SvgDraw.visualizeTree(exp),
            p(cls := "font-mono", exp.toString),
            div(
              h3("comparison"),
              p(s"zipper.length: ${zipperReductionExpressions.length}"),
              p(s"zipper duration: $durationZipper"),
              displayReductionSteps(zipperReductionExpressions)
            )
          )
      }
    )
  }

  def displayReductionSteps(list1: List[Expr]): VModifier = {
    div(
      list1.map { e1 =>
        div(
          cls := "flex flex-row mt-6 pt-6 border-t-4",
          div(cls := "w-11/12", Day18.SvgDraw.visualizeTree(e1), p(cls := "font-mono", e1.toString))
        )
      }
    )
  }

}
