package adventOfCode.year2021

import adventOfCode.util.Util.withTime
import adventOfCode.{AocDay, VisualDebugger}
import cats.effect.IO
import outwatch.dsl._

object Day20 extends AocDay {
  val day = 20

  object Pos {
    val neighborOffsets: List[(Int, Int)] = List(
      (0, 1),
      (1, 0),
      (-1, 0),
      (0, -1)
    )

    implicit val ordering: Ordering[Pos] = Ordering.by { case Pos(x, y) =>
      (x, y)
    }
  }

  case class Puzzle(
      algorithmChars: String,
      imageMap: Map[Pos, Boolean],
      iteration: Int,
      minX: Int,
      maxX: Int,
      minY: Int,
      maxY: Int
  ) {
    assert(imageMap.nonEmpty, "imageMap mustn't be empty")

    lazy val width: Int = maxX - minX

    lazy val height: Int = maxY - minY

    override def toString: String = s"""
iteration: $iteration
outOfBoundsPixel: $outOfBoundsPixel
width: $width
  maxX: $maxX
  minX: $minX
height: $height
  maxY: $maxY
  minY: $minY
                                        """.trim

    val outOfBoundsPixel: Boolean = {
      val firstValueInAlgo = if (algorithmChars.head == '#') 1 else 0
      val newValue = iteration & 1 & firstValueInAlgo
      newValue == 1
    }

    def render(padding: Int = 0): String = {

      (minY - padding)
        .to(maxY + padding)
        .map { y =>
          (minX - padding)
            .to((maxX + padding))
            .map { x =>
              val pos = Pos(x, y)
              val value = imageMap.getOrElse(pos, false)
              if (value) "#" else "."
            }
            .mkString
        }
        .mkString("\n")
    }

    def getPixelValue(pos: Pos): (String, Int) = {
      val rangeX = (pos.x - 1).to(pos.x + 1)
      val rangeY = (pos.y - 1).to(pos.y + 1)

      val bits = for {
        y <- rangeY
        x <- rangeX
      } yield imageMap.getOrElse(Pos(x, y), outOfBoundsPixel)
      val bitsString = bits.map(b => if (b) "1" else "0").mkString
      val value = Integer.parseInt(bitsString, 2)
      (bitsString, value)
    }

    def enhance: Puzzle = {
      val padding = 1

      //local mutability for speed
      val newMap = collection.mutable.HashMap.empty[Pos, Boolean]

      for (x <- (minX - padding).to(maxX + padding)) {
        for (y <- (minY - padding).to(maxY + padding)) {
          val pos = Pos(x, y)
          val (_, index) = getPixelValue(pos)
          val algorithmChar = algorithmChars(index)
          val convertedValue = algorithmChar == '#'
          newMap.update(pos, convertedValue)
        }
      }

      val result = newMap.toMap
      Puzzle(
        algorithmChars,
        result,
        iteration = iteration + 1,
        minX = minX - padding,
        maxX = maxX + padding,
        minY = minY - padding,
        maxY = maxY + padding
      )
    }
  }

  object Puzzle {

    def parse(input: String): Puzzle = {
      val lines = input.trim.linesIterator.toList
      val algorithmChars = lines.head
      val imageMap = lines.tail
        .filterNot(_.trim.isEmpty)
        .zipWithIndex
        .flatMap { case (line, y) =>
          line.toCharArray.zipWithIndex.map { case (char, x) =>
            Pos(x, y) -> (char == '#')
          }
        }
        .toMap

      val maxX: Int = imageMap.keys.map(_.x).max
      val minX: Int = imageMap.keys.map(_.x).min
      val maxY: Int = imageMap.keys.map(_.y).max
      val minY: Int = imageMap.keys.map(_.y).min
      Puzzle(algorithmChars, imageMap, 0, minX = minX, maxX = maxX, minY = minY, maxY = maxY)
    }
  }

  def solvePart1(input: String)(implicit visualDebugger: VisualDebugger) = IO{

    val initial = Puzzle.parse(input)

    val enhance1 = initial.enhance
    val enhance2 = enhance1.enhance

    val result = enhance2.imageMap.values.count(identity)

    visualDebugger.render(
      div(
        h3("Algorithm"),
        p("(with line breaks)"),
        pre(initial.algorithmChars.grouped(74).mkString("\n")),
        h3("Puzzle"),
        p(initial.toString),
        //pre(initial.render(padding = 5)),
        h3("Pixel value at Pos(2,2)"), {
          val (bits, value) = initial.getPixelValue(Pos(2, 2))
          val algorithmChar = initial.algorithmChars(value)
          val convertedValue = algorithmChar == '#'
          pre(s"""
               |$bits --> $value
               |algorithm char: $algorithmChar
               |converted value: $convertedValue""".stripMargin)
        },
        h3("Iterated"),
        pre(enhance1.toString),
        hr,
        //pre(enhance1.render(padding = 5)),
        h3("Iterated 2nd time"),
        pre(enhance2.toString),
        hr,
        // pre(enhance2.render(padding = 5)),
        p(s"Pixels lit: $result")
      )
    )

    result.toString
  }

  def solvePart2(input: String)(implicit visualDebugger: VisualDebugger) = IO{
    val initial = Puzzle.parse(input)

    val (finalResult, time) = withTime(1.to(50).foldLeft(initial)((current, _) => current.enhance))
    visualDebugger.log(s"calculated 50 iterations in $time")

    val result = finalResult.imageMap.values.count(identity)
    result.toString
  }

  case class Pos(x: Int, y: Int) {
    override def toString: String = s"($x, $y)"

//    def neighbors: List[Pos] =
//      neighborOffsets.map { case (dx, dy) =>
//        Pos(x + dx, y + dy)
//      }
  }

}
