package adventOfCode.year2022

import FileSystemEntry._
import adventOfCode.util.Parsers._
import adventOfCode.{AocDay, VisualDebugger}
import cats.effect.IO

object Day07 extends AocDay {
  val day = 7

  // 1253145 too low

  val parser                  = identityStringParser
  def parseLine(line: String) = parser.parse(line).toOption.get._2

  def solvePart1(input: String)(implicit visualDebugger: VisualDebugger) = IO {
    val directorySizes: List[(String, Int)] = calcDirectorySizes(input)

    val relevantDirectories = directorySizes.filter(_._2 <= 100000)
    relevantDirectories.foreach { case (d, totalSize) =>
      visualDebugger.log(s"$d $totalSize")
    }

    val result = relevantDirectories.map(_._2).sum

    result.toString
  }

  def solvePart2(input: String)(implicit visualDebugger: VisualDebugger) = IO {
    val directorySizes: List[(String, Int)] = calcDirectorySizes(input)
    val totalDiskSpaceAvailable             = 70000000
    val requiredUnusedDiskSpaceForUpdate    = 30000000
    val currentlyUsed                       = directorySizes.find(_._1 == "/").get._2

    val currentlyFreeSpace = totalDiskSpaceAvailable - currentlyUsed
    val toBeDeleted        = requiredUnusedDiskSpaceForUpdate - currentlyFreeSpace

    visualDebugger.log(s"totalDiskSpaceAvailable: $totalDiskSpaceAvailable")
    visualDebugger.log(s"requiredUnusedDiskSpaceForUpdate: $requiredUnusedDiskSpaceForUpdate")
    visualDebugger.log(s"currentlyUsed: $currentlyUsed")
    visualDebugger.log(s"currentlyFreeSpace: $currentlyFreeSpace")
    visualDebugger.log(s"toBeDeleted: $toBeDeleted")

    val relevantDirectories = directorySizes.filter(_._2 >= toBeDeleted)
    visualDebugger.log(s"\nfound ${relevantDirectories.size} options for deletion:")
    relevantDirectories.sortBy(_._2).foreach { case (path, size) =>
      visualDebugger.log(s"  $path: $size")
    }

    relevantDirectories.map(_._2).min.toString

  }

  private def calcDirectorySizes(input: String) = {
    val entries = parseFilestructure(input.linesIterator.toList, "", Set.empty)

    val directories = entries.groupBy(_.path).keys.toList
    directories.foreach(println)

    val directorySizes = directories.map { d =>
      val filesOfDir = entries.collect { case f: File => f }.filter(_.path.startsWith(d))
      val totalSize  = filesOfDir.map(_.size).sum
      d -> totalSize
    }
    directorySizes
  }

  def parseFilestructure(
    lines: List[String],
    currentDir: String,
    result: Set[FileSystemEntry],
  ): Set[FileSystemEntry] =
    lines match {
      case Nil            => result
      case ::(line, next) =>
        if (line.startsWith("$ cd ")) {
          val directory    = line.replace("$ cd ", "")
          val newDirectory = if (directory == "..") {
            "/" + currentDir.split("/").filterNot(_.isEmpty).dropRight(1).mkString("/")
          }
          else s"$currentDir/$directory"
          val cleaned      = newDirectory.replaceAll("//", "/")
          parseFilestructure(next, cleaned, result + Directory(cleaned))
        }
        else if (line == "$ ls") {
          parseFilestructure(next, currentDir, result)
        }
        else if (line.startsWith("dir ")) {

          val dirName = line.replace("dir ", "")

          val path = currentDir + "/" + dirName
          parseFilestructure(next, currentDir, result + Directory(path.replaceAll("//", "/")))
        }
        else if (line.head.isDigit) {
          val Array(size, filename) = line.split(" ")
          val newFile               = File(currentDir, filename, size.toInt)
          parseFilestructure(next, currentDir, result + newFile)
        }
        else {
          ???
        }

    }
}

sealed trait FileSystemEntry {
  def path: String
}

object FileSystemEntry {
  case class Directory(path: String) extends FileSystemEntry

  case class File(path: String, name: String, size: Int) extends FileSystemEntry
}
