package adventOfCode.year2021

import Day11.Grid.visualizeGrid
import adventOfCode.{AocDay, VisualDebugger}
import cats.effect.IO
import outwatch.VModifier
import outwatch.dsl._

object Day11 extends AocDay {
  val day = 11

  val neighborOffsets: List[(Int, Int)] = (for {
    dx <- (-1).to(1)
    dy <- (-1).to(1)
    if !(dx == 0 && dy == 0)
  } yield (dx, dy)).toList

  case class Pos(x: Int, y: Int) {
    def neighbors: List[Pos] =
      neighborOffsets.map { case (dx, dy) =>
        Pos(x + dx, y + dy)
      }

    override def toString: String = s"($x, $y)"
  }

  case class Grid(cells: Vector[Vector[Int]]) {
    val height = cells.length
    val width = cells.head.length
    val size = height * width

    override def toString: String =
      cells.map(row => row.mkString).mkString("\n")

    def get(pos: Pos): Option[Int] = {
      if (!contains(pos)) None
      else Some(cells(pos.y)(pos.x))
    }

    def contains(pos: Pos): Boolean = {
      cells.isDefinedAt(pos.y) && cells(pos.y).isDefinedAt(pos.x)
    }

    def positions: List[Pos] = {
      (for {
        y <- 0.until(height)
        x <- 0.until(width)
      } yield Pos(x, y)).toList
    }

    def neighborValues(pos: Pos): List[Int] = {
      pos.neighbors.flatMap(get)
    }

    def neighbors(pos: Pos): List[Pos] = {
      pos.neighbors.filter(contains)
    }

  }

  object Grid {
    def parse(lines: Seq[String]) = {

      val cells = lines.zipWithIndex.map { case (l, r) =>
        l.zipWithIndex.map { case (v, c) =>
          v.toString.toInt //char to int doesn't work iirc
        }.toVector
      }.toVector

      Grid(cells)
    }

    def visualizeGrid(
        grid: Grid
    ): VModifier = {
      import svg._

      def getColor(pos: Pos): String = {
        val v = grid.get(pos).get
        val redValue = 0
        val blueValue = 0
        val greenValue = 255 * v / 9.0

        s"rgb($redValue,${greenValue.toInt},$blueValue)"

      }

      svg(
        cls := "w-11/12",
        viewBox := s"0 0 ${grid.width * 5} ${grid.height * 5}",
        style := "background-color:black",
        grid.positions.map { pos =>
          rect(
            x := (pos.x * 5).toString,
            y := (pos.y * 5).toString,
            width := "4.5",
            height := "4.5",
            fill := getColor(pos)
          )
        }
      )
    }
  }

  def step(grid: Grid): (Grid, Int) = {
    val octocats = grid.cells.map(_.toArray).toArray

    grid.positions.foreach { case Pos(x, y) =>
      octocats(y)(x) += 1
    }

    //evaluate flashes
    var again = true
    while (again) {
      again = false
      grid.positions.foreach { case p @ Pos(x, y) =>
        if (octocats(y)(x) > 9 && octocats(y)(x) != -1) {
          //println(s"Flash @ ${p}")
          octocats(y)(x) = -1
          grid.neighbors(p).foreach { n =>
            if (octocats(n.y)(n.x) > -1) {
              //println(s"  neighbor increase @ ${n}")
              octocats(n.y)(n.x) += 1
            }
          }
          again = true
        }
      }
    }

    var numFlashes = 0
    //set flashed values to 0
    grid.positions.foreach { case Pos(x, y) =>
      if (octocats(y)(x) == -1) {
        octocats(y)(x) = 0
        numFlashes += 1
      }
    }

    (Grid(octocats.map(_.toVector).toVector), numFlashes)
  }

  def solvePart1(input: String)(implicit visualDebugger: VisualDebugger) = IO{
    val grid = Grid.parse(input.linesIterator.filterNot(_.trim.isEmpty).toList)

    val (after100, totalFlashes) =
      1.to(100).foldLeft((grid, 0)) { case ((grid, total), _) =>
        val (updated, numFlashes) = step(grid)
        (updated, total + numFlashes)
      }

    visualDebugger.render(
      div(
        h2("initial octopus activation"),
        visualizeGrid(grid),
        h2("final octopus activation"),
        visualizeGrid(after100)
      )
    )

    visualDebugger.log("inital")
    visualDebugger.log(grid.cells.map(_.mkString).mkString("\n"))
    visualDebugger.log("after 100")
    visualDebugger.log(after100.cells.map(_.mkString).mkString("\n"))
    totalFlashes.toString
  }

  def solvePart2(input: String)(implicit visualDebugger: VisualDebugger) = IO{
    val grid = Grid.parse(input.linesIterator.filterNot(_.trim.isEmpty).toList)

    val ((finalGrid, _), steps) = LazyList
      .iterate((grid, -1234)) { case (g, _) =>
        step(g)
      }
      .zipWithIndex
      .find { case ((_, total), _) =>
        total == grid.size
      }
      .get

    visualDebugger.render(
      div(
        h2(s"final state after ${steps} steps"),
        visualizeGrid(finalGrid)
      )
    )

    steps.toString

  }

}
